import React, { useState ,useEffect} from 'react';
import styles from "./PostJob.module.css"
import CommonHeader from "../../../Teacher/CommonHeader/CommomHeader";
import * as constants from "../../constants/constants"
import axios from "axios"
import PageTitle from "../../../PageTitle";
import { useHistory } from "react-router-dom";
import Alert from "../../../Toast/Toast"
import Button from '@material-ui/core/Button';
import { withRouter } from "react-router-dom"


const PostJob = (props) => {

    const [isInvalid, setIsInvalid] = useState(false)
    const [qualificationList, setQualificationList] = useState([])

    const history = useHistory();

    useEffect(() => {
        if(qualificationList.length === 0) {
            axios.get(constants.url + "site_management/qualification")
            .then((response) => {
                // //console.log(response)
                setQualificationList([...response.data.output])
            })
            .catch(err => {
                // //console.log(err)
           })
        }
        // getDetails()
        // setIsInvalid(true)
      }, []);

    const back = () =>{
      history.push("/institution/jobs")
    }
    const [title, setTitle] = useState({
        value: "",
        isValid: false,
        touched: false
    })
    const [experience, setExperience] = useState({
        value: "",
        isValid: false,
        touched: false
    })
    const [qualification, setQualification] = useState({
        value: "",
        isValid: false,
        touched: false
    })
    const [vacancy, setVacancy] = useState({
        value: "",
        isValid: false,
        touched: false
    })
    const [lastDate, setLastDate] = useState({
        value: "",
        isValid: false,
        touched: false
    })
    const [description, setDescription] = useState({
        value: "",
        isValid: false,
        touched: false
    })
    const [otherQualification, setOtherQualification] = useState({
        value: "",
        isValid: false,
        touched: false
    })

    const validateForm = () => {
        let isFormValid = title.isValid && description.isValid && experience.isValid && qualification.isValid &&
        lastDate.isValid && vacancy.isValid

        if (qualification.value.toLowerCase() === "other") {
            isFormValid = isFormValid && otherQualification.isValid
        }

        return isFormValid
    }

    const validate = (val) => {
        return val.length
    } 

    const onPostJob = () => {
        let isValid = validateForm()
        setTitle({
            ...title,
            touched: true
        })
        setDescription({
            ...description,
            touched: true
        })
        setExperience({
            ...experience,
            touched: true
        })
        setLastDate({
            ...lastDate,
            touched: true
        })
        setQualification({
            ...qualification,
            touched: true
        })
        setOtherQualification({
            ...otherQualification,
            touched: true
        })
        setVacancy({
            ...vacancy,
            touched: true
        })
        if(isValid) {
            let id = localStorage.getItem("id") 
            axios.post(constants.url + "institution/create_job", {"title": title.value,
            "required_exp": experience.value,
            "Qualification": qualification.value.toLowerCase() !== "other" ? qualification.value : otherQualification.value,
            "Number_of_vacancy": vacancy.value,
            "Last_Date": lastDate.value,
            "Description": description.value,
            "institution_id": id,
            "institution_name": localStorage.getItem("inst_name")
        })
        .then((res) => {
            if(res.data.error_code) {
                props.history.push({
                    pathname: "/institution/jobs",
                    state: {
                        "message": "Job posted successfully!"
                    }
                }) 

            } else {
                setIsInvalid(true)
                setTimeout(function(){  setIsInvalid(false)
                }, 3000);
            }
        //     setIsInvalid(true)
        //     clearFields()
    }).catch((err) => {
        setIsInvalid(true)
        setTimeout(function(){  setIsInvalid(false)
        }, 3000);
        })
        }
    } 

    const clearFields = () => {
        setDescription({
            value: "",
            isValid: false,
            touched: false
        })
        setTitle({
            value: "",
            isValid: false,
            touched: false
        })
        setExperience({
            value: "",
            isValid: false,
            touched: false
        })
        setVacancy({
            value: "",
            isValid: false,
            touched: false
        })
        setQualification({
            value: "",
            isValid: false,
            touched: false
        })
        setLastDate({
            value: "",
            isValid: false,
            touched: false
        })

    }

    const onCancel = () =>{
       clearFields()
    }

    const onChangeTexthandler = (event, i) => {
        let value = event.target.value

        if(i === 1) {
            setTitle({
                ...title,
                value: value,
                isValid: validate(value)
            })
        }

        if(i === 2) {
            setExperience({
                ...experience,
                value: value,
                isValid: validate(value)
            })
        }
        if(i === 3) {
            setQualification({
                ...qualification,
                value: value,
                isValid: validate(value)
            })
        }

        if(i === 13) {
            setOtherQualification({
                ...otherQualification,
                value: value,
                isValid: validate(value)
            })
        }

        if(i === 4) {
            setVacancy({
                ...vacancy,
                value: value,
                isValid: validate(value)
            })
        }
        if(i === 5) {
            setLastDate({
                ...lastDate,
                value: value,
                isValid: validate(value)
            })
        }

        if(i === 6) {
            setDescription({
                ...description,
                value: value,
                isValid: validate(value)
            })
        }
    }


    return (
        <>

    <PageTitle title="Dashboard / New Job" button="Back" add_new={back} />

          <div className={styles["sub-container"]}>
         
          <form className={styles["post-job-form"]}>
                    <div className={styles["input-container"]}>
                    <label style={{color: title.touched && !title.isValid ? "red" : "grey" }}>
                        Title *
                    </label>
                    <input
                    value={title.value}
                    onBlur={() => {setTitle({
                        ...title,
                        touched: true
                    })}}
                    onChange={(event) => { onChangeTexthandler(event, 1)} }
                    />
                    </div>
                    <div className={styles["input-container"]}>
                        <label style={{color: experience.touched && !experience.isValid ? "red" : "grey" }}>
                            Experience Required *
                        </label>
                        <input
                        type = "number"
                        value={experience.value}
                        onBlur={() => {setExperience({
                            ...experience,
                            touched: true
                        })}}
                        onChange={(event) => { onChangeTexthandler(event, 2)}}/>
                    </div>
                    <div className={styles["input-container"]}>
                        <label style={{color: qualification.touched && !qualification.isValid ? "red" : "grey" }}>
                            Qualification Opting *
                        </label>
                        <select
                        style={{border: qualification.touched && !qualification.isValid ? "1px solid red" : "1px solid black" }}
                        onChange={(event) => {onChangeTexthandler(event, 3)}}
                        value={qualification.val} 
                        onBlur={() => {setQualification({
                            ...qualification,
                            touched: true
                        })}}
                        id="degree" 
                        name="qualification">
                        <option value="">SELECT QUALIFICATION</option>
                        {
                            qualificationList.map((el) => {
                                return <option value={el.name}>{el.name.toUpperCase()}</option>
                            })
                        }
                        <option value="OTHER">OTHER</option>
                    </select>
                    </div>
                    {
                    qualification.value.toLowerCase() === "other" ?
                    <div className={styles["input-container"]}>
                        <label style={{color: otherQualification.touched && !otherQualification.isValid ? "red" : "grey" }}>
                            Qualification *
                        </label>
                        <input
                       value={otherQualification.value}
                       onBlur={() => {setOtherQualification({
                           ...otherQualification,
                           touched: true
                       })}}
                       onChange={(event) => { onChangeTexthandler(event, 13)}}/>
                    </div> : null
                    }
                    <div className={styles["input-container"]}>
                        <label style={{color: vacancy.touched && !vacancy.isValid ? "red" : "grey" }}>
                            Number of Vacancy *
                        </label>
                        <input
                        type="number"
                        value={vacancy.value}
                        onBlur={() => {setVacancy({
                            ...vacancy,
                            touched: true
                        })}}
                        onChange={(event) => { onChangeTexthandler(event, 4)}}/>
                    </div>
                    <div className={styles["input-container"]}>
                        <label style={{color: lastDate.touched && !lastDate.isValid ? "red" : "grey" }}>
                            Last Date *
                        </label>
                        <input 
                        type="date"
                        min={new Date().toISOString().split("T")[0]}
                        value={lastDate.value}
                        onBlur={() => {setLastDate({
                            ...lastDate,
                            touched: true
                        })}}
                        onChange={(event) => { onChangeTexthandler(event, 5)}}/>
                    </div>
                    <div className={styles["description"]}>
                    <label style={{color: description.touched && !description.isValid ? "red" : "grey" }}>
                            Description *
                        </label>
                        <textarea
                        onBlur={() => {setDescription({
                            ...description,
                            touched: true
                        })}}
                        value={description.value}
                        onChange={(event) => { onChangeTexthandler(event, 6)}}/>
                    </div>
                </form>
                <div className={styles["button-container"]}>
            <Button
                variant="contained"
                color="primary"
                style={{marginRight: "3rem"}}
                onClick={onPostJob}
            >
                Create
            </Button>
       
             <Button
                variant="contained"
                color="secondary"
                onClick={() => {history.goBack()}}
            >
                Cancel
            </Button>
            </div>
          </div>
          {
        isInvalid ? <Alert message="Something went wrong" color="red"/> : null 
      }
        </>
  );
}

export default withRouter(PostJob);
