import React, { useEffect, useState } from 'react';
import styles from "./EditProfile.module.css"
import { Grid } from "@material-ui/core";
// import PageTitle from "../PageTitle";
import PageTitle from "../../../PageTitle";
import { useHistory } from "react-router-dom";
import axios from "axios"
import * as constants from "../../constants/constants"
import Button from '@material-ui/core/Button';
import Alert from "../../../Toast/Toast"
import ForgotPassword from "../../../Home/ForgotPassword/ForgotPassword"
import Map from "../../../Map/Map"


const EditProfile = (props) => {
    const history = useHistory()
    const [message, setMessage] = useState("")
    const [isSuccess, setIsSuccess] = useState(false)
    const [isAutoComplete, setIsAutocomplete] = useState(true)
    const [data, setData] = useState("")
    const [isValuesSet, setIsValueSet] = useState(false)
    const [isPhoneNumberChanged, setIsPhoneNumberChanged] = useState(false)
    useEffect(() => {
        window.scrollTo(0, 0);
    }, [])

    const [stateList, setStateList] = useState("")
    const [districtList, setDistrictList] = useState("")
    const [isFormValid, setIsFormValid] = useState({
        touched : false,
        isValid: false
    })
    const [name, setName] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [phoneNumber, setPhoneNumber] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [address, setAddress] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [street, setStreet] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [city, setCity] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [state, setState] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [district, setDistrict] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [pin, setPin] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [about, setAbout] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [webAddress, setWebAddress] = useState({
        val: "",
        isValid: true,
        touched: false
    })
    const [logo, setLogo] = useState({
        val: "",
        isValid: true,
        touched: false
    })
    
    useEffect(() => {
        if(stateList.length === 0) {
            axios.get(constants.url + "site_management/state")
                 .then((response) => {
                     setStateList([...response.data.output])
                 })
                 .catch(err => {
                     //console.log(err)
                    })
        }

        if(districtList.length === 0) {
            axios.get(constants.url + "site_management/district")
            .then((response) => {
                setDistrictList([...response.data.output])
            })
            .catch(err => {
                //console.log(err)
            })
        }

        if(data === "") {
            //console.log(data)
            let id = localStorage.getItem("id")
            axios.get(constants.url + "institution/dashboard/" + id)
            .then((res) => {
                //console.log(res)
                if(res.data.error_code === 1) {
                    setData(res.data.output[0]) 
                }
            })
            .catch((err) => {
                //console.log(err)
            })
        }
        
    }, [])
    
    const setValues = (data) => {
        setName({...name, val: data.Name, isValid: true})
        setAbout({...about, val: data.About, isValid: true})
        setCity({...city, val: data.city, isValid: true})
        setAddress({...address, val: data.Location, isValid: true})
        setState({...state, val: data.State, isValid: true})
        setStreet({...street, val: data.Street, isValid: true})
        setPhoneNumber({...phoneNumber, val: data.Phone_Number, isValid: true})
        setPin({...pin, val: data.Pin, isValid: true})
        setDistrict({...district, val: data.District, isValid: true})
        setWebAddress({...webAddress, val: data.Website, isValid: true})
        setLogo({...logo, val: data.logo, isValid: true})
        setIsValueSet(true)
    }
    
    if(data && !isValuesSet) {
        //console.log(data)
        setValues(data)
    }
    
    let states;
    if(stateList.length) {
        let updatedList = [{name: " "}, ...stateList]
        states = updatedList.map((el, i) => {
            //console.log(el)
    
            return <option value={`${el.name}`}>{el.name.trim() ? el.name.toUpperCase() : "SELECT STATE"}</option>
        })
    }

    let districts;
    if(districtList.length) {
        let updatedList = [{name: " "}, ...districtList]
        districts = updatedList.map((el, i) => {
            return <option value={`${el.name}`}>{el.name.trim() ? el.name.toUpperCase() : "SELECT DISTRICT"}</option>
        })
    }

    
    const validateForm = () => {
        return  name.isValid && phoneNumber.isValid && about.isValid 
    }
    
    
    const onUpdate = (event) => {
        event.preventDefault()
        let isValid = validateForm()
        let id = localStorage.getItem("id")
        //console.log(isValid)
        setName({...name, touched: true})
        setAbout({...about, touched: true})
        setAddress({...address, touched: true})
        setCity({...city, touched: true})
        setState({...state, touched: true})
        setStreet({...street, touched: true})
        setPhoneNumber({...phoneNumber, touched: true})
        setPin({...pin, touched: true})
        setDistrict({...district, touched: true})
        setWebAddress({...webAddress, touched: true})
        setIsFormValid({
            isValid: isValid,
            touched: true 
        })
        //console.log(String(data.Phone_Number) === String(phoneNumber.val))
        if(String(data.Phone_Number) === String(phoneNumber.val)) {
            update()
        } else {
            onSendCode(phoneNumber.val)
            // //console.log("hell")
        }
       
    }

    const update = () => {
        let id = localStorage.getItem('id')
        let isValid = validateForm()
        if(isValid) {
            let formData = new FormData()
            formData.append("id", id)
            formData.append("Name", name.val)
            formData.append("Pass", data.Pass )
            formData.append("Phone_Number", phoneNumber.val)
            formData.append("Email", data.Email)
            formData.append("About", about.val)
            formData.append("Address", address.val)
            formData.append("type", "institution")
            formData.append("State", state.val)
            formData.append("city", city.val)
            formData.append("Location", address.val)
            formData.append("Street", street.val)
            formData.append("District", district.val)
            formData.append("Pin", pin.val)
            formData.append("item_img", logo.val)
            formData.append("Website", webAddress.val)
            axios.post(constants.url + "institution/profile", formData)
            .then((res) => {
                //console.log(res)
                history.push({
                    pathname: "/institution/profile",
                    state: {
                        updateMessage: "updated successfully"
                    }
                })
        }).catch((err) => {
            setMessage("something went wrong!")
            setIsSuccess(false)
            setTimeout(() => {
                setMessage("")
              }, 3000)
            //console.log(err)
        })
        } 
    }

    const validate = (i, val) => {
        if(i === 1) {
            return /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(val)
        }

        if(i === 2) {
            if(val) {
                return true
            }

            return false
        }

        // if(i === 3) {
        //     return val === password.val
        // }

        if(i === 4) {
            return val ? true : false
        }

        if(i === 5) {
            return val ? true : false
        }

        if(i === 6) {
            return val ? true : false
        }

        if(i === 7) {
            return val ? true : false
        }

        if(i === 8) {
            return /^\d{10}$/.test(val)
        }

        if(i === 9) {
            return val ? true : false
        }

        if(i === 10) {
            return val ? true : false
        }

        if(i === 11) {
            return val ? true : false
        }

        if(i === 12) {
            return val ? true : false
        }

        if(i === 13) {
            return val ? true : false
        }

        if(i === 14) {
            return val ? true : false
        }

        if(i === 15) {
            return val ? true : false
        }
    }
    
    const onChangeTextHandler = (event, i) => {

        let value = event.target.value

        if(i === 4) {
            setName({
                ...name,
                val: value,
                isValid:  validate(i, value)
            })
        }

        if(i === 5 && (/^[0-9]*$/.test(value) || value === "")) {
            setPhoneNumber({
                 ...phoneNumber,
                 val: value,
                 isValid: validate(i, value)
            })
        }

        if(i === 6) {
             setAddress({
                 ...address,
                 val: value,
                 isValid: validate(i, value)
             })
        }

        if(i === 7) {
             setStreet({
                 ...street,
                 val: value,
                 isValid: value ? true : false
             })
        }

        if(i === 8) {
             setCity({
                 ...city,
                 val: value,
                 isValid: validate(i, value)
             })
        }

        if(i === 9) {
             setState({
                 ...state,
                 val: value,
                 isValid: validate(i, value)
             })
        }

        if(i === 10) {
             setDistrict({
                 ...district,
                 val: value,
                 isValid: validate(i, value)
             })
        }

        if(i === 11 && (/^[0-9]*$/.test(value) || value === "")) {
             setPin({
                 ...pin,
                 val: value,
                 isValid: validate(i, value)
             })
        }

        if(i === 12) {
             setAbout({
                 ...about,
                 val: value,
                 isValid: validate(i, value)
             })
        }

        if(i === 13) {
            setWebAddress({
                ...webAddress,
                val: value
            })
       }
    }


    const onUploadLogo = (event) => {
        let file = event.target.files[0]
        setLogo({
            ...logo,
            val: file
        })
    }

    const back = ()=>{
        history.goBack();
    }

    const onSendCode = (phoneNumber) => {
        let email = localStorage.getItem('email')
        let id = localStorage.getItem('id')
        let isValid = validateForm()
        axios.get(`https://campusfield.in/api/verify/mobile/${phoneNumber}/${email}`)
        .then(res => {
            if(res.data.error_code === 1) {
                setIsPhoneNumberChanged(true)
                // update(isValid, id)
              
            } else {
              props.setAlert()
              props.onCancel()
            }
        })
        .catch(err => {
            props.setAlert()
            props.onCancel()
        })
      }


    return (
    <>
    <PageTitle title="Edit Profile" button="Back" add_new={back}/>
    {
        isPhoneNumberChanged ? 
        <div style={{display: "flex", justifyContent: "center", alignItems: "center"}}>
        <ForgotPassword
        update={update}
        isCodeSent={true}
        />
        </div> :
        <>
      <div className={styles["sub-container"]}>
      <form className={styles["profile-form"]}>
                <div className={styles["input-container"]}>
                    <label>
                        Name*
                    </label>
                    <input
                    className={[name.touched & !name.isValid ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setName({...name, touched: true})}}
                    value={name.val}
                    onChange={(event) => {onChangeTextHandler(event, 4)}}
                    placeholder="Name*"/>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Phone Number*
                    </label>
                    <input
                    type = "numeric"
                    className={[!phoneNumber.isValid && phoneNumber.touched ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setPhoneNumber({...phoneNumber, touched: true})}}
                    value={phoneNumber.val}
                    maxLength={10}
                    onChange={(event) => {onChangeTextHandler(event, 5)}}
                    placeholder="Phone Number*"/>
                </div>
                {/* <div className={styles["input-container"]}>
                    <label>
                        Address*
                    </label>
                    <input
                    className={[!address.isValid && address.touched  ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setAddress({...address, touched: true})}}
                    value={address.val}
                    onChange={(event) => {onChangeTextHandler(event, 6)}}
                    placeholder="Address*"/>
                </div> */}
                {/* <div className={styles["input-container"]}>
                    <label>
                        Street*
                    </label>
                    <input
                     className={[!street.isValid && street.touched  ? styles["invalid"] : null].join(" ")}
                     onBlur={() => {setStreet({...street, touched: true})}}
                    value={street.val}
                    onChange={(event) => {onChangeTextHandler(event, 7)}}
                    placeholder="Street*"/>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        City*
                    </label>
                    <input
                    // className={[!city.isValid && city.touched  ? styles["invalid"] : null].join(" ")}
                    // onBlur={() => {setCity({...city, touched: true})}}
                    value={city.val}
                    onChange={(event) => {onChangeTextHandler(event, 8)}}
                    placeholder="City*"/>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        State*
                    </label>
                    <select
                    onChange={(event) => {onChangeTextHandler(event, 12)}}
                    style={!state.isValid && state.touched  ? {border: "1px solid red"} : {border: "1px solid black"}}
                    value={state.val} 
                    id="state" 
                    name="select-state"
                    onChange={(event) => {onChangeTextHandler(event, 9)}}>
                        {states}
                    </select>
                </div>
                <div
                className={styles["input-container"]}>
                    <label>
                        District*
                    </label>
                    <select
                    value={district.val} 
                    id="district" 
                    style={!state.isValid && state.touched  ? {border: "1px solid red"} : {border: "1px solid black"}}
                    name="select-district"
                    onChange={(event) => {onChangeTextHandler(event, 10)}}>
                        {districts}
                    </select>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Pin*
                    </label>
                    <input
                     className={[!pin.isValid && pin.touched  ? styles["invalid"] : null].join(" ")}
                     onBlur={() => {setPin({...pin, touched: true})}}
                    value={pin.val}
                    maxLength={6}
                    onChange={(event) => {onChangeTextHandler(event, 11)}}
                    placeholder="Pin*"/>
                </div> */}
                <div className={styles["input-container"]}>
                    <label>
                        Web Address
                    </label>
                    <input
                    onBlur={() => {setWebAddress({...webAddress, touched: true})}}
                    value={webAddress.val}
                    onChange={(event) => {onChangeTextHandler(event, 13)}}
                    placeholder="Web Address"/>
                </div>
                {
                    localStorage.getItem('type') === 'institution' ? 
                    <div className={styles["input-container"]}>
                        <label>
                            Address
                        </label>
                        <Map
                         style={{padding: 0}}
                         currentLocation={address.val}
                         isAutoComplete={isAutoComplete}
                         setCurrentLocation={(formattedAddress) => {setAddress({...address, val: formattedAddress})}}
                         />
                    </div>: null
                }
                <div style={{marginLeft: "1rem"}}>
                    <label style={{marginBottom: 0, marginTop: "10px", textAlign: "left"}}>
                        Logo(.png, .jpeg)
                    </label>
                    <input 
                    style={{border: "none", backgroundColor: "transparent", boxShadow: "0 0 0 0"}}
                    onChange={(event) => {onUploadLogo(event)}}
                    className={styles["file"]}
                    accept="image/png, image/jpeg"
                    type="file"/>
                </div>
                <div className={styles["description"]}>
                    <label>
                        About*
                    </label>
                    <textarea
                    className={[!about.isValid && about.touched  ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setAbout({...about, touched: true})}}
                    value={about.val} 
                    onChange={(event) => {onChangeTextHandler(event, 12)}}/>
                </div>
            </form>
            <div className={styles["button-container"]}>
            <Button
                variant="contained"
                color="primary"
                style={{marginRight: "3rem"}}
                onClick={onUpdate}
            >
                Update
            </Button>
       
             <Button
                variant="contained"
                color="secondary"
                onClick={() => {history.goBack()}}
            >
                Cancel
            </Button>
            </div>
            {
        message ? <Alert message={message} color={isSuccess ? "green" : "red"}/> : null
      }
      </div>
      </>}
    </>
  );
}

export default EditProfile;
