import React, {useState, useEffect} from "react"
import styles from "./InstitutionSidebar.module.css"
import {useHistory} from "react-router"
import { NavLink } from "react-router-dom"


const InstitutionSidebar = (props) => {
    let history = useHistory()
    const paraList = [
        {
            "header": 
                {
                    "text": "Dashboard", "link": "/institution/profile"
                }, 
                    "paras": []
                },
        {   "header": {"text": "Profile", "link": "#"}, 
            "paras": [
                  {title:"Facilities", to: "/institution/facilities"}, 
                  {title:"Achievements", to:"/institution/achievement"}, 
                  {title:"Placements", to:"/institution/placements"}, 
                  {title:"Tie-ups", to:"/institution/tie_up"}, 
                ]
        }, 
        {   "header": {"text": "C-TET Training", "link": "#"}, 
            "paras": [
                  {title:"Programs", to:"/institution/programme"}, 
                  {title:"Enrolled Programs", to:"/institution/enrolled_programme"}, 
                ]
        }, 
        {
            "header": {"text": "Recruitment", "link": "#"}, 
            "paras": [
                {title: "Recruitment type", to: "#"}, 
                {title:"Post Job", to:"/institution/jobs"}]
        }, 
        {
            "header": {"text": "Faculties", "link": "/institution/faculties"}, 
            "paras": []
        }, 
        {
            "header": {"text": "Consultancy", "link": "#"}, 
            "paras": []
        },
        {   
            "header": {"text": "Payment & Transaction", "link": "#"},
            "paras": [
                {title: "Transaction History", to: "/institution/order"}, 
                {title: "Payment", to: "/institution/payments"}
            ]}
    ]

    const [isOpen, setIsOpen] = useState([false, false, false, false, false])
    let element = paraList.map((el, j) => {
    
    const toggle = (i) => {
        let dummyArray = [false, false, false, false, false]
        dummyArray[i] = !isOpen[i]
        setIsOpen(dummyArray)
    }
        return (
        <div key={j}>
            <div className={[styles["toggle-bar"], isOpen[j] ? styles["toggle-open"] : null].join(" ")}>
                <div className={styles["main-link"]} onClick={() => {
                    // props.setIsMobileNav(false)
                    history.push(el.header.link
                    )}}>{el.header.text}</div>
                <div className={styles["toggler"]}
                 onClick={() => {toggle(j)}}>
                     {el.header.text === "Consultancy" || el.header.text === "Dashboard" || el.header.text === "Faculties" ? 
                        null :
                        <p style={{textAlign: "center", margin: 0, color: "rgb(89, 182, 173)", fontWeight: "bold"}}>
                            {!isOpen[j] ?  "+" : "-"} 
                        </p> 
                     } 
                </div>
            </div>
            <div className={[styles["content"], isOpen[j] ? styles["expand"] : null].join(" ")}>
                <div style={{padding: "1rem", paddingLeft: 0, paddingRight: 0}}>
                {el.paras.map((para, i) => {
                   return (
                       <div
                       key={i} 
                       style={{width: "100%"}}>
                            <div className={styles["link"]} onClick={() => {
                                // props.setIsMobileNav(false)
                                history.push(para.to)
                            }}>
                            {para.title}
                            </div>
                        </div>
                   )
                })}
                </div>
            </div>
        </div>
        )
    })

    return (
        <div className={[styles["mobile-nav-container"], props.isMobileNav ? styles["open-nav"] : null].join(" ") }>
            <div>
                {element}
            </div>
        </div>
    )

}

export default InstitutionSidebar
