const mapStateToProps = (state) => {
  return {
    responseToView: state.jobResponse.responseToView,
    jobResponse: state.jobResponse.jobResponse
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    onSetJobResponse: (jobResponse, count) => dispatch(setJobResponse(jobResponse, count))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Sidebar));
