import React, { useState } from 'react';
import styles from "./Testimonial.module.css"
import * as constants from "../constants/constants"
import axios from "axios"
import PageTitle from "../../PageTitle/PageTitle";
import { useHistory } from "react-router-dom";
import Alert from "../../Toast/Toast"
import Button from '@material-ui/core/Button';


const Testimonial = (props) => {
    let isState = props.location.state
    const [message, setMessage] = useState("")
    const [name, setName] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [email, setEmail] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [phone, setPhone] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [isSuccess, setIsSuccess] = useState(false)
    const history = useHistory();


    const [description, setDescription] = useState({
        val: "",
        isValid: false,
        touched: false
    })

    const validateForm = () => {
        return  description.isValid
    }

    const validate = (val, i) => {

        if(i == 3) {
            return /^\d+$/.test(val) && val.length == 10
        }

        if(i == 2) {
            return /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(val)
        }

        return val.length
    } 

    const setTouchedTrue = () => {
        setDescription({
            ...description,
            touched: true
        })

        setName({
            ...name,
            touched: true
        })

        setEmail({
            ...email,
            touched: true
        })

        setPhone({
            ...phone,
            touched: true
        })
    }

    const onAddTestimonial = () => {
        let isValid = validateForm()
        setTouchedTrue()
        
        if(isValid) {
            let id = localStorage.getItem("id")
            axios.post(constants.url + "site_management/testimonials", {
                "Name": localStorage.getItem('inst_name') ? localStorage.getItem('inst_name') : localStorage.getItem('applicant_name'),
                "Email": localStorage.getItem('email'),
                "type": localStorage.getItem('type'),
                "Mobile_Number": localStorage.getItem('phone_number'),
                "Message": description.val
            })
            .then((res) => {
                setMessage("We got your fedback!")
                setIsSuccess(true)
                setTimeout(() => {
                    setMessage("")
                  }, 3000)
                // alert("certificate added successfully")
                clearFields()
            }).catch((err) => {
                setMessage("something went wrong!")
                setIsSuccess(false)
                setTimeout(() => {
                    setMessage("")
                  }, 3000)
            })
        }
    } 

    const clearFields = () => {
        setDescription({
            val: "",
            isValid: false,
            touched: false
        })

        setPhone({
            val: "",
            isValid: false,
            touched: false
        })
        setEmail({
            val: "",
            isValid: false,
            touched: false
        })
        setName({
            val: "",
            isValid: false,
            touched: false
        })
    }

    const onChangeTextHandler = (event, i) => {
        let value = event.target.value

        if(i === 1) {
            setName({
                ...name,
                val: value,
                isValid: validate(value, i)
            })
        }

        
        if(i === 2) {
            setEmail({
                ...email,
                val: value,
                isValid: validate(value, i)
            })
        }

        
        if(i === 3) {
            setPhone({
                ...phone,
                val: value,
                isValid: validate(value, i)
            })
        }
        
        
        if(i === 4) {
            setDescription({
                ...description,
                val: value,
                isValid: validate(value, i)
            })
        }
    }


    return (
    <>
    <PageTitle title="Testimonial"/>
          <div className={styles["sub-container"]}>
         
                <form className={styles["post-job-form"]}>
                {/* <div className={styles["input-container"]}>
                    <label>
                        Name
                    </label>
                    <input
                    value={name.val}
                    style={name.touched && !name.isValid ? {border: "1px solid red"} : null}
                    onBlur={() => {setName({...name, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 1)}}
                    />
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Email
                    </label>
                    <input
                    value={email.val}
                    style={email.touched && !email.isValid ? {border: "1px solid red"} : null}
                    onBlur={() => {setEmail({...email, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 2)}}
                    />
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Phone
                    </label>
                    <input
                    value={phone.val}
                    maxLength={10}
                    style={phone.touched && !phone.isValid ? {border: "1px solid red"} : null}
                    onBlur={() => {setPhone({...phone, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 3)}}/>
                </div> */}
                    <div className={styles["description"]}>
                        <label>
                            Feedback *
                        </label>
                        <textarea
                        style={description.touched && !description.isValid ? {border: "1px solid red"} : null}
                        onBlur={() => {setDescription({
                            ...description,
                            touched: true
                        })}}
                        value={description.val}
                        onChange={(event) => { onChangeTextHandler(event, 4)}}/>
                    </div>
                </form>
                <div className={styles["button-container"]}>
                <Button
                    style={{marginRight: "0.9rem"}}
                    variant="contained"
                    color="primary"
                    onClick={onAddTestimonial}
                >
                    Create
                </Button>
                <Button
                    variant="contained"
                    color="secondary"
                    onClick={() => {
                            clearFields()}
                    }
                >
                    Cancel
                </Button>
            </div>
            {
        message ? <Alert message={message} color={isSuccess ? "green" : "red"}/> : null
      }
          </div>
        </>
  );
}

export default Testimonial;
