import React, {useEffect, useState} from 'react';
import styles from "./Facilities.module.css"
import PageTitle from "../../../PageTitle";
import { useHistory } from "react-router-dom";
import axios from "axios"
import * as constants from "../../constants/constants"
import Alert from "../../../Toast/Toast"
import Facility from "./Facility/Facility"
import EmptyList from '../../../UI/EmptyList/EmptyList';

const Facilities = (props) => {
    let updatemessage = props.location.state ? props.location.state.updateMessage : ""
    const history = useHistory();
    const [data, setData] = useState([])
    const [message, setMessage] = useState("")
    let facilities;

    const onDeleteFacility = (facilityId) => {
      let id = localStorage.getItem("id")
  
      axios.post(constants.url + "institution/Facilities/" + id + `/${facilityId}/0`, {
        "Facilities":{
            "id": facilityId,
        }
      })
      .then((res) => {
          if(res.data.msg.search("delete") != -1) {
            getFacilities()
            setMessage("deleted successfully")
          }
      }).catch((err) => {
          setMessage("network error!")
          setTimeout(() => {
              setMessage("")
            }, 3000)
          //console.log(err)
      })
  }

    let id = localStorage.getItem("id")
    useEffect(() => {
      //console.log(data)
      if(data.length === 0) {
        getFacilities()
      }
      })

      const getFacilities = () => {
        axios.get(constants.url + "institution/dashboard/" + id)
        .then((res) => {
            if(res.data.error_code === 1) {
              // //console.log(res.data.output + "hello")
              setData(res.data.output[0]) 
            }
        })
        .catch((err) => {
            //console.log(err)
        })
      }

    if(data.Facilities && data.Facilities.length) {
      //console.log(data)
      facilities = data.Facilities.map((el, i) => {
        //console.log({...el})
        return (
          <Facility
          onDelete={onDeleteFacility}
          id={el.id}
          key={i}
          title={el.title}
          description={el.description}
          />
        )
      })   
    }

    const move_to_edit = () => {
      history.push("/institution/facilities/new")
    }

  return (
    <>
    <PageTitle title="Facilities" button="Add" add_new={move_to_edit} />
        <div className={styles["sub-container"]}>
        {facilities && facilities.length ? facilities : <EmptyList message="No Facilities to list!"/>}
        </div>
        {message ? <Alert message={message} color={message.search("network") != -1 ? "red" : "green"}/> : null}
        {updatemessage  ? <Alert message={updatemessage} color="green"/>: null}
    </>
  );
}

export default Facilities;
