import React, { useEffect, useState} from 'react';
import styles from "./TrainingPrograms.module.css"
import { Grid } from "@material-ui/core";
import Button from '@material-ui/core/Button';
import MUIDataTable from "mui-datatables";
import PageTitle from "../../PageTitle";
import { useHistory, withRouter } from "react-router-dom";
import axios from 'axios';
import Alert from "../../Toast/Toast"
import TrainingProgrammes from  "../../TrainingProgrammes/TrainingProgrammes"


let tempList = []

const TrainingPrograms = (props) => {

    const history = useHistory()
    const [programmeList, setProgrammeList] = useState([])
    const [tempProgrammeList, setTempProgrammeList] = useState([])
    const [selectedProgrammes, setSelectedProgrammes] = useState([])
    const [enrolled, setEnrolled] = useState([])
    const [reload, setReload] = useState({})
    const [message, setMessage] = useState('')
    const [isViewCart, setIsViewCart] = useState(false)
    const [isEnroll, setIsEnroll] = useState(false)
    const [inCartIds, setInCartIds] = useState([])

    let column =  [
      {
        name: "_id",
      },
        {
          name: "title",
          options: {
            filter: true,
          }
        },{
            name: "city",
            options: {
                filter: true,
            }
        },
        {
          name: "Description",
          options: {
            filter: true,
          }
        },{
          name: "programme_amount",
          options: {
                filter: true,
              }
        },
        {
          name: "training_start_date",
          options: {
            filter: true,
          }
        },{
            name: "training_end_date",
            options: {
                filter: true,
              }
        },
        {
          name: "dueDate",
          options: {
              filter: true,
          }
        },
      {
        name: "enrolled",
        options: {
          filter: true,
        }
    },
    {
      name: "centers",
      options: {
        customRowRender: (value) => {
          let location = value[value.length - 1] 
          return <p>{location ? location : ""}</p>
        }
      }
    },
    {
      name: "isSelected",
      options:{
        filter: true,
      }
    },
    {
      name: "_id",
      options:{
        filter: false,
      }
    },
    {
      name: "amount",
      label: "Amount",
      options:{
        filter: false,
      }
    },
    {
      name: "programmeFor",
      label: "programmeFor",
      options:{
        filter: false,
      }
    },
    {
      name: "dueDateTime",
    },
    {
      name: "today",
    },
    ]

    const updateProgrammeList = () => {

      let id = localStorage.getItem('teacher_id')
        if (id) {
          axios.get(`https://campusfield.in/api/training/get_teacher/${id}`)
          .then(res => {
            //console.log(res.data)
            if (res.data.error_code == 1) {
              for(let data of res.data.output) {
                // //console.log('********')
                //console.log(data.enrolment_programme)
                if (data.members.indexOf(id) !== -1) {
                  // //console.log('ehhhhh')
                  tempList = [...tempList, data.enrolment_programme]
                } 
              }
              axios.get("https://campusfield.in/api/training/training_programme")
                   .then(res => {
            //console.log(res)
                      if (res.data.error_code == 1) {
                        let filteredData = res.data.output.filter(el => {
                          el.isSelected = false
                          if(el.dueDate) {
                            let dueDate = new Date(el.dueDate.split('T')[0])
                            let date = new Date()
                            let d = date.getDate()
                            let m = date.getMonth()
                            let y = date.getFullYear()
                            let today = new Date(`${m + 1}-${d}-${y}`)
                          
                            if (tempList.indexOf(el._id) !== -1) {
                              // //console.log('!!!!!')
                              el.enrolled = true
                            } 
                            let cookies = document.cookie.split(';')
                            for (let data of cookies) {
                              let tempData = data.split('=')
                              if (tempData.length === 2) {
                              
                                if(String(tempData[0]).trim().toLowerCase() === 'selectedprogrammesall') {
                                  if (JSON.parse(tempData[1])?.indexOf(el._id) !== -1 && el.enrolled === false) {
                                    el.isSelected = true
                                  }
                                }
                            } 
                        }
                let type = localStorage.getItem("teacher_id") ? "teacher" : "school" 
                
                if(window.location.href.includes('teacher-training')){
                  if(String(el.programmeFor).toLowerCase() === 'teacher') {
                    el.dueDate = el.dueDate.split('T')[0]
                    el.today = today
                    el.dueDateTime = dueDate

                    return el
                  }
                }else if(window.location.href.includes('school-training')){
                  if(String(el.programmeFor).toLowerCase() === 'school') {
                    el.dueDate = el.dueDate.split('T')[0]
                    el.today = today
                    el.dueDateTime = dueDate

                    return el
                  }
                } else if(el.programmeFor === type &&  dueDate.getTime() > today.getTime() && !el.enrolled) {
                    el.dueDate = el.dueDate.split('T')[0]
                    el.today = today
                    el.dueDateTime = dueDate
  
                    return el
                }
              }
            }) 
              setProgrammeList(filteredData)
            }
          })
          .catch(err => {
            //console.log(err)
        })
            }
        })
        .catch(err => {
            //console.log(err)
          })
        } else {
          axios.get("https://campusfield.in/api/training/training_programme")
          .then(res => {
            //console.log(res)
            if (res.data.error_code == 1) {
              let filteredData = res.data.output.filter(el => {
                el.isSelected = false
                if(el.dueDate) {
                  let dueDate = new Date(el.dueDate.split('T')[0])
                  let date = new Date()
                  let d = date.getDate()
                  let m = date.getMonth()
                  let y = date.getFullYear()
                  let today = new Date(`${m + 1}-${d}-${y}`)
                  
                  let type = localStorage.getItem("teacher_id") ? "teacher" : "school"
                   if(window.location.href.includes('teacher-training')){
                    if(String(el.programmeFor).toLowerCase() === props.filterType.toLowerCase()) {
                      el.dueDate = el.dueDate.split('T')[0]
                      el.today = today
                      el.dueDateTime = dueDate

                      return el
                    }
                  }else if(window.location.href.includes('school-training')){
                    if(String(el.programmeFor).toLowerCase() === props.filterType.toLowerCase()) {
                      el.dueDate = el.dueDate.split('T')[0]
                      el.today = today
                      el.dueDateTime = dueDate

                      return el
                    }
                  } else if(localStorage.getItem('id')) {
                    if(el.programmeFor === type &&  dueDate.getTime() > today.getTime() && !el.enrolled) {
                      el.dueDate = el.dueDate.split('T')[0]
                      el.today = today
                      el.dueDateTime = dueDate
                      return el
                    }
                  }else {
                    props.history.push('/homepage')
                  }
                }
              }) 
              setProgrammeList(filteredData)
            }
          })
          .catch(err => {
            //console.log(err)
          })
        }
    }

    useEffect(() => {
      localStorage.removeItem('isNavigateToTraining')
      // if(selectedProgrammes.length === 0 && cookies.length === 2) {
      //   let parsedCookies = JSON.parse(cookies[1])

      //   setSelectedProgrammes()
      // }
      if(programmeList.length == 0) {
        updateProgrammeList()
      }
    }, [])

    const updateSelection = (i, value) => {
      let tempData;
      if(isViewCart) {
        tempData = [...tempProgrammeList] 
      } else {
        tempData = [...programmeList]
      }
      
      tempData = tempData.map(el =>{
        if(el._id == i) {
          el['isSelected'] = value
        }
        return el
      })

      let tempSelectedProgrammes = tempData.filter(el => {
        return el.isSelected == true
      })

      document.cookie = `selectedProgrammesAll=${JSON.stringify(tempSelectedProgrammes.map(el => el._id))}`
      document.cookie = `selectedProgrammes=${JSON.stringify(tempSelectedProgrammes)}`
      setSelectedProgrammes(tempSelectedProgrammes)
      setProgrammeList(tempData)
    }

    const onCart = () => {
      if(isViewCart === false) {
        setIsViewCart(!isViewCart)
        setIsEnroll(!isEnroll)
      } else {
        if(selectedProgrammes.length) {
          axios.post("https://campusfield.in/api/training/add_enrolment/0", {
                  members: [localStorage.getItem("teacher_id")],
                  enrolment_programme: selectedProgrammes.map(el => {
                    return el._id
                  })
                })
                .then(res => {
                    if(res.data.error_code == 1) {
                        setMessage("enrolled successfully")
                        document.cookie = `selectedProgrammesAll=${JSON.stringify([])}`
                        document.cookie = `selectedProgrammes=${JSON.stringify([])}`
                        setSelectedProgrammes([])
                        updateProgrammeList()
                        setTimeout(() => {
                          setMessage("")
                    }, 3000)
                      }
                      setIsViewCart(!isViewCart)
                      setIsEnroll(!isEnroll)
                })
                .catch(err => {
                  setIsViewCart(!isViewCart)
                  setIsEnroll(!isEnroll)
                  setMessage("network error!")
                  setTimeout(() => {
                  setMessage("")
            }, 3000)
          })
        }
      }
    }
    
    return (
    <div
    style={localStorage.getItem('id') ? {} : {width: "95%", margin: "auto", paddingTop: "9%", background: "white"}} 
    id="programs">
    {/* <div style={{display: "flex", justifyContent: "space-between", height: "auto", width: "100%", margin: "auto", marginBottom: "1rem", marginTop: "2rem"}}>
      <h4 style={{fontSize: "1.3rem", color: "gray"}}>{selectedProgrammes.length} Items in cart</h4>
      <div
      style={{marginRight: "2rem"}}
      >
          <i
          style={{fontSize: "2rem"}} 
          className="fa fa-shopping-cart"/>
      </div> */}
      
      {/* <Button
      variant="contained"
      color="primary"
      disabled={!selectedProgrammes.length > 0}>
        enroll
      </Button> */}
    {/* </div> */}

    <PageTitle title={`Training Programmes`} button="View Cart" add_new={() => {props.history.push(localStorage.getItem("type") === "teacher"? '/teacher/cart': '/institution/cart')}}/>
    
    <Grid container spacing={4}>
        <Grid item xs={12}>
          <MUIDataTable
            title="Training Programmes"
            data={isViewCart ? selectedProgrammes : programmeList}
            columns={column}
            options={{  
              selectableRows: true,
              customRowRender: (data, dataIndex, rowIndex) =>{
                if (props.filterType & String(data[13]).toLowerCase() == String(props.filterType).toLowerCase()) {
                  return (
                    <TrainingProgrammes
                      updateSelection={updateSelection}
                      id={data[11]}
                      amount={data[12]}
                      index={dataIndex}
                      reload={setReload}
                      dueDate={data[7]}
                      dueDateTime={data[14]}
                      today={data[15]}
                      title={data[1]}
                      enrolled={data[8]}
                      id={data[0]}
                      duration={data.duration}
                      startDate={data[5]}
                      endDate={data[6]}
                      fee={data[4]}
                      centres={data[9] ? data[9] : []}
                      city={data[2]}
                      isSelected={data[10]}
                      description={data[3]}
                      enroll={""}
                      />
                      )
                } else {
                  return (
                    <TrainingProgrammes
                      updateSelection={updateSelection}
                      id={data[11]}
                      amount={data[12]}
                      index={dataIndex}
                      reload={setReload}
                      dueDate={data[7]}
                      dueDateTime={data[14]}
                      today={data[15]}
                      title={data[1]}
                      enrolled={data[8]}
                      id={data[0]}
                      duration={data.duration}
                      startDate={data[5]}
                      endDate={data[6]}
                      fee={data[4]}
                      centres={data[9] ? data[9] : []}
                      city={data[2]}
                      isSelected={data[10]}
                      description={data[3]}
                      enroll={""}
                      />
                      )
                }
              },
              selectableRowsOnClick: true,
              filter: true,
              onRowsSelect: (a, b) => {
                b.map((el) => {
                  let tempList = [];
                //   suggestedList.push(data[el.index]._id)
                  // se tSuggested(tempList)
                })
              },
            }}
          />
        </Grid>
        {props.location.state ? <Alert message={props.location.state.message} color="green"/> : null}        
    </Grid>
    {message ? <Alert message={message} color={message.search("network") != -1 ? "red" : "green"}/> : null}        

    </div>
)
}

export default withRouter(TrainingPrograms);
