import React, { useEffect, useState } from 'react';
import styles from "./News.module.css"
import PageTitle from "../../PageTitle";
import Alert from "../../Toast/Toast"
import EmptyList from "../../UI/EmptyList/EmptyList"
import {getNews} from "../../../MockService/Teacher"
import axios from "axios"


const News = (props) => {

  const [news, setNews] = useState([]);
  let newsList;

  useEffect(() => {
    if(!news.length) {
      axios.get('https://campusfield.in/api/site_management/news')
      .then(res => {
        //console.log(res)
        if(res.data) {
          setNews(res.data.output)
        }
      })
      .catch(err => {
        //console.log("error")
      })
    }
  }, [])

  if(news && news.length) {
    newsList = news.map((el) => {
      return (
        <div className={styles["experience-container"]}>
        <h4>
            {el.title}
        </h4>
        <div className={styles["detail-container"]}>
            <h3>
                {el.description}
            </h3>
        </div>
    </div>
      )
    })   
  }

  return (
    <>
    <PageTitle title="Announcements"/>
    {newsList ? newsList : 
          <EmptyList
          message="No news for you!"/>}
    </>
  );
}

export default News;
