import React, { useEffect, useState} from 'react';
// import styles from "./add_new.module.css"
import { Grid } from "@material-ui/core";
import MUIDataTable from "mui-datatables";
// import PageTitle from "../PageTitle";
import PageTitle from "../../../PageTitle/PageTitle";
import { useHistory, withRouter } from "react-router-dom";
import axios from "axios"
import * as constants from "../../constants/constants"
import Alert from "../../../Toast/Toast"
import Button from '@material-ui/core/Button';

const EnrollFaculties = (props) => {

    const history = useHistory()
    
    const [teachers, setTeachers] = useState([])
    const [message, setMessage] = useState("")
    const [selectedTeachers, setSelectedTeachers] = useState([])
    const [enrolled, setEnrolled] = useState([])
    let selected = []
    let selectedObject = []

    if(selected.length) {
      //console.log(selected)
    }

    let column =  [
      // {
      //   name: "Profile_pic",
      //   label:'profile picture',
      //   options: {
      //     filter: true,
      //     customBodyRender: (value, meta, updatevalue) => {
      //       //console.log(meta)
      //       return <div style={{
      //         backgroundImage: `url(${value})`,
      //         borderRadius: "50%",
      //         height: "40px",
      //         backgroundColor: "#0664b5",
      //         display: "flex",
      //         alignItems: 'center',
      //         justifyContent: 'center',
      //         width: "40px",
      //       backgroundSize: "cover"}}>{String(value).trim() == "" ? <p style={{margin: 0, color: "white", textTransform: "uppercase"}}>{meta.rowData[1][0]}</p> : null}</div>
      //     }
      //  }
      // },
        {
          name: "First_name",
          label:'First Name',
          options: {
            filter: true,
            checked: true
          }
        },{
          name: "Last_name",
          label:'Last Name',
          options: {
            filter: true,
            checked: true
          }
        },
        // {
        //     name: "Gender",
        //     label:'gender',
        //     options: {
        //         filter: true,
        //     }
        // },
        {
          name: "subject",
          label:'Subject',
          options: {
              filter: true,
          }
      },
      {
        name: "isFresher",
        label:'Experienced',
        options: {
          filter: true,
          customBodyRender: (value, meta, updatevalue) => {
            //console.log(meta)
            return <div >{String(value).toLowerCase() === "yes" ? "NO" : "YES"}</div>
          }
       }
      }
    ]

    useEffect(() => {
      let id = localStorage.getItem("i_id")
      if(!props.location.state) {
        props.history.goBack()
      }


      if(props.location.state?.title) {

        axios.get(`https://campusfield.in/api/institution/dashboard/${id}`)
        .then(res => {
          if(res.data.error_code === 1) {
            let teachers = res.data.output[0].Faculties
            let temp = []
            for(let t of teachers) {
              if(props.location.state?.teachers.indexOf(t.id) !== -1) {
                temp.push(t)
              }
            }
            setTeachers(temp)
          }
        })
        .catch(err => {

        })
      } else {
        
        axios.get(`https://campusfield.in/api/institution/dashboard/${id}`)
        .then(res => {
          if(res.data.error_code === 1) {
            let teachers = res.data.output[0].Faculties
            console.log(props.location.state.programme.programme_id)

          //console.log(teachers)
          let enrolled = []
          if(props.location.state?.programme.programme_id && teachers.length) {

            let pro_id = props.location.state?.programme.programme_id
            axios.get(`https://campusfield.in/api/training/get_enrolment/${pro_id}`)
            .then((res) => {

              if(res.data.output.length) {
                teachers = teachers.filter(el => {
                  const el_id = el._id ? el._id : el.id
                  return res.data.output[0].members.indexOf(el_id) == -1
                })
                enrolled = res.data.output[0].members
                console.log(enrolled)
                //console.log(enrolled)
                setEnrolled(enrolled)
              } 
              if(props.location.state.teachers) {
                let teachers_id = teachers.map((el) => {
                  return el.id ? el.id : el._id
                })
                let indexArray = []
                for(let a of props.location.state.teachers) {
                  let index = teachers_id.findIndex(el => el == a.id || el == a._id)
                  if (index !== -1) {
                    indexArray.push(index)
                  }
                }
                setSelectedTeachers(indexArray)
              }
              setTeachers(teachers)
              
            })
            .catch(err => {
              //console.log(err)
            })
          }
          if(!props.location.state.programme.programme_id) {
            if(props.location.state.teachers) {
              let teachers_id = teachers.map((el) => {
                return el.id ? el.id : el._id
              })
              let indexArray = []
              for(let a of props.location.state.teachers) {
                let index = teachers_id.findIndex(el => el == a.id || el == a._id)
                if (index !== -1) {
                  indexArray.push(index)
                }
              }
              setSelectedTeachers(indexArray)
            }
            setTeachers(teachers)
          }
        }
      })
      .catch(err => {
        //console.log(err)
      })}
    },[])
    
    const enRoll = () => {
      if(props.location.state?.title) {
        props.history.goBack()
        return
      }
      if(selected.length) {
        if(props.location.state.isUpdate) {
          axios.put("https://campusfield.in/api/cart/" + props.location.state._id, {
                ...props.location.state.programme,
                _id: props.location.state._id,
                teachers: [
                  ...selectedObject
                ],
                enrolled: enrolled
              })
              .then(res => {
                  if(res.data.error_code == 1) {
                      setMessage("Successfully Updates the  Cart")
                  }
                  props.history.push('/institution/cart')
              })
              .catch(err => {
                setMessage("network error!")
                setTimeout(() => {
                setMessage("")
                }, 3000)
              })
        } else {
          axios.post("https://campusfield.in/api/cart/", {
                ...props.location.state.programme,
                teachers: [
                  ...selectedObject
                ],
                enrolled: enrolled
              })
              .then(res => {
                  if(res.data.error_code == 1) {
                      setMessage("Successfully Added to  Cart")
                  }
                  props.history.push('/institution/cart')
              })
              .catch(err => {
                setMessage("network error!")
                setTimeout(() => {
                setMessage("")
                }, 3000)
              })
      }
        }
        
    }

    return (
    <>
    <PageTitle title={props.location.state?.title ? `${props.location.state?.title}`: "Enroll"} 
               button={props.location.state?.title ? "Back" : props.location.state?.isUpdate ? "Update Cart" : "Add to Cart"} 
               add_new={enRoll} />
    <Grid container spacing={4}>
        <Grid item xs={12}>
          <MUIDataTable
            title="Existing Faculty List"
            data={teachers}
            columns={column}
            options={{  
              rowsSelected: selectedTeachers,
              filterType: "checkbox",
              selectableRows: props.location.state?.title ? false:true,
              filter: true,
              selectableRowsOnClick: props.location.state?.title ? false:true,
              onRowsSelect: (a, b) => {
                  let selectedRows = b.map(el => {
                    return teachers[el.index].id ? teachers[el.index].id : teachers[el.index]._id 
                  })
                  selectedObject = b.map(el => {
                    return teachers[el.index]
                  })
                  selected = selectedRows
                },
              onRowClick: (rowData, rowState) => {
              if("") {
                history.push({
                  "pathname": "/institution/faculties/edit",
                  "state": {
                    data: ""
                  }
                })
              } else {

              }
            }}
          }
          />
        </Grid>
    </Grid>
    {message ? <Alert message={message} color={message.search("network") != -1 ? "red" : "green"}/> : null}        

    </>
)

}
export default  withRouter(EnrollFaculties);
