 const data = [
  { name: "Joe James", company: "Test Corp", city: "Yonkers", state: "NY" },
  { name: "John Walsh", company: "Test Corp", city: "Hartford", state: "CT" },
  { name: "Bob Herm", company: "Test Corp", city: "Tampa", state: "FL" },
  { name: "James Houston", company: "Test Corp", city: "Dallas", state: "TX" },
 ];


export default function News() {
  const [data, setData] = useState([])
  const [updatemessage, setupdatemessage] = useState(false)
  const [title, setTitle] = useState('')
  const [description, setDescription] = useState('')

  const delete_function = (id) => {
    axios.delete("https://campusfield.in/api/site_management/news/" + id)
      .then(res => {
        if (res.data) {
          if (res.data.error_code) {
            getDetails()
          }
        }
      })
  }
  const columns = [
    {
     name: "title",
     label: "Title",
     options: {
      filter: true,
      sort: true,
     }
    },
    {
     name: "description",
     label: "Description",
     options: {
      filter: true,
      sort: false,
     }
    },
  
    {
     name: "create_date",
     label: "Create date",
     options: {
      filter: true,
      sort: false,
     }
    },
    {
      name: "_id",
      label: "Name",
      options: {
        filter: true,
        display: false,
        sort: true,
      }
    },
    {
      name: "status",
      label: "Actions",
  
      options: {
        filter: false,
        sort: false,
        empty: true,
  
        customBodyRender: (value, tableMeta, updateValue) => {
          //console.log()
          return (
            <div>
  
              <Button
                onClick={() => { delete_function(tableMeta.rowData[3]) }}
                color="secondary"
                size="small"
                className="px-2"
                variant="contained"
              >
                Delete
                </Button>
            </div>
  
          );
        }
      }
    },
   ];


  const onChangeTextHandler = (event, i) => {
    let value = event.target.value
  
    if (i === 1) {
      setTitle(value)
    }
    if (i === 2) {
      setDescription(value)
    }
  
  }

  useEffect(() => {
    getDetails()
  }, []);

  const getDetails = () =>{
    axios.get("https://campusfield.in/api/site_management/news")
    .then(res => {
      if (res.data) {
        if (res.data.error_code) {
          // //console.log(res.data.output, 'dat')
          setData(res.data.output)
        }
      }
    })
  }
  const addAnnoucement = () =>{
    if(title != '' && description != ''){
      // setOpen(true)
      let value = new FormData()
      value.append("title", title)
      value.append("description", description)
      // value.append("item_img", file)
      axios.post("https://campusfield.in/api/site_management/news", value)
        .then((res) => {
          //console.log(res)
  
          setTitle('')
          setDescription('')
          // setFile('')
          getDetails()
          // setOpen(false)
        })
        .catch((err) => {
          //console.log(err)
        })
      }else{
        setupdatemessage(true)
        setTimeout(function () { setupdatemessage(false) }, 2000);
      }
  }
  return (
    <>
      <PageTitle title="Announcements" button="Add" add_new={addAnnoucement} />
      <div style={{marginBottom: "2rem"}}>
      <TextField id="standard-basic" required value={title} label="Add Title" style={{ width: "45%", marginRight: "10px" }} onChange={(event) => { onChangeTextHandler(event, 1) }} />
        <TextField
          value={description}
          required
          // style={{ width: "45%", height: "45%" }}
          style={{ width: "45%" }}
          id="standard-multiline-basic"
          label="Description"
          multiline
          // rows="4"
          onChange={(event) => { onChangeTextHandler(event, 2) }}
        />
      </div>
      <Grid container spacing={4}>
        <Grid item xs={12}>
          <MUIDataTable
            title="News List"
            data={data}
            columns={columns}
            options={{
              filterType: "checkbox",
            }}
          />
        </Grid>

      </Grid>
      {updatemessage  ? <Alert message="Need to enter Title & Decription" color="red"/>: null}

    </>
  );
}
