// import React from "react";
import { Grid } from "@material-ui/core";
import MUIDataTable from "mui-datatables";
import React, { useState ,useEffect} from "react"
import Alert from "../../Toast/Toast"
import styles from "./Institutions.module.css"
import {Places} from "../../Map/Map"
import {MyMapComponent} from "../../Map/Map"
// import * as constants from "./..//Home/constants/constants"

import * as constants from "../constants/constants" 
import axios from "axios"
// components
import PageTitle from "../../PageTitle";
import { useHistory } from "react-router";
import Button from '@material-ui/core/Button';

// import Widget from "../../components/Widget";
// import Table from "../dashboard/components/Table/Table";

// data

let suggestedList = [] 
let suggestedEmail = []

export default function Institutions(props) {
  const [onInitiateSuggest, setOnInitiateSuggest] = useState(false)
  const [message, setMessage] = useState("")
  const [isSuccess, setIsSuccess] = useState(false)
  let updatemessage = props.location.state ? props.location.state.updateMessage : ""
  const [data, setData] = useState([])
  const [isAutoComplete, setIsAutocomplete] = useState(true)
  const [suggest_on, setsuggest_on] = useState(false)

  
  const [lat, setLat] = useState(10.850516)
  const [long, setLong] = useState(76.271080)
  const [isValidLocation, setIsValidLocation] = useState(true)
  const [row, setRow] = useState([])
  const [isShowMarker, setIsShowMarker] = useState(false)
  const [isFaculties, setIsFaculties] = useState("")
  let isState = props.location.state
    const [location, setLocation] = useState("")
    const [date, setDate] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [phone, setPhone] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [description, setDescription] = useState({
      val: "",
      isValid: false,
      touched: false
    })
    
    const history = useHistory();
    const validateForm = () => {
        return  description.isValid && location && phone.isValid && date.isValid
    }

    const validate = (val, i) => {

        if(i == 3) {
            return /^\d+$/.test(val) && val.length == 10
        }

        return val.length
    } 

    const setTouchedTrue = () => {
        setDescription({
            ...description,
            touched: true
        })

        setDate({
            ...date,
            touched: true
        })

        setPhone({
            ...phone,
            touched: true
        })
    }

    const clearFields = () => {
        setDescription({
            val: "",
            isValid: false,
            touched: false
        })

        setPhone({
            val: "",
            isValid: false,
            touched: false
        })
        setDate({
            val: "",
            isValid: false,
            touched: false
        })
        setLocation("")
    }

    const onChangeTextHandler = (event, i) => {
        let value = event.target.value

        // if(i === 1) {
        //   //console.log("hello")
        //     setLocation({
        //         ...location,
        //         val: value,
        //         isValid: validate(value, i)
        //     })
        // }

        
        if(i === 2) {
          setDate({
                ...date,
                val: value,
                isValid: validate(value, i)
            })
        }

        
        if(i === 3) {
            setPhone({
                ...phone,
                val: value,
                isValid: validate(value, i)
            })
        }
        
        
        if(i === 4) {
            setDescription({
                ...description,
                val: value,
                isValid: validate(value, i)
            })
        }
    }

  const column= isFaculties ? ['name','gender'] : [
    {
      name: "applicant_name",
      label:'Applicant',
      options: {
        filter: true,
      }
    },
    {
      name: "select_status",
      label:'Interview Scheduled',
      options: {
        filter: true,
        customBodyRender: (value) => {
          return (
            <div style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
              {
                value === true ? <i style={{color: "green", margin: "auto"}} className="fa fa-check"/> :  <i style={{color: "red"}} className="fa fa-close"/>
              }
            </div>
          )

        }
      }
    }
    ,
      {
      name: "profile",
      label:'Qualification',
      options: {
        filter: false,
        customBodyRender: (value, tableMeta, updateValue) => {
          //console.log(value)
          let desig;
          try{
            desig = JSON.parse(value)['Qualification']
            desig = desig[desig.length - 1].qualification
          } catch(error) {
            // //console.log(error)
          }
          return (
            <p>
              {desig ? desig : ''}
            </p>
          )
        }
      }
    },
    {
      label: "Date",
      name: "date",
      options: {
        filter: false,
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <p>
              {value ? value.split('T')[0] : ''}
            </p>
          )
        }
      }
    }]
  

  useEffect(() => {
    if(data.length === 0) {
      setIsFaculties(props.match.url.search("faculties") != -1)
      if(props.match.url.search("faculties") != -1) {
        getFaculties()
      } else {
        getDetails()
      }
    }

  },[]);
  const getDetails = () =>{
    //console.log(props)

    let Job_id = localStorage.getItem("job_id");
    axios.get("https://campusfield.in/api/job/getApplicantList/"+Job_id)
    .then(res => {
      if(res.data){
        //console.log(res.data)
        setData(res.data.output)
      }
    })
  }


  const getFaculties = () => {
    let id = localStorage.getItem("id")
    axios.get(constants.url + "institution/dashboard/" + id)
        .then((res) => {
            if(res.data.error_code === 1) {
              // //console.log({...res.data.output} + "hello")
              setData(res.data.output[0].Faculties) 
            }
        })
        .catch((err) => {
            //console.log(err)
        })

  }

  const getDate = () => {
    let date = new Date()
    let d = date.getDate()
    let m = date.getMonth()
    let y = date.getFullYear()
    //console.log(`${y}-${m + 1}-${d}`)
    //console.log('hello')
    return `${y}-${m + 1}-${d}`
  }

  const onAdd = () => {
    history.push("/institution/faculties/new")
  }

  let data_map;

  if(!isFaculties && data.length) {
    data_map = data.map((el) => {
      //console.log(el)
      return {
        "user_id": el.user_id.slice(0, 4),
        "resume_path": el.resume_path,
        "date": el.date,
        "applicant_name": el.applicant_name,
        "select_status": el.select_status,
        "profile": el.profile
      }
    })
  }
  
  if(isFaculties) {
      if(data.length) {
        data_map = data.map((el) => {
          return {
            "name": `${el.First_name}  ${el.Last_name}`,
            "gender": el.Gender
          }
        })
      }
  }

  if(suggestedList.length) {
    // //console.log(suggestedList)
  } 

  const onSuggest = () => {
    if(!location) {
      setIsValidLocation(false)
    }

    let isValid = validateForm()
    setTouchedTrue()
    if(isValid) {
      
      setsuggest_on(true)
      axios.post("https://campusfield.in/api/job/select", {
        "value": suggestedList,
        "emails": suggestedEmail,
        "details": {
          location: location,
          date: date.val,
          instruction: description.val,
          phone: phone.val
        }
      })
      .then((res) => {
          if(res.data.error_code === 1) {
            suggestedList = []
            suggestedEmail = []
            setSuggestion(false)
            setsuggest_on(false)

            setMessage("Successfully suggested")
                  setIsSuccess(true)
                  setOnInitiateSuggest(false)
                  // setTimeout(() => {
                  //     setMessage("")
                  //   }, 3000)
            props.history.push("/institution/jobs")
            // let id = localStorage.setItem("id")
          }
      })
      .catch((err) => {
      setsuggest_on(false)

        suggestedList = []
        suggestedEmail = []
        setMessage("Something went wrong!")
        setIsSuccess(false)
        setOnInitiateSuggest(false)
        setTimeout(() => {
            setMessage("")
          }, 3000)
      })
    }
  }

  const suggestFaculties = () => {
    if(suggestedList.length) {
      setOnInitiateSuggest(true)
    }
  }

  const handleMarkerClick = (event) => {
    // //console.log(this.state.location)
  }

  const onMapClick  = async (event) => {
    let lat =  event.latLng.lat()
    let long = event.latLng.lng()
    //console.log(event)
    await axios.post(`https://maps.googleapis.com/maps/api/geocode/json?latlng=${lat},${long}&sensor=true&key=AIzaSyAyQTUfdF5HDuUEXesoLLhIXxldK0YgREg`)
    .then(res => {
      if(res.status == 200) {
        //console.log(res)
        let loc = res.data.results[0].formatted_address
        // loc = loc.slice(1, loc.length).join(" ")

        setLat(lat)
        setLong(long)
        setIsShowMarker(true)
        //console.log(res.data)
        setLocation(loc)
        
      }
    })
    .catch(err => {
      //console.log(err)
    })
  }

  const onLocationChange = (event) => {
      setLocation(event.target.value)
      setIsValidLocation(true)
  }


  const [isSuggestion, setSuggestion] = useState(false)

  return (
    <>
    {onInitiateSuggest && isAutoComplete ? 
      <div className={styles["backdrop"]}>
                        <div className={styles["verification-alert"]}>
                        <form className={styles["qualification-form"]}>
                {
                  !isValidLocation ? <p style={{color: "red"}}>Please select location from list</p> : null
                }
                <div className={styles["input-container"]}>
                    <label>
                        location
                    </label>
                  <div style={{display: "flex", justifyContent: "space-around"}}>
                   <Places
                   onChange={(event) => {onLocationChange(event)}}
                   value={location}
                   onPlaceSelected={(place) => {setLocation(place.formatted_address)}}
                   />
                   <span
                   onClick={() => {setIsAutocomplete(false)}} 
                   className={styles['map-pin']}>
                   <i class="fa fa-map-pin" aria-hidden="true"></i>
                  </span>
                </div>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Interview Date
                    </label>
                    <input
                    type="date"
                    value={date.val}
                    min={new Date().toISOString().split("T")[0]}
                    style={date.touched && !date.isValid ? {border: "1px solid red"} : null}
                    onBlur={() => {setDate({...date, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 2)}}
                    />
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Phone
                    </label>
                    <input
                    value={phone.val}
                    style={phone.touched && !phone.isValid ? {border: "1px solid red"} : null}
                    onBlur={() => {setPhone({...phone, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 3)}}/>
                </div>
                    <div className={styles["description"]}>
                        <label>
                            Job description *
                        </label>
                        <textarea
                        style={description.touched && !description.isValid ? {border: "1px solid red"} : null}
                        onBlur={() => {setDescription({
                            ...description,
                            touched: true
                        })}}
                        value={description.val}
                        onChange={(event) => { onChangeTextHandler(event, 4)}}/>
                    </div>
                </form>
                <div className={styles["button-container"]}>
                <Button
                    style={{marginRight: "0.9rem"}}
                    variant="contained"
                    color="primary"
                    disabled = {suggest_on}
                    onClick={onSuggest}
                >
                    Schedule Interview
                </Button>
                <Button
                    variant="contained"
                    color="secondary"
                    onClick={() => {
                            clearFields()
                            suggestedList = []
                            suggestedEmail = []
                            setOnInitiateSuggest(false)
                            setsuggest_on(false)
                          
                          }
                    }
                >
                    Cancel
                </Button>
            </div>
                        </div>
                      </div>: onInitiateSuggest && !isAutoComplete ? <div className={styles["backdrop"]}>
          <div style={{height: '80%', width: '80%', maxHeight: "800px",maxWidth: '1000px', background: "white", padding: "2rem", overflow: "scroll", position: "relative"}}>
          <i
                 onClick={() => {setIsAutocomplete(true)}} 
                 style={{position: "absolute", top: 10, right: 10, cursor: "pointer"}} className="fa fa-close"/>
         <MyMapComponent
          isMarkerShown={isShowMarker}
          lat={lat}
          long={long}
          onMapClick={onMapClick}
          onMarkerClick={handleMarkerClick}
        />
        <div
        onClick={() => {setIsAutocomplete(true)}} 
        style={{margin: "auto", height: '40px', width: "70px", display: "flex", justifyContent: "center", alignItems: "center",  backgroundColor: "blue", color: "white", marginTop: "1rem"}}>
          Ok
        </div> 
        </div></div>: null
      }
      <PageTitle title="Applicants" button="Select" add_new={() => {suggestFaculties()}}/>
      <Grid container spacing={4}>
        <Grid item xs={12}>
          <MUIDataTable
            title="Applicant List"
            data={data_map}
            columns={column}
            options={{  
              filterType: "checkbox",
              selectableRows: 'multiple',
              filter: true,
              selectableRowsOnClick: true,
              onRowsSelect: (a, b) => {
                suggestedList = []
                suggestedEmail = []
                b.map((el) => {
                  //console.log(data[el.index])
                  let tempList = [];
                  suggestedList.push(data[el.index]._id)
                  suggestedEmail.push(data[el.index].email)
                  // setSuggested(tempList)
                })
                //console.log(suggestedList)
              },
              // onRowClick: (rowData, rowState) => {
              // if(isFaculties) {
              //   history.push({
              //     "pathname": "/institution/faculties/edit",
              //     "state": {
              //       data: data[rowState.rowIndex]
              //     }
              //   })
            //   } else {

            //   }
              
            }
          }
          />
        </Grid>
        {/* <Grid item xs={12}>
          <Widget title="Material-UI Table" upperTitle noBodyPadding>
            <Table data={mock.table} />
          </Widget>
        </Grid> */}
      </Grid>
      {
        message ? <Alert message={message} color={isSuccess ? "green" : "red"}/> : null
      }
      {updatemessage  ? <Alert message={updatemessage} color="green"/>: null}
    </>
  );
}
