import React, {useEffect, useState} from 'react';
import styles from "./Placements.module.css"
import PageTitle from "../../PageTitle";
import { useHistory } from "react-router-dom";
import axios from "axios"
import * as constants from "../constants/constants"
import Alert from "../../Toast/Toast"
import Placement from "./Placement/Placement"
import EmptyList from "../../UI/EmptyList/EmptyList"

const Placements = (props) => {
    let updatemessage = props.location.state ? props.location.state.updateMessage : ""
    const history = useHistory();
    const [data, setData] = useState([])
    const [message, setMessage] = useState("")
    let placements;

    const onDeletePlacements = (placementId) => {
      let id = localStorage.getItem("i_id")
  
      axios.post(constants.url + "institution/Placement/" + id + `/${placementId}/0`, {
        "Placement":{
            "id": placementId,
        }
      })
      .then((res) => {
          if(res.data.msg.search("delete") != -1) {
            getFacilities()
            setMessage("deleted successfully")
          }
      }).catch((err) => {
          setMessage("network error!")
          setTimeout(() => {
              setMessage("")
            }, 3000)
          //console.log(err)
      })
  }

    let id = localStorage.getItem("id")
    useEffect(() => {
      //console.log(data)
      if(data.length === 0) {
        getFacilities()
      }
      })

      const getFacilities = () => {
        axios.get(constants.url + "institution/dashboard/" + id)
        .then((res) => {
            if(res.data.error_code === 1) {
              //console.log(res.data.output + "hello")
              setData(res.data.output[0]) 
            }
        })
        .catch((err) => {
            //console.log(err)
        })
      }

    if(data.Placement && data.Placement.length) {
      //console.log(data)
      placements = data.Placement.map((el, i) => {
        //console.log({...el})
        return (
          <Placement
          onDelete={onDeletePlacements}
          id={el.id}
          key={i}
          title={el.title}
          description={el.description}
          />
        )
      })   
    }

    const move_to_edit = () => {
      history.push("/institution/placements/new")
    }

  return (
    <>
    <PageTitle title="Placements" button="Add" add_new={move_to_edit} />
        <div className={styles["sub-container"]}>
        {placements && placements.length ? placements : <EmptyList message="No placements added!"/>}          
        </div>
        {message ? <Alert message={message} color={message.search("network") != -1 ? "red" : "green"}/> : null}
        {updatemessage  ? <Alert message={updatemessage} color="green"/>: null}
    </>
  );
}

export default Placements;
