import React, {useEffect, useState} from 'react';
import styles from "./Experiences.module.css"
import Experience from './Experience/Experience';
import CommonHeader from "../CommonHeader/CommomHeader"
import PageTitle from "../../PageTitle";
import { useHistory } from "react-router-dom";
import axios from "axios"
import * as constants from "../constants/constants"
import { getThemeProps } from '@material-ui/styles';
import Alert from "../../Toast/Toast"
import EmptyList from '../../UI/EmptyList/EmptyList';

const TotalYears = (props) => {
  
return <p>{props.years}</p>
}

const Experiences = (props) => {
  const history = useHistory();
  let updatemessage = props.location.state ? props.location.state.updateMessage : ""
  const [data, setData] = useState([])
  const [isFresher, setIsFresher] = useState("")
  let experiences;
  const [totalYears, setTotalYears] = useState(0);
  let tempTotalYears = 0
  const [isExperienceRendered, setIsExperienceRendered] = useState(false)
  const [loading, setLoading] = useState(false)
  const [message, setMessage] = useState("")

  const onDeleteExperience = (expId) => {
    let id = localStorage.getItem("id")
    //console.log(expId)
    axios.post(constants.url + "teachers/edit/" + id + "/Experience/" + expId + "/0", {"Experience": {
        "id": props.id,
    }})
    .then((res) => {
        if(res.data.message.search("delete") != -1) {
          getExperience()
          setMessage("deleted successfully")
        }
    }).catch((err) => {
        setMessage("network error!")
        setTimeout(() => {
            setMessage("")
          }, 3000)
        //console.log(err)
    })
}
  
  const add_new_exp = () => {
    history.push('/teacher/experiences/new')
  }
  
  const addTotalYears = (diff) => {
    // setTotalYears(diff + totalYears)
    // setIsExperienceRendered(true)
  }

  if(data.Experience && data.Experience.length) {
    experiences = data.Experience.map((el) => {
      return (
        <Experience
        id={el.id}
        institution={el.institution}
        subject={el.subject}
        // setTotalYears={(years) => {addTotalYears(years)}}
        designation={el.designation}
        from={el.from}
        jobType={el.jobType}
        isCurrentlyWorking={el.isCurrentlyWorking}
        onDelete={onDeleteExperience}
        to={el.to}
        description={el.description}
        />
      )
    })
  }


  let id = localStorage.getItem("id")
  useEffect(() => {
    if(data.length === 0) {
      getExperience()
    }
    setLoading(false)
  })

  const getExperience = () => {
    axios.get(constants.url + "teachers/dashboard/" + id)
      .then((res) => {
          //console.log(res.data)
          setIsFresher(res.data.output[0].isFresher)
          if(res.data.error_code === 1) {
            if(typeof res.data.output[0].Qualification[0] === "string") {
              res.data.output[0].Qualification = JSON.parse(res.data.output[0].Qualification[0])
            }

            
            if(typeof res.data.output[0].Certification[0] === "string") {
              res.data.output[0].Certification = JSON.parse(res.data.output[0].Certification[0])
            }

            
            if(typeof res.data.output[0].Experience[0] === "string") {
              res.data.output[0].Experience = JSON.parse(res.data.output[0].Experience[0])
            }
            setData(res.data.output[0]) 
          }
      })
      .catch((err) => {
          //console.log(err)
      })
  }

  const toggleisFresher = () => {
      let value = document.getElementById("yes").checked    
      let noValue = document.getElementById("no").checked    

      if(!value && !noValue) {
        return 
      }

      //console.log(data)
      let id = localStorage.getItem('teacher_id')
      let formData = new FormData()
      formData.append("id", id)
      // formData.append("profile", profilePic.val)
      formData.append("First_name", data.First_name)
      formData.append("Last_name", data.Last_name)
      formData.append("Pass", data.Pass)
      formData.append("DOB", data.DOB)
      formData.append("Phone_Number", data.Phone_Number)
      formData.append("Email", data.Email)
      formData.append("Gender", data.Gender)
      formData.append("About", data.About)
      formData.append("type", "teacher")
      formData.append("State", data.State)
      formData.append("Street", data.Street)
      formData.append("District", data.District)
      formData.append("Pin", data.Pin)
      formData.append("item_img", data.item_img)
      formData.append("Resume", data.Resume)
      formData.append("Qualification", JSON.stringify(data.Qualification))
      formData.append("Certification", JSON.stringify(data.Certification))
      formData.append("Training_from_campus", [])
      formData.append("Experience", JSON.stringify(data.Experience))
      formData.append("Transactions", [])
      formData.append("applied_jobs", [])
      formData.append("isFresher", `${value}`)
      formData.append("is_subscribed", data.is_subscribed)
      formData.append("is_skip", data.is_skip)
      // setLoading(true)
       axios.post(constants.url + "teachers/profile", formData)
      .then((res) => {
        //console.log(res)
        window.location.reload()
      }).catch((err) => {
        //console.log("error")
        })
        //console.log("hellllllllllll")
      } 
      

  if(loading) {
    return "loading"
  }
 
  return (
    <>
        {
          !loading &&  isFresher ?
          <>
          <p style={{textAlign: "center"}}>If you are experienced please select No!</p>
          <div className={styles["fresher-container"]}>
            <div className={styles['fresher']} data-aos="fade-up">
              <p>Are you a fresher?</p>
              <div> 
                <input type="radio" name="isfresher" id="yes"/>
                <label for="yes">Yes</label>
              </div>
              <div>
                <input type="radio" name="isfresher" id="no"/>
                <label for="no">No</label>
              </div>
              <div
              onClick={() => {toggleisFresher()}} 
              className={styles["is-fresher-button"]}>
                  <p>Submit</p>
              </div>
            </div>
          </div>
          </>
           :
           <>
           <PageTitle title="Experience" button="Add" add_new={add_new_exp}/>
            <div className={[styles["sub-container"], experiences !== undefined ? null : styles["empty"]].join(" ")}>
              {experiences !== undefined ? experiences :
              <EmptyList
              message="No Experiences added to show!"/>}
            </div>
          {message ? <Alert message={message} color={message.search("network") != -1 ? "red" : "green"}/> : null}
          {updatemessage  ? <Alert message={updatemessage} color="green"/>: null}
          </>}
    </>
  );
}

export default Experiences;
