import React, { useEffect, useState} from 'react';
import styles from "./ViewTrainingCenter.module.css"
import { Grid } from "@material-ui/core";
import MUIDataTable from "mui-datatables";
import PageTitle from "../../../PageTitle/PageTitle";
import { useHistory, withRouter } from "react-router-dom";
import Info from "../../../Institutution/Dashboard/Info/Info"
import EmptyList from "../../../UI/EmptyList/EmptyList"
import TrainingProgrammes from "../../../TrainingProgrammes/TrainingProgrammes"
import Districts from '../../../../pages/Districts/Districts';

const ViewTrainingCenter = (props) => {
    const history = useHistory()
    const [data, setData] = useState([])
    const [programmes, setProgrammes] = useState([])
    let dataTemplate;
    let programmeList;

    useEffect(() => {
        let id = props.match.params.id
        //console.log(props)
        if(!props.location.data) {
            if(props.location.pathname.search("teacher") != -1) {
                props.history.replace("/teacher/centre")
            } else {
                props.history.replace("/institution/centre")
            }

            return
        }

        if(data.length == 0) {
            setData([props.location.data.data])
        }
        
        // if(programmes.length == 0) {
        //     setProgrammes([{
        //         title: "Programme Name",
        //         duration: "name",
        //         fee: "address",
        //         descriptionn: "description"
        //     }])
        // }
        
    }, [])

    if(data && data.length) {
        dataTemplate = data.map((el) => {
            return (
                <Info data={{
                    Name: el.name,
                    Website: el.website,
                    State: el.State,
                    District: el.city,
                    Logo: " ",
                    About: el.About
                }}/>)
        })
    }
    
    
    if(programmes && programmes.length) {
        programmeList = programmes.map((el) => {
            return (
                <TrainingProgrammes
                title={el.title}
                duration={el.duration}
                fee={el.fee}
                centres={el.centres ? el.centres : []}
                description={el.description}
                enroll={""}
                />
            )
        })
    }

    const onBack = () => {
        history.goBack()
    }
    
    return (
        <>
        <PageTitle title="Training Centre" button="Back" add_new={onBack}/>
        <div className={styles["basic-info-container"]}>
            {data.length ? dataTemplate : "Loading"}
        </div>
        <div className={styles["qualification"]}>
        <p>
            Training Programmes
        </p>
        {programmeList && programmeList[0] !== undefined ?  programmeList : 
        <EmptyList message="No programmes added to show!"/>}
        </div>
        </>
    )

}
export default withRouter(ViewTrainingCenter);
