import React, { useEffect, useState, useRef } from 'react';
import styles from "./AddExperiences.module.css"
import Button from '@material-ui/core/Button';
import axios from "axios";
import * as constants from "../../constants/constants"
import PageTitle from "../../../PageTitle";
import { useHistory } from "react-router-dom";
import Alert from "../../../Toast/Toast"
import Create from '@material-ui/icons/Create';


const AddExperiences = (props) => {
    let date = new Date()
    let ref = useRef()
    let month = date.getMonth() 
    let institutionTemplate;
    month = String(month).length === 1 ? `0${month + 1}` : month + 1
    const [designationList, setDesignationList] = useState([])
    let parsedDate = `${date.getFullYear()}-${month}-${date.getDate()}`
    let isEdit = props.match.url.search("edit") != -1
    let isState = props.history.location.state
    const [institutionList, setInstitutionList] = useState([])
    const [subjectList, setSubjectList] = useState([])

    const history = useHistory();

    if(isEdit && !props.history.location.state) {
        history.replace("/teacher/experiences")
    }


    const back = ()=>{
        history.goBack()
    }

    if (institutionList.length > 0) {
        institutionTemplate = institutionList.map((el) => {
        return <option value={`${el._id}:${el.Name}`}>{String(el.Name).toUpperCase()}</option>
        })
    }

    useEffect(() => {

        axios.get("https://campusfield.in/api/institution/list_of_institution/1")
        .then(res => {
            if(res.data.error_code === 1) {
                setInstitutionList(res.data.output)
                if (isEdit && isState && res.data.output.findIndex(el => el.toLowerCase() === props.history.location.state.institution.toLowerCase() === -1)) {
                    setIsDropDown(false)
                }
            }
        }) 
        .catch(err => {
            // //console.log(err)
        })

        if(designationList.length === 0) {
            axios.get(constants.url + "site_management/designation")
            .then((response) => {
                if(response.data.error_code) {
                    setDesignationList([...response.data.output])
                }
            })
            .catch(err => {
                // //console.log(err)
           })
        }

        if(subjectList.length === 0) {
            axios.get(constants.url + "site_management/subject")
            .then((response) => {
                if(response.data.error_code) {
                    setSubjectList([...response.data.output])
                }
            })
            .catch(err => {
                // //console.log(err)
           })
        }
    }, [])

    const [message, setMessage] = useState("")
    const [isDropDown, setIsDropDown] = useState(true)
    const [institution, setInstitution] = useState({
        val: isEdit && isState ? props.history.location.state.institution : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })
    const [designation, setDesignaiton] = useState({
        val: isEdit && isState ? props.history.location.state.designation : "" ,
        isValid: isEdit && isState ? true : false,
        touched: false
    })
    const [subject, setSubject] = useState({
        val: isEdit && isState ? props.history.location.state.subject : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })
    const [institutionId, setInstitutionId] = useState({
        val: isEdit && isState ? props.history.location.state.subject : "",
    })
    const [from, setFrom] = useState({
        val: isEdit && isState ? props.history.location.state.from : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })
    const [to, setTo] = useState({
        val: isEdit && isState ? props.history.location.state.to : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })
    const [description, setDescription] = useState({
        val: isEdit && isState ? props.history.location.state.description : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })
    const [jobType, selectJobType] = useState({
        val: isEdit && isState ? props.history.location.state.jobType : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })
    const [isCurrentlyWorking, setIsCurrentlyWorking] = useState({
        val: isEdit && isState ? props.history.location.state.isCurrentlyWorking : "NO",
        isValid: true,
        touched: false
    })
    const [experienceInYears, setExperienceInYears] = useState({
        val: 0,
        isValid: true,
        touched: false
    })
    const [isSuccess, setIsSuccess] = useState(false)

    const validate = (i, val) => {
        return val ? true : false
    }

    let subjects;
    if(subjectList.length) {
        let updatedList = [{name: " "}, ...subjectList]
        subjects = updatedList.map((el, i) => {
            //console.log(el)
    
            return <option value={`${el.name}`}>{el.name.trim() ? el.name.toUpperCase() : "SELECT SUBJECT"}</option>
        })
    }

    const validateForm = () => {
        let partialValidation = institution.isValid && description.isValid && designation.isValid && from.isValid 
                                && subject.isValid && jobType.isValid && isCurrentlyWorking.isValid

        if(!isCurrentlyWorking) {
            partialValidation = partialValidation && to.isValid
        }

        return partialValidation
    }

    const setTouchedTrue = () => {
        setInstitution({...institution, touched: true})
        setFrom({...from, touched: true})
        setTo({...to, touched: true})
        setDesignaiton({...designation, touched: true})
        setDescription({...description, touched: true})
        setSubject({...subject, touched: true})
        selectJobType({...jobType, touched: true})
        setIsCurrentlyWorking({...isCurrentlyWorking, touched: true})
    }
    
    
    const onAddExperience = () => {
        let isValid = validateForm()
        setTouchedTrue()
        if(isValid) {
            let id = localStorage.getItem("id")
            axios.post(constants.url + "teachers/update/" + id + "/Experience", {"Experience": {
                "institution": institution.val,
                "designation": designation.val,
                "subject": subject.val,
                "from": from.val,
                "to": to.val,
                "description": description.val,
                "jobType": jobType.val,
                "isCurrentlyWorking": isCurrentlyWorking.val,
                "institution_id": isCurrentlyWorking.val == "YES" ? institutionId.val : ""
            }})
            .then((res) => {
                history.push({
                    pathname: "/teacher/experiences",
                    state: {
                    updateMessage: "added successfully"
                    }
                })
                // alert("experience added successfully")
                // props.history.push("/sign-in")
            }).catch((err) => {
                setMessage("Network error!")
                setIsSuccess(false)
                setTimeout(() => {
                    setMessage("")
                  }, 3000)
                //console.log(err)
            })
        }
    }

    const onUpdateExperience = () => {
        let isValid = validateForm()
        setTouchedTrue()

        if(isValid) {
            let id = localStorage.getItem("id")
            axios.post(constants.url + "teachers/edit/" + id + "/Experience/" + props.history.location.state.id + "/1", 
            {"Experience": {
                "institution": institution.val,
                "designation": designation.val,
                "subject": subject.val,
                "from": from.val,
                "to": to.val,
                "description": description.val,
                "jobType": jobType.val,
                "isCurrentlyWorking": isCurrentlyWorking.val
            }})
            .then((res) => {
                //console.log(res)
                history.push({
                    pathname: "/teacher/experiences",
                    state: {
                    updateMessage: "updated successfully"
                    }
                })
            }).catch((err) => {
                setMessage("Network error!")
                setIsSuccess(false)
                setTimeout(() => {
                    setMessage("")
                  }, 3000)
                //console.log(err)
            })
        }
    }

    // const getExperience = (from , to) => {
        
    //     const diffTime = Math.abs(to - from);
    //     const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    //     return
    // }

    const clearFields = () => {
        setInstitution({...institution, val: "", touched: false})
        setFrom({...from, val: "", touched: false})
        setTo({...to, val: "", touched: false})
        setDescription({...description, val: "", touched: false})
        setDesignaiton({...designation, val: "", touched: false})
        setSubject({...subject, val: "", touched: false})
        selectJobType({...jobType, val: "", touched: false})
        setIsCurrentlyWorking({...isCurrentlyWorking, val: "", touched: false})
    }

    const onChangeTextHandler = (event, i) => {
        let value = event.target.value
        let isValid = false
        
        if(i === 1) {
            if(value === "Other") {
                setIsDropDown(false)
                setInstitutionId({
                    ...institutionId,
                    val: ""
                })

                setInstitution({
                    ...institution,
                    val: "",
                    isValid: false
                    
                })
                // ref.current.focus()
            } else {
                let splitValue = value.split(':')
                setInstitutionId({
                    ...institutionId,
                    val: splitValue[0]
                })
                
                setInstitution({
                    ...institution,
                    val: value,
                    isValid: validate(i, value)
                    
                })
            }

        }

        if(i === 2) {
            setDesignaiton({
                ...designation,
                val: value,
                isValid: validate(i, value)
                
            })
        }

        if(i === 3) {
            setSubject({
                ...subject,
                val: value,
                isValid: validate(i, value)
                
            })
        }

        if(i === 4) {
            setFrom({
                ...from,
                val: value,
                isValid: validate(i, value)
                
            })
        }

        if(i === 5) {
            setTo({
                ...to,
                val: value,
                isValid: validate(i, value)
                
            })
        }

        if(i === 6) {
            setDescription({
                ...description,
                val: value,
                isValid: validate(i, value)
                
            })
        }

        if(i === 7) {
            selectJobType({
                ...jobType,
                val: value,
                isValid: validate(i, value)
                
            })
        }

        if(i === 8) {
            setIsCurrentlyWorking({
                ...isCurrentlyWorking,
                val: value,
                isValid: validate(i, value)
                
            })
        }
    }

    const setOtherInstitution = (event, i) => {
        let value = event.target.value
        //console.log(value)
        if(value.search(":") != -1) {
            return
        }
        
        setInstitution({
            ...institution,
            val: value,
            isValid: validate(i, value)
        })
    }

    return (
    <>
        <PageTitle title={isEdit ? "Experiences / Edit" : "Experiences / New"} button="Back" add_new={back}  />
        <div className={styles["sub-container"]}>
            <form className={styles["experience-form"]}>
                <div className={styles["input-container"]}>
                    <label>
                        INSTITUTION / COMPANY
                    </label>
                    {
                        !isDropDown ?
                        <input
                        ref={ref}
                        className={[!institution.isValid && institution.touched ? styles["invalid"] : null].join(" ")}
                        onBlur={() => {setInstitution({...institution, touched: true})}}
                        onChange={(event) => {setOtherInstitution(event, 1)}}
                        value={institution.val}
                        placeholder="Institution/Company"/> :
                    
                    <select 
                    id="degree" 
                    name="institution"
                    style={{border: institution.touched && !institution.isValid ? "1px solid red" : null}}
                    onChange={(event) => {onChangeTextHandler(event, 1)}}
                    value={institution.val}>
                        <option value="">SELECT INSTITUTION</option>
                            {institutionTemplate}
                        <option value="Other">Other</option>
                    </select>}
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Job Type*
                    </label>
                    <select 
                    id="jobType" 
                    name="jobType"
                    style={{border: jobType.touched && !jobType.isValid ? "1px solid red" : null}}
                    onChange={(event) => {onChangeTextHandler(event, 7)}}
                    value={jobType.val}>
                        <option value="">SELECT JOB TYPE</option>
                        <option value="full time">FULL TIME</option>
                        <option value="part time">PART TIME</option>
                    </select>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Currently Working Here*
                    </label>
                    <select 
                    id="currentWorking" 
                    name="currentWorking"
                    style={{border: isCurrentlyWorking.touched && !isCurrentlyWorking.isValid ? "1px solid red" : null}}
                    onChange={(event) => {onChangeTextHandler(event, 8)}}
                    value={isCurrentlyWorking.val}>
                        <option value="NO">NO</option>
                        <option value="YES">YES</option>
                    </select>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        DESIGNATION *
                    </label>
                    <select 
                    id="designation" 
                    name="desig"
                    style={{border: designation.touched && !designation.isValid ? "1px solid red" : null}}
                    onChange={(event) => {onChangeTextHandler(event, 2)}}
                    value={designation.val}>
                        <option value="">SELECT DESIGNATION</option>
                        {
                            designationList.map((el, i) => {

                            return <option 
                                    key={i}
                                    value={el.name}>{String(el.name).toUpperCase()}</option>
                            })
                        }
                    </select>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        SUBJECT *
                    </label>
                    <select 
                    id="degree" 
                    name="institution"
                    style={{border: subject.touched && !subject.isValid ? "1px solid red" : null}}
                    onBlur={() => {setSubject({...subject, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 3)}}
                    value={subject.val}>
                        {subjects}
                    </select>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        JOINING DATE *
                    </label>
                    <input type="date"
                    max={new Date().toISOString().split("T")[0]}
                    className={[from.touched && !from.isValid ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setFrom({...from, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 4)}}
                    value={from.val}/>
                </div>
                {
                    isCurrentlyWorking.val == "NO" ?
                        <div className={styles["input-container"]}>
                            <label>
                                RELIEVING DATE *
                            </label>
                            <input type="date"
                            min={from.val}
                            max={new Date().toISOString().split("T")[0]}
                            className={[to.touched && !to.isValid ? styles["invalid"] : null].join(" ")}
                            onBlur={() => {setTo({...to, touched: true})}}
                            onChange={(event) => {onChangeTextHandler(event, 5)}}
                            value={to.val}/>
                        </div> : null
                }
                <div className={styles["description"]}>
                    <label>
                        DESCRIPTION *
                    </label>
                    <textarea
                    className={[description.touched && !description.isValid ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setDescription({...description, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 6)}}
                    value={description.val}/>
                </div>
            </form>
            <div className={styles["button-container"]}>
                <Button
                    style={{marginRight: "0.9rem", fontSize: "0.8rem"}}
                    variant="contained"
                    color="primary"
                    onClick={isEdit ? onUpdateExperience : onAddExperience}
                >
                    {isEdit ? "Update" : "Create"}
                </Button>
                <Button
                variant="contained"
                style={{fontSize: "0.8rem"}}
                color="secondary"
                onClick={() => {
                    if(isEdit) {
                        history.goBack()
                    } else {
                        clearFields()}}}
                >
                    Cancel
                </Button>
            </div>
        </div>
        {
            message && isSuccess ? <Alert message={message} color="green"/> : null
        }
        {
            message && !isSuccess ? <Alert message={message} color="red"/> : null
        }
    </>
  );
}

export default AddExperiences;
