import React, { useEffect, useState } from 'react';
import styles from "./AcademicQualifications.module.css"
import AcademicQualification from './AcademicQualification/AcademicQualification';
import CommonHeader from "../CommonHeader/CommomHeader";
import PageTitle from "../../PageTitle";
import { useHistory, withRouter } from "react-router-dom";
import axios from "axios"
import * as constants from "../constants/constants"
import Alert from "../../Toast/Toast"
import EmptyList from '../../UI/EmptyList/EmptyList';
import { FormatAlignJustifyOutlined } from '@material-ui/icons';

const AcademicQualifications = (props) => {
  const history = useHistory();
  const [data, setData] = useState([])
  const [message, setMessage] = useState("")

  let updatemessage = props.location.state ? props.location.state.updateMessage : ""
  let qualifications;

  const onDeleteQualificaton = (qualId) => {
    let id = localStorage.getItem("id")

    axios.post(constants.url + "teachers/edit/" + id + "/Qualification/" + qualId + "/0", {"Qualification": {
        "id": qualId,
    }})
    .then((res) => {
        if(res.data.msg.search("delete") != -1) {
          getQualification()
          setMessage("deleted successfully")
        }
    }).catch((err) => {
        setMessage("network error!")
        setTimeout(() => {
            setMessage("")
          }, 3000)
        //cons.log(err)
    })
}
  

  if(data.Qualification && data.Qualification.length) {
    //cons.log(data.Qualification)
    qualifications = data.Qualification.map((el) => {
      return (
        <AcademicQualification
        onDelete={onDeleteQualificaton}
        id={el.id}
        resume={el.data_path}
        subject={el.mainSubject}
        college={el.college}
        isQualification={true}
        percentage={el.percentage}
        qualification={el.qualification}
        passOut={el.passout}
        serialNumber={el.certificate_id}
        university={el.university}
        />
      )
    })   
  }

  let id = localStorage.getItem("id")
  useEffect(() => {
    if(data.length === 0) {
      getQualification()
    }
  })
  
  
  const getQualification = () => {
    axios.get(constants.url + "teachers/dashboard/" + id)
    .then((res) => {
          //cons.log(res.data)
          if(res.data.error_code === 1) {
            if(typeof res.data.output[0].Qualification[0] === "string") {
              // res.data.output[0].Qualification.pop()
              // res.data.output[0].Qualification = JSON.parse(res.data.output[0].Qualification[0])
            }

            
            if(typeof res.data.output[0].Certification[0] === "string") {
              // res.data.output[0].Certification = JSON.parse(res.data.output[0].Certification[0])
            }

            
            if(typeof res.data.output[0].Experience[0] === "string") {
              // res.data.output[0].Experience = JSON.parse(res.data.output[0].Experience[0])
            }
            setData(res.data.output[0]) 
          }
      })
      .catch((err) => {
          //cons.log(err)
      })
    }
  const new_qualification = () =>{
    props.history.push({pathname: `${props.match.url}/new`, state: {isQualification: data.Qualification.length}})
  }

  return (
    <>
       <PageTitle title="Academic Qualifications" button="Add" add_new={new_qualification} />
      <div className={styles["sub-container"]}>
      {qualifications && qualifications[0] !== undefined ?  qualifications : 
      <EmptyList
      message="No qualifications added to show!"
      />}
      </div>
      {message ? <Alert message={message} color={message.search("network") != -1 ? "red" : "green"}/> : null}
        {updatemessage  ? <Alert message={updatemessage} color="green"/>: null}
    </>
  );
}

export default withRouter(AcademicQualifications);
