import React, { useEffect, useState } from 'react';
import styles from "../TrainingProgrammes/TrainingProgrammes.module.css"
import orderStyles from "./OrderTemplate.module.css"
import { NavLink, useHistory, withRouter } from "react-router-dom" 
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import DeleteIcon from '@material-ui/icons/Delete';
import Create from '@material-ui/icons/Create';
import axios from "axios"
import * as constants from "../Teacher/constants/constants"
import Alert from "../Toast/Toast"
import { Document, Page, Text, View,StyleSheet, PDFDownloadLink, Image } from '@react-pdf/renderer';


const OrderTemplate = (props) => {
    const [message, setMessage] = useState("")
    const [cart, setCart] = useState([])
    const [isExpand, setIsExpand] = useState(false)
    const history = useHistory()
    const useStyles = makeStyles((theme) => ({
        button: {
          margin: theme.spacing(1),
        },
      }));
    const classes = useStyles();
    const receiptHeader = ['Item', 'Quantity', 'Unit price', 'Amount']

    const pdfStyle = StyleSheet.create({
        page: {
          flexDirection: 'row',
          backgroundColor: 'white'
        },
        section: {
            display: "flex", 
            width: "100%", 
            flexDirection: "row", 
            padding: 10,
            borderBottom: "1px solid lightgray",
        }
      });
    const receiptTitleBlockStyle = StyleSheet.create({
        block:{
            height: "100%",
            flex: 0.3,
            paddingBottom: "10%"
        } 
    })

    const totalBlockStyle = {
        fontSize: "10px", 
        minHeight: "25px",
        flex: 0.2,
        borderBottom: "1px solid gray"
    }

    const addressStyle = {
        fontSize: "10px",
        color: "gray",
        textAlign: "right",
        minHeight: "10px",
        height: "auto",
        marginBottom: "5px"
    }

    const MyDocument = () => (
        <Document>
          <Page size="A4" style={pdfStyle.page}>
            <View style={{display: "flex", flexDirection: "column", width: "100%", height: "100%"}}>
                <View style={{...pdfStyle.section, justifyContent: "space-between", 
                marginBottom: 0, paddingBottom: 0, flex: 1.1, borderBottom: null}}>
                    <View style={receiptTitleBlockStyle.block}>
                        <Image
                        src={require('../../assets/cf-logo.png')}
                        style={{height: "100%", width: "90%"}}
                        />
                    </View>
                    <View 
                    style={{...receiptTitleBlockStyle.block}}>
                        <Text 
                        style={{...addressStyle, fontSize: "15px",  marginBottom: "15px", color: "black"}}>
                            CampusField Education
                        </Text>
                        <Text style={addressStyle}>
                            Plot No : 10,
                        </Text>
                        <Text style={addressStyle}>
                            Campus of VSSF, Thengode P.O,
                        </Text>
                        <Text style={addressStyle}>
                            Kakkanad – Kochin -30
                        </Text>
                        <Text style={addressStyle}>
                            tel: +91 7306500143
                        </Text>
                        <Text style={addressStyle}>
                            email: campusfield@gmail.com
                        </Text>
                    </View>
                </View>
                <View style={{fontSize: "10px", borderBottom: "1px solid black", paddingBottom: 10}}>
                        <Text style={{marginLeft: "10px"}}>Invoice ID: {props.orderId}</Text>
                </View>
                <View style={{flex: 7, display: "flex", flexDirection: "column"}}>   
                    <View style={pdfStyle.section}>
                        {
                            generateReceiptHeader(true)
                        }
                    </View>
                    <View>    
                        {
                            generateReceiptContent()
                        }
                    </View>
                        {
                            generateReceiptTotal()
                        }
                </View>
            </View>
          </Page>
        </Document>
      );
    
    const generateReceiptTotal = () => {
        return (
        <View style={{fontSize: "10px"}}>
            <View style={{...pdfStyle.section, minHeight: "25px", paddingBottom: 0, borderBottom: 0}}>
                <Text style={{flex: 0.8}}>
                </Text>
                <Text style={totalBlockStyle}>
                    GST: 18%
                </Text>
                <Text style={totalBlockStyle}>
                        {(props.amount * 0.18).toFixed(2)}
                </Text>
            </View>
            <View style={{...pdfStyle.section, minHeight: "25px", paddingBottom: 0, borderBottom: 0}}>
                <Text style={{flex: 0.8}}>
                </Text>
                <Text style={totalBlockStyle}>
                        Amount
                </Text>
                <Text style={totalBlockStyle}>
                        INR {(props.amount).toFixed(2)}
                </Text>
            </View>
        </View>
        )
      }

    const generateReceiptContent = () => {
        return ( props.programmes.map((el, i) => (
            <View style={{...pdfStyle.section, minHeight: "25px"}}>
                <Text style={{flex: 0.6, textAlign: "left", wordBreak: "break-word", fontSize: "10px"}}>
                    {i + 1}. {el.title}
                </Text>
                <Text style={{flex: 0.2, fontSize: "10px"}}>
                        {el.teachers?.length || 1}
                </Text>
                <Text style={{flex: 0.2, fontSize: "10px"}}>
                        {(el.amount * 0.82).toFixed(2)}
                </Text>
                <Text style={{flex: 0.2, fontSize: "10px"}}>
                        {((el.amount * 0.82) * (el.teachers?.length || 1)).toFixed(2)}
                </Text>
            </View>
        )))
      }
    
    const generateReceiptHeader = (isPdf) => {
        if (isPdf) {
            return (
                (
                    <>
                        {
                            receiptHeader.map(el => {
                                return  (
                                    <Text style={{flex: el == 'Item'? 0.6: 0.2, fontSize: "10px"}}>{el}</Text>
                                )
                            })
                        }
                    </>
                    )
            )
        }

        return (
        <>
            {
                receiptHeader.map(el => {
                    return  (
                        <div style={{flex: el == 'Item'? 0.6: 0.2}} 
                        className={orderStyles['receiptHeader']}>{el}</div>
                    )
                })
            }
        </>
        )
    }

    return (
      <div className={styles["experiences-container"]}>
          <div className={styles["experience-container"]}>
              <div className={styles["title-container"]}>
                <h4>
                  Order {props.title}
                </h4>
                <p onClick={() => {setIsExpand(!isExpand)}}>
                    {isExpand ? '-' : '+'}
                </p>
              </div>
            <div
            style={{display: isExpand ? "block": "none"}} 
            className={styles["detail"]}>
            <div style={{display: "flex", width: "100%"}}>
                {generateReceiptHeader()}
            </div>
              {
                  props.programmes.map((el, i) => {
                    return (
                        <div className={styles["detail-container"]}>
                            <div style={{flex: 0.6, textAlign: "left"}} 
                                 className={orderStyles['receiptHeader']}>
                                <h3 style={{wordBreak: "break-word"}}>
                                    {i + 1}. {el.title}
                                </h3>
                            </div>
                            <div style={{flex: 0.2}} className={orderStyles['receiptHeader']}>
                                <h3>
                                    {el.teachers?.length || 1}
                                </h3>
                            </div>
                            <div style={{flex: 0.2}} className={orderStyles['receiptHeader']}>
                                <h3>
                                    {(el.amount * 0.82).toFixed(2)}
                                </h3>
                            </div>
                            <div style={{flex: 0.2}} className={orderStyles['receiptHeader']}>
                                <h3>
                                    {((el.amount * 0.82) * (el.teachers?.length || 1)).toFixed(2)}
                                </h3>
                            </div>
                        </div>
                    )
                  }) 
              }
              
            <div
            style={{marginTop: "16px", borderTop: "1px solid lightgray"}} 
            className={styles['detail-container']}>
                <div 
                style={{flex: 0.6, textAlign: "right", paddingTop: "5px"}} 
                className={orderStyles['receiptHeader']}>
                </div>
                <div style={{flex: 0.2}}>
                </div>
                <div
                className={orderStyles['receiptHeader']}
                style={{flex: 0.2, paddingTop: "5px", fontWeight: 500, color: "gray"}}>
                    GST. 18%
                </div>
                <div 
                style={{flex: 0.2, paddingTop: "5px"}} 
                className={orderStyles['receiptHeader']}>
                    {(props.amount * 0.18).toFixed(2)}
                </div>
            </div>
            <div className={styles['detail-container']}>
                <div 
                style={{flex: 0.6, textAlign: "left", paddingTop: "5px"}} 
                className={orderStyles['receiptHeader']}>
                    <PDFDownloadLink
                    document={MyDocument()} fileName="campus_field_receipt.pdf">
                        {({ blob, url, loading, error }) => (loading ? 'Loading Receipt...' : 'Download Receipt')}
                    </PDFDownloadLink>
                </div>
                <div style={{flex: 0.2}}>
                </div>
                <div
                className={orderStyles['receiptHeader']}
                style={{flex: 0.2, paddingTop: "5px", fontWeight: 500, color: "gray",  borderTop: "1px solid lightgray"}}>
                    Amount
                </div>
                <div 
                style={{flex: 0.2, borderTop: "1px solid lightgray", paddingTop: "5px", paddingTop: "5px"}} 
                className={orderStyles['receiptHeader']}>
                    INR {props.amount.toFixed(2)}
                </div>
            </div>
              {
                props.teachers !== undefined ?
                <div className={styles["detail-container"]}>
                  <i class="fa fa-users" aria-hidden={true}/>
                  <b>Number of Teachers</b> : {props.programmes?.teachers.length}
                </div> : null
              }              
              </div>
            </div>
            {message ? <Alert message={message} color={message.search("network") != -1 ? "red" : "green"}/> : null}        
          </div>
    );
}   

export default withRouter(OrderTemplate);
