import React, { useEffect, useState } from 'react';
import styles from "./EditProfile.module.css"
import CommonHeader from "../../CommonHeader/CommomHeader";
import { Grid } from "@material-ui/core";
// import PageTitle from "../PageTitle";
import PageTitle from "../../../PageTitle";
import { useHistory } from "react-router-dom";
import axios from "axios"
import * as constants from "../../constants/constants"
import Button from '@material-ui/core/Button';
import ForgotPassword from "../../../Home/ForgotPassword/ForgotPassword"
import Alert from "../../../Toast/Toast"


const EditProfile = (props) => {
    const history = useHistory()
    const [message, setMessage] = useState("")
    const [isSuccess, setIsSuccess] = useState(false)
    const [data, setData] = useState("")
    const [stateList, setStateList] = useState("")
    const [districtList, setDistrictList] = useState("")
    const [isValuesSet, setIsValueSet] = useState(false)
    const [isPhoneNumberChanged, setIsPhoneNumberChanged] = useState(false)
    const [isFormValid, setIsFormValid] = useState({
        touched : false,
        isValid: false
    })
    const [email, setEmail] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [password, setPassword] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [confirmPassword, setConfirmPassword] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [religion, setReligion] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [caste, setCaste] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [maritalStatus, setMaritalStatus] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [spouseName, setSpouseName] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [firstName, setFirstName] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [lastName, setLastName] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [birthDate, setBirthDate] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [gender, setGender] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [phoneNumber, setPhoneNumber] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [street, setStreet] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [city, setCity] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [address, setAddress] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [state, setState] = useState({
        val: "",
        isValid: true,
        touched: false
    })
    const [district, setDistrict] = useState({
        val: "",
        isValid: true,
        touched: false
    })
    const [pin, setPin] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [about, setAbout] = useState({
        val: "",
        isValid: false,
        touched: false
    })
    const [profilePic, setProfilePic] = useState({
        val: "",
        isValid: true,
        touched: false
    })
    
    useEffect(() => {
        if(stateList.length === 0) {
            axios.get(constants.url + "site_management/state")
                 .then((response) => {
                     setStateList([...response.data.output])
                 })
                 .catch(err => {
                    })
        }

        if(districtList.length === 0) {
            axios.get(constants.url + "site_management/district")
            .then((response) => {
                setDistrictList([...response.data.output])
            })
            .catch(err => {
            })
        }

        if(data === "") {
            let id = localStorage.getItem("id")
            axios.get(constants.url + "teachers/dashboard/" + id)
            .then((res) => {
                if(res.data.error_code === 1) {
                    setData(res.data.output[0]) 
                }
            })
            .catch((err) => {
            })
        }
        
    }, [])
    
    const setValues = (data) => {
        setFirstName({...firstName, val: data.First_name, isValid: true})
        setLastName({...lastName, val: data.Last_name, isValid: true})
        setGender({...gender, val: data.Gender, isValid: true})
        setAbout({...about, val: data.About, isValid: true})
        setCity({...city, val: data.city, isValid: true})
        setAddress({...address, val:data.address, isValid: true})
        setState({...state, val: data.State, isValid: true})
        setStreet({...street, val: data.Street, isValid: true})
        setPhoneNumber({...phoneNumber, val: data.Phone_Number, isValid: true})
        setPin({...pin, val: data.Pin, isValid: String(data.Pin).trim().length > 0})
        setDistrict({...district, val: data.District, isValid: true})
        setBirthDate({...birthDate, val: data.DOB, isValid: true})
        setProfilePic({...profilePic, val: data.Profile_pic, isValid: true})
        setCaste({...caste, val: data.caste ? data.caste : "", isValid: true})
        setReligion({...religion, val: data.religion ? data.religion : "", isValid: true})
        setMaritalStatus({...maritalStatus, val: data.maritalStatus ? data.maritalStatus : "", isValid: true})
        setSpouseName({...spouseName, val: data.spouseName ? data.spouseName : "", isValid: true})
        setIsValueSet(true)
    }
    
    if(data && !isValuesSet) {
        setValues(data)
    }
    
    let states;
    if(stateList.length) {
        let updatedList = [{name: " "}, ...stateList]
        states = updatedList.map((el, i) => {
            return <option value={`${el.name}`}>{el.name.trim() ? el.name.toUpperCase() : "SELECT STATE"}</option>
        })
    }

    let districts;
    if(districtList.length) {
        let updatedList = [{name: " "}, ...districtList]
        districts = updatedList.map((el, i) => {
            return <option value={`${el.name}`}>{el.name.trim() ? el.name.toUpperCase() : "SELECT DISTRICT"}</option>
        })
    }

    
    const validateForm = () => {
        return firstName.isValid && lastName.isValid && street.isValid && city.isValid &&
        gender.isValid && phoneNumber.isValid && about.isValid && pin.isValid && district.isValid
    }

    const onUpdate = (event) => {
        event.preventDefault()
        let isValid = validateForm()
        let id = localStorage.getItem("id")
        setFirstName({...firstName, touched: true})
        setLastName({...lastName, touched: true})
        setGender({...gender, touched: true})
        setBirthDate({...birthDate, touched: true})
        setAbout({...about, touched: true})
        setAddress({...address, touched: true})
        setCity({...city, touched: true})
        setState({...state, touched: true})
        setStreet({...street, touched: true})
        setPhoneNumber({...phoneNumber, touched: true})
        setPin({...pin, touched: true})
        setDistrict({...district, touched: true})
        setIsFormValid({
            isValid: isValid,
            touched: true 
        })

        if(String(data.Phone_Number) === String(phoneNumber.val)) {
            update()
        } else {
            onSendCode(phoneNumber.val)
            // //console.log("hell")
        }
        // } else {
        //     update(isValid, id)
        // }

       
    }

    const onSendCode = (phoneNumber) => {
        let email = localStorage.getItem('email')
        let id = localStorage.getItem('id')
        let isValid = validateForm()
        axios.get(`https://campusfield.in/api/verify/mobile/${phoneNumber}/${email}`)
        .then(res => {
            if(res.data.error_code === 1) {
                setIsPhoneNumberChanged(true)
                // update(isValid, id)
              
            } else {
              props.setAlert()
              props.onCancel()
            }
        })
        .catch(err => {
            props.setAlert()
            props.onCancel()
        })
      }


    const update  = () => {
        let id = localStorage.getItem('id')
        let isValid = validateForm()
        if(isValid) {
            let formData = new FormData()
            formData.append("id", id)
            // formData.append("profile", profilePic.val)
            formData.append("First_name", firstName.val)
            formData.append("religion", religion.val)
            formData.append("caste", caste.val)
            formData.append("maritalStatus", maritalStatus.val)
            formData.append("spouseName", spouseName.val)
            formData.append("Last_name", lastName.val)
            formData.append("Pass", data.Pass)
            formData.append("DOB", birthDate.val)
            formData.append("Phone_Number", phoneNumber.val)
            formData.append("Email", data.Email)
            formData.append("Gender", gender.val)
            formData.append("About", about.val)
            formData.append("type", "teacher")
            formData.append("city", city.val)
            formData.append("State", state.val)
            formData.append("Street", street.val)
            formData.append("District", district.val)
            formData.append("Pin", pin.val)
            formData.append("item_img", profilePic.val)
            formData.append("Resume", data.Resume)
            formData.append("Qualification", JSON.stringify([...data.Qualification]))
            formData.append("Certification", JSON.stringify([...data.Certification]))
            formData.append("Training_from_campus", [])
            formData.append("Experience", JSON.stringify([...data.Experience]))
            formData.append("Transactions", [])
            formData.append("applied_jobs", [])
            // {
            //     "id": id,
            //     "First_name": firstName.val,
            //     "Last_name": lastName.val,
            //     "Pass": data.Pass,
            //     "DOB": data.DOB,
            //     "Phone_Number": phoneNumber.val,
            //     "Email": data.Email,
            //     "Gender": gender.val,
            //     "About": about.val,
            //     "type":"teacher",
            //     "State": state.val,
            //     "Street": street.val,
            //     "District": district.val,
            //     "Pin": pin.val,
            //     "Profile_pic": profilePic.val,
            //     "Resume": data.Resume,
            //     "Qualification": data.Qualification,
            //     "Certification": data.Certification,
            //     "Training_from_campus":[],
            //     "Experience": data.Experience,
            //     "Transactions":[],
            //     "applied_jobs":[]
            // }
            axios.post(constants.url + "teachers/profile", formData)
            .then((res) => {
                history.push({
                    pathname: "/teacher/profile",
                    state: {
                    updateMessage: "updated successfully"
                    }
                })
        }).catch((err) => {
            setMessage("something went wrong!")
            setIsSuccess(false)
            setTimeout(() => {
                setMessage("")
              }, 3000)
        })
        } 
    }

    const validate = (i, val) => {
        if(i === 1) {
            return /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(val)
        }

        if(i === 2) {
            if(val) {
                return true
            }

            return false
        }

        if(i === 3) {
            return val === password.val
        }

        if(i === 4) {
            return val ? true : false
        }

        if(i === 5) {
            return val ? true : false
        }

        if(i === 6) {
            return val ? true : false
        }

        if(i === 7) {
            return val ? true : false
        }

        if(i === 8) {
            return /^\d{10}$/.test(val)
        }

        if(i === 9) {
            return val ? true : false
        }

        if(i === 10) {
            return val ? true : false
        }

        if(i === 11) {
            return val ? true : false
        }

        if(i === 12) {
            return val ? true : false
        }

        if(i === 13) {
            return val ? true : false
        }

        if(i === 14) {
            return val ? true : false
        }

        if(i === 15) {
            return val ? true : false
        }
    }
    
    const onChangeTextHandler = (event, i) => {
        let value = event.target.value
        let isValid = false

        if(i === 4) {
            
             setFirstName({
                 ...firstName,
                 val: value,
                 isValid:  validate(i, value)
             })
        }

        if(i === 5) {

             setLastName({
                 ...lastName,
                 val: value,
                 isValid:  validate(i, value)
             })
        }

        if(i === 6) {
 
             setBirthDate({
                 ...birthDate,
                 val: value,
                 isValid:  validate(i, value)
             })
        }

        if(i === 7) {
    
             setGender({
                 ...gender,
                 val: value,
                 isValid: validate(i, value)
             })
        }

        if(i === 8 && (/^[0-9]*$/.test(value) || value === "") && value.length <= 10) {
            
             setPhoneNumber({
                 ...phoneNumber,
                 val: value,
                 isValid: validate(i, value)
             })
        }

        if(i === 9) {

             setAddress({
                 ...address,
                 val: value,
                 isValid: validate(i, value)
             })
        }

        if(i === 10) {

             setStreet({
                 ...street,
                 val: value,
                 isValid: value ? true : false
             })
        }

        if(i === 11) {
 
             setCity({
                 ...city,
                 val: value,
                 isValid: validate(i, value)
             })
        }

        if(i === 12) {
            if(state.touched) {
                isValid =  validate(i, value)
             }
 
             setState({
                 ...state,
                 val: value,
                 isValid: validate(i, value)
             })
        }

        if(i === 13) {
            if(district.touched) {
                isValid =  validate(i, value)
             }
 
             setDistrict({
                 ...district,
                 val: value,
                 isValid: validate(i, value)
             })
        }

        if(i === 14) {
            if(pin.touched) {
                isValid =  validate(i, value)
             }
 
             setPin({
                 ...pin,
                 val: value,
                 isValid: validate(i, value)
             })
        }

        if(i === 15) {
 
             setAbout({
                 ...about,
                 val: value,
                 isValid: validate(i, value)
             })
        }

        if(i === 41) {
 
            setReligion({
                ...religion,
                val: value,
                isValid: true
            })
       }

       if(i === 42) {
 
        setCaste({
            ...caste,
            val: value,
            isValid: true
        })
   }

    if(i === 43) {
    
        setMaritalStatus({
            ...maritalStatus,
            val: value,
            isValid: true
        })

        if (value.toLowerCase() == "no") {
            setSpouseName({
                ...spouseName,
                val: "",
                isValid: true
            })
        }
    }

    if(i === 44) {
    
        setSpouseName({
            ...spouseName,
            val: value,
            isValid: true
        })
    }
    }

    const onFileChange = (event) => {
        let value = event.target.files[0]
        setProfilePic({
            ...profilePic,
            isValid: true,
            val: value
        })
    }

    const onCancel = () => {
        setIsPhoneNumberChanged(false)
    }

    const back = ()=>{
        history.push("/teacher/profile");
    }


    return (
    <>
    <PageTitle title="Dashboard/Edit Profile" button="Back" add_new={back}/>
    {
        isPhoneNumberChanged ? 
        <div style={{display: "flex", justifyContent: "center", alignItems: "center"}}>
        <ForgotPassword
        onCancel={onCancel}
        update={update}
        phoneNumber={phoneNumber.val}
        isCodeSent={true}
        />
        </div> :
    
        <>
      <div className={styles["sub-container"]}>
            <form className={styles["profile-form"]}>
                <div className={styles["input-container"]}>
                    <label>
                        First *
                    </label>
                    <input
                        className={[firstName.touched & !firstName.isValid ? styles["invalid"] : null].join(" ")}
                        onBlur={() => {setFirstName({...firstName, touched: true})}}
                        onChange={(event) => {onChangeTextHandler(event, 4)}}
                        value={firstName.val}
                        placeholder="First Name*"/>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Last *
                    </label>
                    <input
                        className={[!lastName.isValid && lastName.touched  ? styles["invalid"] : null].join(" ")}
                        onBlur={() => {setLastName({...lastName, touched: true})}}
                        onChange={(event) => {onChangeTextHandler(event, 5)}}
                        value={lastName.val}
                        placeholder="Last Name*"/>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Gender *
                    </label>
                    <select 
                    id="gender" 
                    name="select-gender" 
                    value={gender.val}
                    onChange={(event) => {onChangeTextHandler(event, 7)}}>
                        <option value="">Select Gender</option>
                        <option value="Male">Male</option>
                        <option value="Female">Female</option>
                    </select>
                </div>
                {/* <div className={styles["address"]}>
                    <label>
                        Address *
                    </label>
                    <input
                    className={[!address.isValid && address.touched  ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setAddress({...address, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 9)}}
                    value={address.val}
                    placeholder="Address*"/>
                </div> */}
                <div className={styles["input-container"]}>
                    <label>
                        Religion 
                    </label>
                    <input
                        onBlur={() => {setReligion({...religion, touched: true})}}
                        onChange={(event) => {onChangeTextHandler(event, 41)}}
                        value={religion.val}
                        placeholder="Religion"/>
                </div>
                {/* <div className={styles["input-container"]}>
                    <label>
                        Caste
                    </label>
                    <input
                        onBlur={() => {setCaste({...caste, touched: true})}}
                        onChange={(event) => {onChangeTextHandler(event, 42)}}
                        value={caste.val}
                        placeholder="Caste"/>
                </div> */}
                <div className={[styles["input-container"], styles["margin"]].join(" ")}>
                        <label>
                            BirthDate *
                        </label>
                        <input
                        className={[!birthDate.isValid && birthDate.touched  ? styles["invalid"] : null].join(" ")}
                        onBlur={() => {setBirthDate({...birthDate, touched: true})}}
                        onChange={(event) => {onChangeTextHandler(event, 6)}}
                        value={birthDate.val}
                        // min={`${new Date().getFullYear() - 18}-${new Date().getMonth() + 1 < 10 ? `0${new Date().getMonth() + 1}` : new Date().getMonth() + 1}-${new Date().getDate().length === 1 ? "0"+ new Date().getDate() : new Date().getDate() }`}
                        max={new Date().toISOString().split("T")[0]}
                        type="date"/>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Marital Status
                    </label>
                    <select 
                    id="maritalStatus"  
                    value={maritalStatus.val}
                    onBlur={() => {setMaritalStatus({...maritalStatus, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 43)}}>
                        <option value="">SELECT MARITAL STATUS</option>
                        <option value="single">SINGLE</option>
                        <option value="married">MARRIED</option>
                        <option value="divorced">DIVORCED</option>
                    </select>
                </div>
                {
                    maritalStatus.val.toLowerCase() === 'yes' ?
                    <div className={styles["input-container"]}>
                    <label>
                        Spouse Name *
                    </label>
                    <input
                        onBlur={() => {setSpouseName({...spouseName, touched: true})}}
                        onChange={(event) => {onChangeTextHandler(event, 44)}}
                        value={spouseName.val}
                        placeholder="Spouse Name*"/>
                </div> : null
                }
                <div className={styles["address"]}>
                    <label>
                        Street *
                    </label>
                    <input
                    className={[!street.isValid && street.touched  ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setStreet({...street, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 10)}}
                    value={street.val}
                    placeholder="Street*"/>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        City *
                    </label>
                    <input
                    className={[!city.isValid && city.touched  ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setCity({...city, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 11)}}
                    value={city.val}
                    placeholder="City*"/>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        State *
                    </label>
                    <select 
                    onChange={(event) => {onChangeTextHandler(event, 12)}}
                    className={[!state.isValid && state.touched  ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setState({...state, touched: true})}}
                    id="state" 
                    name="select-state" 
                    value={state.val}>
                        <option value="">SELECT STATE</option>
                        {states}
                    </select>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        District *
                    </label>
                    <select 
                    id="district" 
                    name="select-district" 
                    value={district.val}
                    onChange={(event) => {onChangeTextHandler(event, 13)}}>
                        <option value="">SELECT DISTRICT</option>
                        {districts}
                    </select>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Pin *
                    </label>
                    <input
                    maxLength={6}
                    className={[!pin.isValid && pin.touched  ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setPin({...pin, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 14)}}
                    value={pin.val}
                    placeholder="Pin*"/>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Phone Number *
                    </label>
                    <input
                    className={[!phoneNumber.isValid && phoneNumber.touched ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setPhoneNumber({...phoneNumber, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 8)}}
                    value={phoneNumber.val}
                    placeholder="Phone Number*"/>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Profile Photo
                    </label>
                    <input 
                    style={{border: "none", backgroundColor: "white", boxShadow: "0 0 0 0"}}
                    className={styles["file"]} 
                    accept="image/png, image/jpeg"
                    onChange={(event) => {onFileChange(event)}}
                    type="file"/>
                </div>
                <div className={styles["description"]}>
                    <label>
                        DESCRIPTION *
                    </label>
                    <textarea
                    className={[!about.isValid && about.touched  ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setAbout({...about, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 15)}}
                    value={about.val}/>
                </div>
            </form>
            {/* <div className={styles["button-container"]}>
                <button className={styles["create-button"]} onClick={onUpdate}>
                    Update
                </button>
                <button className={styles["cancel-button"]}>
                    Cancel
                </button>
            </div> */}
            {
                isFormValid.touched && !isFormValid.isValid ? <p style={{color: "red", padding: "1rem"}}>Please fill all mandatory fields</p> : null
            }
            <div className={styles["button-container"]}>
            <Button
                variant="contained"
                color="primary"
                style={{marginRight: "3rem"}}
                onClick={onUpdate}
            >
                Update
            </Button>
       
             <Button
                variant="contained"
                color="secondary"
                onClick={() => {history.goBack()}}
            >
                Cancel
            </Button>
            </div>
            {
        message ? <Alert message={message} color={isSuccess ? "green" : "red"}/> : null
      }
      </div>
    </>
    }
    </>
  );
}

export default EditProfile;
