import React, {useEffect, useState} from 'react';
import styles from "./Cart.module.css"
import CommonHeader from "../CommonHeader/CommomHeader"
import PageTitle from "../../PageTitle";
import Button from '@material-ui/core/Button';
import { useHistory } from "react-router-dom";
import { makeStyles } from '@material-ui/core/styles';
import axios from "axios"
import * as constants from "../constants/constants"
import TrainingProgrammes from "../../TrainingProgrammes/TrainingProgrammes"
import Alert from "../../Toast/Toast"

const Cart = (props) => {
    const history = useHistory();
    const [data, setData] = useState([])
    const [message, setMessage] = useState("")
    const [totalAmount, setTotalAmount] = useState(0)
    const useStyles = makeStyles((theme) => ({
        button: {
          margin: theme.spacing(1),
        },
      }));
    const classes = useStyles();

    
    const calculateTotalAmount = (programme) => {
        let total = 0
        for (let p of programme) {
            let amount = parseInt(p.amount)
            
            let teachers = p.teachers

            if (amount && !isNaN(amount)) {
                if(p.programmeFor === 'school' || typeof teachers === 'object') {
                    total += amount * teachers.length
                } else {
                    total += amount 
                }

            } 
        }
        setTotalAmount(total)
    }

    const updateData = () => {
        let id = localStorage.getItem('id')
        axios.get('https://campusfield.in/api/cart/' + id)
        .then(res => {
            if (res.data.error_code === 1) {
                let programme = res.data.output;

                if(window.location.href.search('institution/cart') !== -1) {
                    programme = programme.filter((el => el.teachers.length))
                }
                setData(programme)
                calculateTotalAmount(programme)
            }
        }) 
        .catch(err => {
            setMessage("Something went wrong")
        })
    }
    useEffect((props) => {
        updateData()
    }, []) 

    const onDelete = (id) => {
        axios.delete('https://campusfield.in/api/cart/' + id)
        .then((res) => {
            if(res.data.error_code === 1) {
                setMessage('Removed item from cart')
                updateData()
            }
        })
        .catch(err => {
            setMessage('Network Error')
        })

    }

    const deleteAll = (id) => {
        axios.delete('https://campusfield.in/api/cart/deleteAll/' + id)
            .then(res => {
                if(res.data.error_code == 1) {
                    updateData()
                }
            })
            .catch(err => {})
    }

    const addTrainingProgramsForTeacher = () => {
        axios.post("https://campusfield.in/api/training/add_enrolment/0", {
          members: [localStorage.getItem("teacher_id")],
          enrolment_programme: data.map(el => {
            return el.programme_id
          })
        })
        .then(res => {
            if(res.data.error_code == 1) {
                deleteAll(localStorage.getItem('teacher_id'))
                setMessage("enrolled successfully")
                setTimeout(() => setMessage(""), 3000)
            }
        })
        .catch(err => {
            setMessage("Network error!")
            setTimeout(() => setMessage(""), 3000)
        })
    }

    const notifyPaymentFailed = () => {
        setMessage("Couldn't process payment. Please try again!")
        setTimeout(() => setMessage(""), 3000)
    }

    const updateOrder = async(data, order_id, userType) => {
        await axios.put(`https://campusfield.in/api/order/${order_id}`, data)
            .then(res => {
                if (res.data.error_code == 1) {
                    if (userType == 'teacher') {
                        addTrainingProgramsForTeacher()
                    } else {
                        addTrainingProgramsForSchool()
                    }
                } else {
                    notifyPaymentFailed()
                }
            })
            .catch(err => {
                notifyPaymentFailed()
            })
    }

    const loadScript = (src) => {
        return new Promise((resolve) => {
            const script = document.createElement("script");
            script.src = src;
            script.onload = () => {
                resolve(true);
            };
            script.onerror = () => {
                resolve(false);
            };
            document.body.appendChild(script);
        });
    }

    const displayRazorpay = (options) => {
        const paymentObject = new window.Razorpay(options);
        paymentObject.open();
    }
    

    const initiatePayment = async(userType) => {
        const res = await loadScript(
            "https://checkout.razorpay.com/v1/checkout.js"
        );

        if (!res) {
            alert("Razorpay SDK failed to load. Are you online?");
            return;
        }

        await axios.post("https://campusfield.in/api/order", {
            amount : totalAmount,
            programmes: data.map(el => {
                return  {
                    prgrammeId: el.programmeId, amount: el.amount || 0,
                    title: el.title, description: el.description, 
                    ...(el.teachers && {teachers: el.teachers})
                    }
            }),
            userId: localStorage.getItem('id')
        }).then(res => {
            if (res.data.error_code == 1) {
                let order_detail = res.data.output.orderDetail
                let razorPayOptions = {
                    'name': 'CampusField',
                    'key': res.data.output.razorId,
                    'prefill': {
                        'name': localStorage.getItem('applicant_name'),
                        'email': localStorage.getItem('email'),
                        'contact': localStorage.getItem('phone_number')
                    },
                    'order_id': order_detail.order_attr.id,
                    'handler': async(response) => {
                        await updateOrder({'payment_id': response.razorpay_payment_id,
                        'order_id': response.razorpay_order_id,
                        'signature': response.razorpay_signature}, order_detail._id, userType)
                    }
                }
                if (totalAmount > 0) {
                    displayRazorpay(razorPayOptions)
                } else {
                    if(userType === 'teacher') {
                        addTrainingProgramsForTeacher()
                    } else {
                        addTrainingProgramsForSchool()
                    }
                }
            } 
        }).catch(err => {
            notifyPaymentFailed()
        })
    }

    const initiateAddTrainingProgramsForSchool = () => {
        for(let pro of data) {
            axios.post("https://campusfield.in/api/training/add_enrolment/1", {
                members: [...pro.teachers.map(el => el.id), ...pro.enrolled].filter(el => Boolean(el)),
                Institution_enrolment: [localStorage.getItem('i_id')],
                enrolment_programme: [pro.programme_id]
            })
            .then(res => {
                if(res.data.error_code == 1) {                    
                }
            })
            .catch(err => {
            })
        }
    }
    const addTrainingProgramsForSchool = async() => {
        await initiateAddTrainingProgramsForSchool()

        deleteAll(localStorage.getItem('i_id'))
        props.history.push({
            pathname: "/institution/programme",
            state: {
                "message": "enrolled successfully"
            }
        })
    }

    const onEnroll = () => {
        if (data.length > 0) {
            initiatePayment(localStorage.getItem('type'))
        }
    }

    return (
      <>
      <div style={{display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center"}}>
        <div style={{flex: 0.8}}>
        <PageTitle title="Order Summary" button="Enroll" disabled={!data.length > 0} add_new={onEnroll} />
        </div>
        <div style={{flex: 0.2}}>
            <Button
            style={{
                backgroundColor: '#FF5C93',
                height: '40px',
                color: 'white',
                margin: '1rem',
                marginBottom: 0,
                width: '130px',
                marginTop: "0.5rem"
            }}
            onClick={() => {
                let type = localStorage.getItem('type')
                if (type) {
                    history.push(`/${type}/order`)
                }
            }}>
                Order History
            </Button>
        </div>
      </div>
        <div style={{minHeight: "100px", height: "auto", minWidth: "200px",width: "auto", maxWidth: "300px", padding: "1rem", backgroundColor: "white", borderRadius: "5px", margin: "1rem", boxShadow: "0 2px 2px 2px lightgray"}}>
            <p style={{fontSize: "1rem", fontWeight: 500}}>Number of Items: {data.length}</p>
            <p style={{fontSize: "1rem", fontWeight: 500}}>Total Amount: {totalAmount}</p>
            <p style={{fontSize: "1rem", fontWeight: 500}}>{totalAmount > 0 ? "Incl. of 18% GST": ""}</p>
        </div>
          <div className={styles["sub-container"]}>
          {data.length == 0 ?  null :
              data.map((el) => {
                  return (
                      <TrainingProgrammes
                      _id={el._id}
                      amount={el.amount}
                      key={el.id}
                      id={el.programme_id}
                      title={el.title}
                      dueDate={el.dueDate}
                      teachers={el.teachers}
                      startDate={el.startDate}
                      endDate={el.endDate}
                      isDelete={() => {onDelete(el._id)}}
                      city={el.city}
                      fee={el.fee}
                      description={el.description}
                      isCart={true}
                      />
                  )
              })
          }
          </div>
          {message ? <Alert message={message} 
          color={message.search("network") != -1 | message.search("process") != -1 | message.search("wrong") != -1 ? "red" : "green"}/> : null}
      </>
    );
}

export default Cart;
