import React, { useEffect, useState} from 'react';
// import styles from "./add_new.module.css"
import { Grid } from "@material-ui/core";
import MUIDataTable from "mui-datatables";
import TrainingProgrammes from  "../../../TrainingProgrammes/TrainingProgrammes"
import { withRouter } from 'react-router';
import { useUserState } from '../../../../context/UserContext';
// import PageTitle from "../PageTitle";

const ViewTrainingPrograms = (props) => {
    const [data, setData] = useState({})
    useEffect(() => {
        let program = props.location.state
        
        if(program) {
            if(Object.keys(data).length == 0) {
                setData(program.data)
            } 
        } else {
            let domain = window.location.href.search('teacher') != -1 ? "teacher": "institution"
            props.history.replace(`/${domain}/programme`)
        }
    })

    useEffect(() => {
        //console.log(props.location)
    })
    return (
        <TrainingProgrammes
        title={data.title}
        enrolled={data.enrolled}
        id={data._id}
        amount={data.amount}
        duration={data.duration}
        startDate={data.training_start_date}
        endDate={data.training_end_date}
        fee={data.programme_amount}
        description={data.Description}
        enroll={""}
        />
    )

}
export default withRouter(ViewTrainingPrograms);
