import React, { useEffect, useState} from 'react';
import { Grid } from "@material-ui/core";
import MUIDataTable from "mui-datatables";
import { useHistory, withRouter } from "react-router-dom";
import axios from 'axios';
import Alert from "../../../Toast/Toast"
import TrainingProgrammes from  "../../../TrainingProgrammes/TrainingProgrammes"

const EnrolledProgramme = (props) => {

    const history = useHistory()
    const [programmeList, setProgrammeList] = useState([])

    let column =  [
      {
        name: "_id",
      },
        {
          name: "title",
          options: {
            filter: true,
          }
        },{
            name: "city",
            options: {
                filter: true,
            }
        },
        {
          name: "Description",
          options: {
            filter: true,
          }
        },{
          name: "programme_amount",
          options: {
                filter: true,
              }
        },
        {
          name: "training_start_date",
          options: {
            filter: true,
          }
        },{
            name: "training_end_date",
            options: {
                filter: true,
              }
        },
        {
          name: "dueDate",
          options: {
              filter: true,
          }
      },
      {
        name: "enrolled",
        options: {
          filter: true,
        }
    },
    {
      name: "link"
  },
    {
      name: "centers",
      options: {
        customRowRender: (value) => {
          let location = value[value.length - 1] 
          return <p>{location ? location : ""}</p>
        }
      }
    }, {
      name: "amount",
      label: "Amount"
    },
    {
      name: "teachers",
      label: "Teacher"
    }
    ]


    useEffect(() => {
      if(programmeList.length == 0) {
        //console.log(window.location)
        let id = localStorage.getItem('i_id')
        axios.get(`https://campusfield.in/api/training/get_institute/${id}`)
        .then(res => {
          //console.log(res.data.output[0])
          if (res.data.error_code == 1) {
            let pro = []
            console.log(res.data.output)
            for(let d of res.data.output) {
              for(let e of d.programme) {
                pro.push({...e, teachers: d.members})
              }
            }
            setProgrammeList(pro)
          }
        })
        .catch(err => {
          //console.log(err)
        })
      }
    }, [])
    
    return (
      <div id="programs">
    <Grid container spacing={4}>
        <Grid item xs={12}>
          <MUIDataTable
            title="Enrolled Programmes"
            data={programmeList}
            columns={column}
            options={{  
              customRowRender: (data, dataIndex, rowIndex) =>{
                //console.log(data)
                return (
                  <TrainingProgrammes
                    // reload={setReload}
                    viewString="View"
                    dueDate={data[7]}
                    title={data[1]}
                    enrolled={data[8]}
                    amount={data[11]}
                    teachers={data[12]}
                    id={data[0]}
                    duration={data.duration}
                    startDate={data[5]}
                    endDate={data[6]}
                    fee={data[4]}
                    link={data[9]}
                    centres={data[10] ? data[10] : []}
                    city={data[2]}
                    description={data[3]}
                    enroll={""}
                    enrolled={true}
                    />
                )
              },
              selectableRows: false,
              selectableRowsOnClick: false,
              filter: true,
              onRowsSelect: (a, b) => {
                b.map((el) => {
                  let tempList = [];
                 })
              },
            }
          }
          />
        </Grid>
        {props.location.state ? <Alert message={props.location.state.message} color="green"/> : null}        
    </Grid>
    </div>
)
}

export default withRouter(EnrolledProgramme);
