import React, { useEffect } from 'react';

import styles from "./AvailableJob.module.css"
import { NavLink,Link } from "react-router-dom" 
import { ThemeProvider } from '@material-ui/core';

const Availablejob = (props) => {

    useEffect(() => {
        //console.log(props)
    })
    const delete_job = (jobId, instId) =>{
        // alert(jobId)
        // //console.log(jobId)
        props.delete_jobCB(jobId, instId)
    }
    const temp_function = (jobId, instId, title, instName) =>{
     localStorage.setItem("job_id", jobId);
     localStorage.setItem("job_title", title);
     localStorage.setItem("inst_id", instId);
     localStorage.setItem("name", instName);
    }
  return (
    <div className={styles["jobs-container"]}>
        <div className={styles["job-container"]}>
            <h4>
                {props.title}
            </h4>
            {
                props.isSubscribedUser || props.appliedJobs ?     
                <div className={styles["detail-container"]}>
                    <i className="fa fa-university"  aria-hidden={true}/>
                    <h3>
                       <b> INSTITUTION :</b> {props.inst_name}
                    </h3>
                </div>: null
            }
            {
                props.appliedJobs ? null :
                <>
            <div className={styles["detail-container"]}>
                <i className="fa fa-book" aria-hidden={true}/>
                <h3>
                   <b> QUALIFICATION :</b> {props.qualification}
                </h3>
            </div>
            <div className={styles["detail-container"]}>
                <i className="fa fa-bookmark"  aria-hidden={true}/>
                <h3>
                    <b>EXPERIENCE :</b> {props.experience}
                </h3>
            </div>
            <div className={styles["detail-container"]}>
                <i className="fa fa-book" aria-hidden={true}/>
                <h3>
                    <b>VACANCY :</b> {props.vacancy}
                </h3>
            </div>
            </>
            }
            <div className={styles["interview-date"]}>
                <i className="fa fa-clock-o" aria-hidden={true}/>
                <b>DATE:</b> {props.lastDate.slice(0, 10)}
            </div>
            {
                props.appliedJobs ? null :
            <div className={styles["links"]}>
                <div className={styles["apply-link"]}>
                    <NavLink 
                    onClick = {() => {temp_function(props.id, props.instId, props.title, props.inst_name)}}
                    to={
                         props.isInstitution ? 
                            "/institution/applications" : "/teacher/jobs/apply"
                        }
                    >
                        {props.isInstitution ? 
                            "Applicants" : "Apply"
                        }
                    </NavLink>
                </div>
                {
                    props.isInstitution ? 
                    <div className={styles["delete"]} onClick={()=>{delete_job(props.id, props.instId, props.title)}}>
                        Delete
                    </div> : null
                }
                
            </div>
            }
        </div>
    </div>
  );
}

export default Availablejob;
