import React, { useState } from 'react';
import styles from "./add_new.module.css"
import CommonHeader from "../../../Teacher/CommonHeader/CommomHeader";
import * as constants from "../../constants/constants"
import axios from "axios"
import PageTitle from "../../../PageTitle";
import { useHistory } from "react-router-dom";

const Add_new_courses = (props) => {
    const history = useHistory();

    const back = () =>{
      history.push("/institution/jobs")
    }
    const [title, setTitle] = useState("")
    const [experience, setExperience] = useState("")
    const [qualificaiton, setQualification] = useState("")
    const [vacancy, setVacancy] = useState("")
    const [lastDate, setLastDate] = useState("")
    const [description, setDescription] = useState("")

    const onPostJob = () => {
        let id = localStorage.getItem("id") 
        axios.post(constants.url + "institution/create_job", {"title": title,
        "required_exp": experience,
        "Qualification": qualificaiton,
        "Number_of_vacancy": vacancy,
        "Last_Date": lastDate,
        "Description": description,
        "institution_id": id,
        "institution_name": "name"
    })
    .then((res) => {
        //console.log(res)
        alert("added sucessfully")
        // props.history.push("/sign-in")
    }).catch((err) => {
        //console.log(err)
    })
    } 

    const onChangeTexthandler = (event, i) => {
        let value = event.target.value

        if(i === 1) {
            setTitle(value)
        }

        if(i === 2) {
            setExperience(value)
        }

        if(i === 3) {
            setQualification(value)
        }

        if(i === 4) {
            setVacancy(value)
        }

        if(i === 5) {
            setLastDate(value)
        }

        if(i === 6) {
            setDescription(value)
        }
    }


    return (
        <>

    <PageTitle title="Dashboard / New Job" button="Back" add_new={back} />

          <div className={styles["sub-container"]}>
         
                <form className={styles["post-job-form"]}>
                    <div className={styles["input-container"]}>
                        <label>
                            Title *
                        </label>
                        <input
                        value={title}
                        onChange={(event) => { onChangeTexthandler(event, 1)} }
                        />
                    </div>
                    <div className={styles["input-container"]}>
                        <label>
                            Experience Required *
                        </label>
                        <input
                        value={experience}
                        onChange={(event) => { onChangeTexthandler(event, 2)} }/>
                    </div>
                    <div className={styles["input-container"]}>
                        <label>
                            Qualification Opting *
                        </label>
                        <input
                        value={qualificaiton}
                        onChange={(event) => { onChangeTexthandler(event, 3)} }/>
                    </div>
                    <div className={styles["input-container"]}>
                        <label>
                            Number of Vacancy *
                        </label>
                        <input
                        value={vacancy}
                        onChange={(event) => { onChangeTexthandler(event, 4)} }/>
                    </div>
                    <div className={styles["input-container"]}>
                        <label>
                            Last Date *
                        </label>
                        <input 
                        value={lastDate}
                        type="date"
                        onChange={(event) => { onChangeTexthandler(event, 5)}}/>
                    </div>
                    <div className={styles["description"]}>
                        <label>
                            Description *
                        </label>
                        <textarea
                        value={description}
                        onChange={(event) => { onChangeTexthandler(event, 6)}}/>
                    </div>
                </form>
                <div className={styles["button-container"]}>
                    <button className={styles["create-button"]} onClick={onPostJob}>
                        Create
                    </button>
                    <button className={styles["cancel-button"]}>
                        Cancel
                    </button>
                </div>
          </div>
        </>
  );
}

export default Add_new_courses;
