import React, { useEffect, useState } from 'react';
import styles from "./PostedJobs.module.css"
import CommonHeader from "../../Teacher/CommonHeader/CommomHeader"
import Postedjob from "../../Teacher/AvailableJobs/AvailableJob/AvailableJob"
import axios from "axios"
import * as constants from "../constants/constants"
import PageTitle from "../../PageTitle";
import { withRouter } from "react-router-dom"
import { useHistory } from "react-router-dom";
import EmptyList from '../../UI/EmptyList/EmptyList';
import Alert from "../../Toast/Toast"

const Postedjobs = (props) => {
  const history = useHistory();

  const move_to_edit = () =>{
    history.push("/institution/jobs/new")
  }
  const  [jobs, setJobs] = useState([]); 
  const delete_job = (job_id,institution_id) =>{

    axios.delete("https://campusfield.in/api/" + "institution/delete_job/" + job_id)
    .then((res) => {
        // if(res.data.error_code === 1) {
          getDetails()

        // }
    })
    .catch((err) => {
        //console.log(err)
    })

  }
  let jobList = jobs.map((el) => {

    return (
      <Postedjob
      id={el._id}
      isInstitution={true}
      title={el.title}
      experience={el.required_exp}
      qualification={el.Qualification}
      vacancy={el.Number_of_vacancy}
      lastDate={el.Last_Date}
      inst_name={el.institution_name}
      delete_jobCB={delete_job}
      />
    )
  })

  useEffect(() => {
    //console.log(props)
    getDetails()
  },[])
const getDetails = () =>{
  // if(jobs.length === 0) {
    let id = localStorage.getItem("id")
    axios.get("https://campusfield.in/api/" + "institution/job_list/" + id)
    .then((res) => {
        if(res.data.error_code === 1) {
          //console.log(res,'data')
          // let id = localStorage.setItem("id")
          setJobs(res.data.output)
        }
    })
    .catch((err) => {
        //console.log(err)
    })
  // }
}
  return (
    <>

    <PageTitle title="Dashboard / Jobs" button="Post Job" add_new={move_to_edit} />
        <div className={styles["sub-container"]}>
              {jobList && jobList.length ? jobList : <EmptyList message="No Jobs Posted!"/>}
        </div>
        {
          props.location.state?.message ? <Alert message={props.location.state.message} color="green"/> : null
        }
    </>
  );
}

export default withRouter(Postedjobs);
