import React, { useEffect, useState } from 'react';
import styles from "./main.module.css"
import CommonHeader from "../../Teacher/CommonHeader/CommomHeader"
// import Postedjob from "../../Teacher/AvailableJobs/AvailableJob/AvailableJob"
import add_new from './add_new/add_new'
import axios from "axios"
import * as constants from "../constants/constants"
import PageTitle from "../../PageTitle";

  import { useHistory } from "react-router-dom";

const Main_placement = (props) => {
  const history = useHistory();

  const move_to_edit = () =>{
    history.push("/institution/placement/new")
  }
  const  [jobs, setJobs] = useState([]); 

  let jobList = jobs.map((el) => {

    return (
      <add_new
      id={el._id}
      isInstitution={true}
      experience={el.required_exp}
      qualification={el.Qualification}
      vacancy={el.Number_of_vacancy}
      lastDate={el.Last_Date}
      />
    )
  })

  useEffect(() => {

    if(jobs.length === 0) {
      let id = localStorage.getItem("id")
      axios.get("https://campusfield.in/api/" + "institution/job_list/" + id)
      .then((res) => {
          if(res.data.error_code === 1) {
            //console.log(res,'data')
            // let id = localStorage.setItem("id")
            setJobs(res.data.output)
          }
      })
      .catch((err) => {
          //console.log(err)
      })
    }
  },[])

  return (
    <>

    <PageTitle title="Placements" button="Add" add_new={move_to_edit} />
        <div className={styles["sub-container"]}>
              {jobList}
        </div>
    </>
  );
}

export default Main_placement;
