export default function Baners() {
  const delete_function = (id) => {
    axios.delete("https://campusfield.in/api/site_management/banner/" + id)
      .then(res => {
        if (res.data) {
          if (res.data.error_code) {
            getDetails()
          }
        }
      })
  }
  const columns = [
    {
      name: "name",
      label: "Name",
      options: {
        filter: true,
        sort: true,
      }
    },
    {
      name: "description",
      label: "Diescription",
      options: {
        filter: true,
        sort: true,
      }
    },
    {
      name: "image_path",
      label: "Image",
      options: {
        filter: false,
        sort: false,
        empty: true,

        customBodyRender: (value, tableMeta, updateValue) => {
          let img = tableMeta.rowData[2];
          //console.log(img)
          return (
            <div>
              <img src={img} style={{ width: "50px", height: "auto" }} />
              {/* <img src ={ require('./small-logo.png') } style={{width:"50px",height:"auto"}}/> */}
            </div>
          );
        }
      }
    },
    {
      name: "_id",
      label: "Name",
      options: {
        filter: true,
        display: false,
        sort: true,
      }
    },
    {
      name: "status",
      label: "Actions",

      options: {
        filter: false,
        sort: false,
        empty: true,

        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div>

              <Button
                onClick={() => { delete_function(tableMeta.rowData[3]) }}
                color="secondary"
                size="small"
                className="px-2"
                variant="contained"
              >
                Delete
                </Button>
            </div>

          );
        }
      }
    },
  ];
  const [data, setData] = useState([])
  const [name, setname] = useState([])
  const [description, setDescription] = useState([])
  const [file, setFile] = useState([])
  const [updatemessage, setupdatemessage] = useState(false)




  const [open, setOpen] = React.useState(false);
  const [isdiable, setIsdiable] = React.useState(false);
  const classes = useStyles();

  // const handleClose = () => {
  //   setOpen(false);
  // };
  // const handleToggle = () => {
  //   setOpen(!open);
  // };

  useEffect(() => {
    getDetails()
  }, []);

  const onFileChange = (event) => {
    let value = event.target.files[0]
    setFile(value)
  }

  const getDetails = () => {
    axios.get("https://campusfield.in/api/site_management/banner")
      .then(res => {
        if (res.data) {
          if (res.data.error_code) {
            // //console.log(res.data.output, 'dat')
            setData(res.data.output)
          }
        }
      })
  }

  // const handleOpen = (params) => {
  //   //console.log(params)
  //   setOpen(params);
  // };

  // const handleClose = () => {
  //   setOpen(false);
  //   setIsdiable(false)

  // };
  const onChangeTextHandler = (event, i) => {
    let value = event.target.value

    if (i === 1) {
      setname(value)
    }
    if (i === 2) {
      setDescription(value)
    }

  }
  const handleChange = event => {
    // setCurrency(event.target.value);
  };

  const addNewBanner = () => {
    if(name != '' && description != ''){
    setOpen(true)
    let value = new FormData()
    value.append("name", name)
    value.append("description", description)
    value.append("item_img", file)
    axios.post("https://campusfield.in/api/site_management/banner", value)
      .then((res) => {
        // //console.log(res)

        setname('')
        setDescription('')
        setFile('')
        getDetails()
        setOpen(false)
      })
      .catch((err) => {
        //console.log(err)
      })
    }else{
      setupdatemessage(true)
      setTimeout(function () { setupdatemessage(false) }, 2000);
    }
  }

  return (
    <>
    <div>
      {/* <Button variant="outlined" color="primary" onClick={handleToggle}>
        Show backdrop
      </Button> */}
      <Backdrop className={classes.backdrop} open={open} >
        <CircularProgress color="inherit" />
      </Backdrop>
    </div>
      <PageTitle title="Banners" button="Add" add_new={addNewBanner} />
      <div style={{marginBottom: "2rem"}}>
        <div style={{ marginTop: 16, padding: 16, display: "flex", justifyContent: "space-between" }}>
          <input type="file" id="12" placeholder="File should be .jpeg .png" onChange={(event) => { onFileChange(event) }} />
        </div>
        <TextField id="standard-basic" value={name} required label="Add Title" style={{ width: "45%", marginRight: "10px" }} onChange={(event) => { onChangeTextHandler(event, 1) }} />
        <TextField
          value={description}
          required
          disabled={isdiable}
          // style={{ width: "45%", height: "45%" }}
          style={{ width: "45%" }}
          id="standard-multiline-basic"
          label="Description"
          multiline
          // rows="4"
          onChange={(event) => { onChangeTextHandler(event, 2) }}
        />

      </div>
      <Grid container spacing={4}>
        <Grid item xs={12}>
          <MUIDataTable
            title="Banners List"
            data={data}
            columns={columns}
            options={{
              onRowClick: (rowData, rowState) => {
                setname(rowData[0])
                setDescription(rowData[1])
                // delete_function(rowData[3])
                // //console.log(rowData, rowState);

              }
            }}
          />
        </Grid>

      </Grid>

      {updatemessage  ? <Alert message="Need to enter Name & decription" color="red"/>: null}
    </>
  );
}
