import React, {useEffect, useState} from 'react';
import CommonHeader from "../CommonHeader/CommomHeader"
import PageTitle from "../../PageTitle/PageTitle";
import Button from '@material-ui/core/Button';
import { useHistory } from "react-router-dom";
import styles from "./Order.module.css"
import { makeStyles } from '@material-ui/core/styles';
import axios from "axios"
import * as constants from "../constants/constants"
import OrderTemplate from "../../OrderTemplate/OrderTemplate"
import Alert from "../../Toast/Toast"

const Order = (props) => {
    const history = useHistory();
    const [data, setData] = useState([])
    const [message, setMessage] = useState("")
    const useStyles = makeStyles((theme) => ({
        button: {
          margin: theme.spacing(1),
        },
      }));
    const classes = useStyles();

    
    const updateData = () => {
        let id = localStorage.getItem('id')
        axios.get('https://campusfield.in/api/order/' + id)
        .then(res => {
            if (res.data.error_code === 1) {
                setData(res.data.output)
            }
        }) 
        .catch(err => {
            setMessage("Something went wrong")
        })
    }
    useEffect((props) => {
        updateData()
    }, []) 

    const onBack = () => {
        history.goBack()
    }

    return (
      <>
        <PageTitle title="Order History" button="Back" disabled={!data.length > 0} add_new={onBack} />
          <div className={styles["sub-container"]}>
          {data.length == 0 ?  null :
              data.map((el, i) => {
                  return (
                      <OrderTemplate
                      title={i+1}
                      orderId={el.order_id}
                      programmes={[...el.programmes]}
                      amount={el.amount}
                      />
                  )
              })
          }
          </div>
          {message ? <Alert message={message} 
          color={message.search("network") != -1 | message.search("process") != -1 | message.search("wrong") != -1 ? "red" : "green"}/> : null}
      </>
    );
}

export default Order;
