import React, { useEffect, useState } from 'react';
import styles from "./AddQualifications.module.css"
import axios from "axios"
import * as constants from "../../constants/constants"
import CommonHeader from "../../CommonHeader/CommomHeader";
import PageTitle from "../../../PageTitle";
import { useHistory } from "react-router-dom";
import Alert from "../../../Toast/Toast"
import Button from '@material-ui/core/Button';
import InvalidHighlighter from "../../../Home/Auth/InvalidHighlighter/InvalidHighlighter"

const AddQualifications = (props) => {
    const [message, setMessage] = useState("")
    let isState = props.location.state
    const [isSuccess, setIsSuccess] = useState(false)
    const history = useHistory();
    let isEdit = props.match.url.search("edit") != -1
    const [qualificationList, setQualificationList] = useState([])
    const [universityList, setUniversityList] = useState([])
    const [isSerialNumberExist, setIsSerialNumberExist] = useState(false)

    if(isEdit && !props.history.location.state) {
        history.replace("/teacher/qualifications")
    }

    const [qualification, setQualification] = useState({
        val: isEdit && isState ? props.location.state.qualification?.toUpperCase() : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })
    const [passout, setPassOut] = useState({
        val: isEdit && isState ? props.location.state.passOut : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })
    const [percentage, setPercentage] = useState({
        val: isEdit && isState ? props.location.state.percentage : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })
    const [mainSubject, setMainSubject] = useState({
        val: isEdit && isState ? props.location.state.subject : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })
    const [college, setCollege] = useState({
        val: isEdit && isState ? props.location.state.college : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })
    const [university, setUniversity] = useState({
        val: isEdit && isState ? props.location.state.university : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })
    const [universityOther, setUniversityOther] = useState({
        val: isEdit && isState ? props.location.state.university : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })
    const [serialNumber, setSerialNumber] = useState({
        val: isEdit && isState ? props.location.state.serialNumber : "",
        isValid: isEdit && isState && props.location.state.serialNumber ? true : false,
        touched: false
    })
    const [certificate, setCertificate] = useState({
        val: "",
        // isValid: isEdit && isState,
        isValid: true,
        touched: isEdit && isState
    })

    const back = ()=>{
        history.goBack();
    }

    useEffect(() => {
        console.log(props.location.state)
        if(!props.location.state) {
            props.history.push('/teacher/qualifications')
        }



        if(qualificationList.length === 0) {
            axios.get(constants.url + "site_management/qualification")
            .then((response) => {
                // //cons.log(response)
                setQualificationList([...response.data.output])
            })
            .catch(err => {
                // //cons.log(err)
           })
        }

        if(universityList.length === 0) {
            axios.get(constants.url + "site_management/universities")
            .then((response) => {
                // //cons.log(response)
                setUniversityList([...response.data.output])
                if(isEdit && isState && universityList.indexOf(props.location.state.university) === -1) {
                    setUniversity({
                        ...university,
                        val: "other"
                    })  
                } 
            })
            .catch(err => {
                // //cons.log(err)
           })
        }
    }, [])


    const validateForm = () => {
        let partialValidation = qualification.isValid && college.isValid  && passout.isValid &&
                percentage.isValid && certificate.isValid

        if(qualification.val.search("10") != -1) {
            partialValidation = partialValidation && serialNumber.isValid 
        } else {
            partialValidation = partialValidation && mainSubject.isValid
        }

        if(props.location.state) {
            if(!props.location.state.resume === undefined) {
                partialValidation = partialValidation && certificate.isValid
            }
        }

        if(university.val.toLowerCase() === "other") {
            partialValidation = partialValidation && universityOther.isValid
        } else {
            partialValidation = partialValidation && university.isValid
        }
        
        return partialValidation
    }

    const setTouchedTrue = () => {
        setQualification({...qualification, touched: true})
        setPassOut({...passout, touched: true})
        setPercentage({...percentage, touched: true})
        setMainSubject({...mainSubject, touched: true})
        setCollege({...college, touched: true})
        setUniversity({...university, touched: true})
        
        if(props.location.state) {
            if(props.location.state.resume === undefined) {
                setCertificate({...certificate, touched: true})
            }
        }

        setSerialNumber({...serialNumber, touched: true})
    }

    let getFormData = () => {
        let currentState = props.location.state
        let data = new FormData()
        if(certificate.val !== "") {
            data.append('file_id', certificate.val)
        }
        let tempData = {}
    
        if(qualification.val !== currentState.qualification) {
            tempData["qualification"] = qualification.val
        }

        if(passout.val !== currentState.passOut) {
            tempData["passout"] = passout.val
        }

        if(percentage.val !== currentState.percentage) {
            tempData["percentage"] = percentage.val
        }

        if(mainSubject.val !== currentState.subject) {
            tempData["mainSubject"] = mainSubject.val
        }

        if(college.val !== currentState.college) {
            tempData["college"] = college.val
        }

        if(String(university.val).toLowerCase() !== String(currentState.university).toLowerCase()) {
            tempData["university"] = university.val.toLowerCase() === "other" ? universityOther.val : university.val
        }

        if(serialNumber.val !== currentState.serialNumber) {
            tempData["certificate_id"] = serialNumber.val
        }
        
        data.append('Qualification', JSON.stringify(tempData))
        // data.append("certificate_id", serialNumber.val)
        return data
    }


    const initiateAddQualification = () => {
        let id = localStorage.getItem("id")
        let data = getFormData()
        axios.post(constants.url + "teachers/update/" + id + "/Qualification", data)
            .then((res) => {
                // alert("qualification added successfully")
                history.push({
                    pathname: "/teacher/qualifications",
                    state: {
                    updateMessage: "added successfully"
                    }
                })
                // props.history.push("/sign-in")
            }).catch((err) => {
                setMessage("something went wrong!")
                setIsSuccess(false)
                setTimeout(() => {
                    setMessage("")
                  }, 3000)
            })
    }

    const onAddQualification = () => {
        let isValid = validateForm()
        setTouchedTrue()
        
        if(isValid) {
            
            if(qualification.val.search("10") != -1) {
                validateSerialNumberAndAdd(false)
            } else {
                initiateAddQualification()
            }
        }
    }

    const validateSerialNumberAndAdd = (isUpdate) =>{
        axios.get(constants.url + `teachers/certificate_verification/${serialNumber.val}`)
                .then((res) => {
                    if(res.data.msg == 'Not found') {
                        if(isUpdate) {
                            initiateUpdateQualification()
                        } else {
                            initiateAddQualification()
                        }
                    } else {
                        throw Error()
                    }
                })
                .catch((err) => {
                    setIsSerialNumberExist(true)
                    setSerialNumber({
                        ...serialNumber,
                        isValid: false
                    })
                })
    }

    const initiateUpdateQualification = () => {
        let id = localStorage.getItem("id")
        let data = getFormData()
        axios.post(constants.url + "teachers/edit/" + id + "/Qualification/" + props.location.state.id + "/1", data)
        .then((res) => {
            history.push({
                pathname: "/teacher/qualifications",
                state: {
                updateMessage: "updated successfully"
                }
            })
            // props.history.push("/sign-in")
        }).catch((err) => {
            setMessage("something went wrong!")
            setIsSuccess(false)
            setTimeout(() => {
                setMessage("")
              }, 3000)
        })
    }

    const onUpdateQualification = () => {
        let isValid = validateForm()
        setTouchedTrue()
        //cons.log(isValid)
        if(isValid) {
            if(qualification.val.search("10") != -1) {
                if(isEdit && isState && props.location.state.serialNumber) {
                    if(props.location.state.serialNumber !== serialNumber.val) {
                        validateSerialNumberAndAdd(true)
                    } else {
                        initiateUpdateQualification()
                    }
                } else {
                    initiateUpdateQualification()
                }
            } else {
                initiateUpdateQualification()
            }

        }
    }

    const clearFields = () => {
        setQualification({...qualification, val: "", touched: false})
        setPassOut({...passout, val: "", touched: false})
        setPercentage({...percentage, val: "", touched: false})
        setMainSubject({...mainSubject, val: "", touched: false})
        setCollege({...college, val: "", touched: false})
        setUniversity({...university, val: "", touched: false})
        setSerialNumber({...serialNumber, val: "", touched: false })
    }
    
    const validate = (i, val) => {
        if(i === 8) {
            return val.length > 0
        }

        if(i == 7) {
            return val.length > 0;
        }

        if(i == 6) {
            if(qualification.val.search("10") !== -1) {
                return true
            }

            return val.length
        }

        if(i !== 2 && i !==3) {
            if(val.length) {
                return true
            }
        }

        if(i === 2) {
            return val.length === 4    
        }

        if(i === 3) {
            return  /^\d+$/.test(val) && val >= 0 && val <= 100 
        }

        
        if(i === 4) {
            return val.length > 0 
        }
    }

    
    //cons.log(mainSubject.isValid)

    const onChangeTextHandler = (event, i) => {
        let value = event.target.value
        let isValid = false

        if(i === 1) {
            setQualification({
                ...qualification,
                val: value?.toUpperCase(),
                isValid: validate(i, value)
                
            })

            setMainSubject({
                ...mainSubject,
                isValid: value.search("10") !== -1 ? true : validate(4, mainSubject.val)
            })
        }

        if(i === 2) {
            if(value.length && isNaN(parseInt(value))) {
                return
            }

            setPassOut({
                ...passout,
                val: value,
                isValid: validate(i, value)
                
            })
        }

        if(i === 3) {
            setPercentage({
                ...percentage,
                val: value,
                isValid: validate(i, value)
                
            })
        }

        if(i === 4) {
            setMainSubject({
                ...mainSubject,
                val: value,
                isValid: qualification.val.search("10") !== -1 ? true : validate(i, value)
                
            })
        }

        if(i === 5) {
            setCollege({
                ...college,
                val: value,
                isValid: validate(i, value)
                
            })
        }

        if(i === 6) {
            setUniversity({
                ...university,
                val: value,
                isValid: validate(i, value)
                
            })
        }

        if(i === 7) {
            if(/^[0-9]+$/.test(value) || value === '') {
                setSerialNumber({
                    ...serialNumber,
                    val: value,
                    isValid: validate(i, value)
                    
                })
            }
        }

        if(i === 8) {
            setUniversityOther({
                ...universityOther,
                val: value,
                isValid: validate(i, value)
                
            })
        }
    }

    const onFileChange = (event) => {
        let value = event.target.files[0]
        setCertificate({
            ...certificate,
            val: value,
            isValid: true
        })
    }

    return (
    <>
      {/* <div className={styles["title"]}>
        DASHBOARD / ACADEMIC QUALIFICATIONS / NEW
      </div> */}
<PageTitle title={`Academic Qualification / ${isEdit ? "Edit" : "New"}`} button="Back" add_new={back}  />

      <div className={styles["sub-container"]}>
            {/* <CommonHeader
            title="ADD NEW QUALIFICATION"
            link="BACK"
            to="/teacher/qualifications"/> */}
            <form className={styles["qualification-form"]}>
                <div className={styles["input-container"]}>
                    <label>
                        Qualification*
                    </label>
                    <select
                    style={{border: qualification.touched && !qualification.isValid ? "1px solid red" : "1px solid black" }}
                    onChange={(event) => {onChangeTextHandler(event, 1)}}
                    value={qualification.val} 
                    id="degree" 
                    name="qualification">
                        <option value="">SELECT QUALIFICATION</option>
                        {
                            props.location.state && !props.location.state.isQualification ? <option value={'10th'.toUpperCase()}>{'10th'.toUpperCase()}</option> : qualificationList.map((el) => {
                                return <option value={el.name}>{el.name.toUpperCase()}</option>
                            })
                        }
                    </select>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Pass-out year*
                    </label>
                    <input
                    maxLength={4}
                    value={passout.val}
                    className={[passout.touched && !passout.isValid ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setPassOut({...passout, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 2)}}
                    />
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Mark Percentage*
                    </label>
                    <input
                    type="number"
                    value={percentage.val}
                    className={[percentage.touched && !percentage.isValid ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setPercentage({...percentage, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 3)}}
                    />
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        Main Subject*
                    </label>
                    
                    <input
                    disabled={qualification.val.search("10") !== -1}
                    value={mainSubject.val}
                    className={[mainSubject.touched && !mainSubject.isValid ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {
                        if(qualification.val.search("10") !== -1) {
                            setMainSubject({...mainSubject, isValid: true, touched: true})
                        } else {
                            
                            setMainSubject({...mainSubject, touched: true})
                        }
                    }}
                    onChange={(event) => {onChangeTextHandler(event, 4)}}/>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        College/School*
                    </label>
                    <input
                    value={college.val}
                    className={[college.touched && !college.isValid ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setCollege({...college, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 5)}}/>
                </div>
                <div className={styles["input-container"]}>
                    <label>
                        University / Board*
                    </label>
                    <select
                    style={{border: university.touched && !university.isValid ? "1px solid red" : "1px solid black" }}
                    onChange={(event) => {onChangeTextHandler(event, 6)}}
                    value={university.val} 
                    id="degree" 
                    name="qualification">
                        <option value="">SELECT UNIVERSITY</option>
                        {
                            universityList.map((el) => {
                                return <option value={el.name}>{el.name.toUpperCase()}</option>
                            })
                        }
                        <option value="other">OTHER</option>
                    </select>
                </div>
                {
                    university.val.toLowerCase() === "other" ? 
                    <div className={styles["input-container"]}>
                    <label>
                        University*
                    </label>
                    <input
                    value={universityOther.val}
                    className={[universityOther.touched && !universityOther.isValid ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setUniversityOther({...universityOther, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 8)}}/>
                </div> : null
                }
                
                {
                    qualification.val.search("10") != -1 ?
                <div className={styles["input-container"]}>
                    <label>
                        Register / Roll numbers*
                    </label>
                    <input
                    value={serialNumber.val}
                    className={[serialNumber.touched && !serialNumber.isValid ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setSerialNumber({...serialNumber, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 7)}}/>
                    {isSerialNumberExist ? <p style={{color: "red", marginTop: "5px"}}>Serial Number already exists</p> : null}
                </div> : null
                }
                {
                    isEdit && isState ? 
                    <div className={styles["input-container"]}>
                    <label>
                        Certificate*
                    </label>
                    <a
                    style={{marginTop: "1rem", display: "block"}} 
                    href={props.location.state?.resume}>Download Certificate</a>
                </div> : null
                }
                <div className={styles["input-container"]}>
                    <label style={{color: certificate.touched && !certificate.isValid ? "red" : "rgb(74, 74, 74"}}>
                        {isEdit && isState ? "Update Certificate (.png, .jpeg, .pdf)" : "Certificate (.png, .jpeg, .pdf)*"}
                    </label>
                    <input 
                    onChange={(event) => {onFileChange(event)}}
                    className={styles["file"]}
                    style={{border: "none", boxShadow: "0 0 0 0", backgroundColor: "white"}}
                    accept="image/png, image/jpeg, .pdf"
                    type="file"/>
                </div>
                
            </form>
            <div className={styles["button-container"]}>
                <Button
                    style={{marginRight: "0.9rem", fontSize: "0.8rem"}}
                    variant="contained"
                    color="primary"
                    onClick={isEdit ? onUpdateQualification : onAddQualification}
                >
                    {isEdit ? "Update" : "Create"}
                </Button>
                <Button
                    style={{fontSize: "0.8rem"}}
                    variant="contained"
                    color="secondary"
                    onClick={() => {
                        if(isEdit) {
                            history.goBack()
                        } else {
                            clearFields()}}}
                >
                    Cancel
                </Button>
            </div>
      </div>
      {
        message ? <Alert message={message} color={isSuccess ? "green" : "red"}/> : null
      }
    </>
  );
}

export default AddQualifications;
