import React, {useEffect, useState} from 'react';
import styles from "./Achievements.module.css"
import PageTitle from "../../PageTitle";
import { useHistory } from "react-router-dom";
import axios from "axios"
import * as constants from "../constants/constants"
import Alert from "../../Toast/Toast"
import Achievement from "./Achievement/Achievement"
import EmptyList from '../../UI/EmptyList/EmptyList';

const Achievements = (props) => {
    let updatemessage = props.location.state ? props.location.state.updateMessage : ""
    const history = useHistory();
    const [data, setData] = useState("")
    const [message, setMessage] = useState("")
    let achievements;

    React.useEffect(() => {
      window.scrollTo(0, 0);
    }, [])

    const onDeleteAchievement = (achievementId) => {
      let id = localStorage.getItem("id")
  
      axios.post(constants.url + "institution/Achievement/" + id + `/${achievementId}/0`, {
        "Achievement":{
            "id": achievementId,
        }
      })
      .then((res) => {
          if(res.data.msg.search("delete") != -1) {
            getAchievements()
            setMessage("deleted successfully")
          }
      }).catch((err) => {
          setMessage("network error!")
          setTimeout(() => {
              setMessage("")
            }, 3000)
          //console.log(err)
      })
  }

    let id = localStorage.getItem("id")
    useEffect(() => {
      //console.log(data)
      if(data.length === 0) {
        getAchievements()
      }
    })

      const getAchievements = () => {
        axios.get(constants.url + "institution/dashboard/" + id)
        .then((res) => {
            if(res.data.error_code === 1) {
              setData(res.data.output[0]) 
            }
        })
        .catch((err) => {
            //console.log(err)
        })
      }

    if(data.Achievement && data.Achievement.length) {
      //console.log(data)
      achievements = data.Achievement.map((el, i) => {
        //console.log({...el})
        return (
          <Achievement
          onDelete={onDeleteAchievement}
          id={el.id}
          key={i}
          year={el.year}
          title={el.title}
          description={el.description}
          />
        )
      })   
    }

    const move_to_edit = () => {
      history.push("/institution/achievement/new")
    }

  return (
    <>
    <PageTitle title="Achievements" button="Add" add_new={move_to_edit} />
        <div className={styles["sub-container"]}>
        {achievements && achievements.length ? achievements : <EmptyList message="No achievements to list!"/>}
        </div>
        {message ? <Alert message={message} color={message.search("network") != -1 ? "red" : "green"}/> : null}
        {updatemessage  ? <Alert message={updatemessage} color="green"/>: null}
    </>
  );
}

export default Achievements;
