import React, { useState, useEffect } from 'react';
import styles from "./ApplyJob.module.css"
import CommonHeader from "../../../CommonHeader/CommomHeader"
import axios from "axios"
import PageTitle from "../../../../PageTitle";
import Alert from "../../../../Toast/Toast"
import { useHistory } from "react-router-dom";
import Button from '@material-ui/core/Button';
import {setProfile} from '../../../../../store/actions/profile';
import { connect } from "react-redux"


const Applyjob = (props) => {
    const [isInvalid, setIsInvalid] = useState(false)
    const [message, setMessage] = useState("")
    const [isSuccess, setIsSuccess] = useState(false)
    const [start, setStart] = useState(false)

    const [resume, setResume] = useState({
        val: "",
        touched: false,
        isValid: false
    })

    const history = useHistory();
    const [isValid, setIsValid] = useState(true);

    const back = ()=>{
        history.push("/teacher/jobs");
    }
    useEffect(() => {
        // setIsInvalid(true)

        if(!props.profile) {
            history.push("/teacher/profile")
        }
        //console.log(localStorage.getItem("job_id"))
        //console.log(localStorage.getItem("id"))
        //console.log(localStorage.getItem("inst_id"))
    })
    
    const onFileChange = (event) => {
        let value = event.target.files[0]
        setResume({
            ...resume,
            isValid: "true",
            val: value
        })
    }

    const onApplyJob = () => {
        if(!resume.val) {
            setResume({
                ...resume,
                touched: true,
                isValid: false
            })

            return
        }

        let id = localStorage.getItem("id")
        let data = new FormData()
        data.append("job_id", localStorage.getItem("job_id"))
        data.append("user_id", localStorage.getItem("id"))
        data.append("institution_id", localStorage.getItem("inst_id"))
        data.append("institution_name", localStorage.getItem("name"))
        data.append("applicant_name", localStorage.getItem("applicant_name"))
        data.append("job_title", localStorage.getItem("job_title"))
        data.append("email", localStorage.getItem("email"))
        data.append("item_img", resume.val)
        data.append("profile", JSON.stringify(props.profile))
        setStart(true)
        axios.post("https://campusfield.in/api/teachers/apply_job", data)
        .then((res) => {
            //console.log(res)
            history.push({
                pathname: "/teacher/jobs",
                state: {
                updateMessage: "Applied successfully"
                }
            })
            // alert("applied successfully")
            // back()
        })
        .catch((err) => {
            setMessage("Something went wrong!")
            setIsSuccess(false)
            setStart(false)
            setTimeout(() => {
                setMessage("")
              }, 3000)
            //console.log(err)
            //console.log({...err})
        })    
    }

    return (
    <>
    <PageTitle title="Jobs / Apply" button="Back" add_new={back}  />
        <div className={styles["sub-container"]}>
            <div className={styles["input-container"]}>
                <label style={{color: !resume.isValid && resume.touched ? "red": "grey"}}>
                    Resume (.pdf)*
                </label>
                <input
                onChange={(event) => {onFileChange(event)}} 
                type="file"
                accept=".pdf"/>
            </div>
            <div className={styles["button-container"]}>
            <Button
                    disabled={start}
                    style={{marginRight: "0.9rem"}}
                    variant="contained"
                    color="primary"
                    onClick={onApplyJob}
                >
                    Submit
                </Button>
                <Button
                    variant="contained"
                    color="secondary"
                >
                    Cancel
                </Button>
            </div>
        </div>
        {
        message ? <Alert message={message} color={isSuccess ? "green" : "red"}/> : null
      }
    </>
  );
}

const mapStateToProps = (state) => {
    //console.log(state)
    return {
      profile: state.profile.profile,
    }
}

export default connect(mapStateToProps, null)(Applyjob);
