export default function Questions() {
  const [data, setData] = useState([])

  useEffect(() => {
    getDetails()
  }, []);

  const getDetails = () =>{
    axios.get("https://campusfield.in/site_management/list_of_institution")
    .then(res => {
      if (res.data) {
        if (res.data.error_code) {
          // //console.log(res.data.output, 'dat')
          setData(res.data.output)
        }
      }
    })
  }
  return (
    <>
      <PageTitle title="Questions" />
      <Grid container spacing={4}>
        <Grid item xs={12}>
          <MUIDataTable
            title="Questions List"
            data={data}
            columns={columns}
            options={{
              filterType: "checkbox",
            }}
          />
        </Grid>

      </Grid>
    </>
  );
}
