import React, { useEffect, useState } from 'react';
import styles from "./AppliedJobs.module.css"
import Availablejob from '../AvailableJob/AvailableJob';
import axios from "axios"
import PageTitle from "../../../PageTitle";
import Alert from "../../../Toast/Toast"
import EmptyList from "../../../UI/EmptyList/EmptyList"


const Appliedjobs = (props) => {

  const [jobs, setJobs] = useState([])
  let jobList;
  const [profileStrength, setProfileStrength] = useState(0);
  let updatemessage = props.location.state ? props.location.state.updateMessage : ""

  if(jobs.length) {
    jobList = jobs.map((el) => {
      //console.log(el)
      return (
        <Availablejob
        appliedJobs={true}
        id={el._id}
        isSubscribedUser={false}
        title={el.job_title}
        inst_name={el.institution_name}
        instId={el.institution_id}
        experience={el.required_exp}
        qualification={el.Qualification}
        vacancy={el.Number_of_vacancy}
        lastDate={el.date}
        />
      )
    })
  }

  useEffect(() => {
    if(jobs.length === 0) {
      axios.get("https://campusfield.in/api/job/getAppliedJob/" + localStorage.getItem('teacher_id'))
      .then((res) => {
        //console.log(res)
        setJobs(res.data.output)
      })
      .catch((err) => {
        //console.log(err)
      })
    }

    let strength = localStorage.getItem('profileStrength')
    if(strength) {
      setProfileStrength(strength)
    }

  }, [])

  return (
    <>
        {/* <div className={styles["title"]}>
          DASHBOARD / JOBS 
        </div> */}
       <PageTitle title="Applied Jobs" />
        <div className={[styles["sub-container"],jobList && jobList[0] !== undefined ? null: styles["empty"]].join(" ")}>
            {
              profileStrength < 80 ? <EmptyList message="Your profile strength must be atleast 80% to apply job"/> :
            
              jobList && jobList.length ? jobList : 
              <EmptyList message="No Jobs Applied!"/>}
        </div>
        {updatemessage  ? <Alert message={updatemessage} color="green"/>: null}

    </>
  );
}

export default Appliedjobs;
