import React, { useEffect, useState } from 'react';
import styles from "./Dashboard.module.css"
import Profile from "./Profile/Profile"
import PageTitle from "../../PageTitle";
import { useHistory, withRouter } from "react-router-dom";
import Alert from "../../Toast/Toast"
import axios from "axios"
import {getTeacher} from "../../../MockService/Teacher"
import * as constants from "../constants/constants"
import {connect} from "react-redux"
import {setProfile} from '../../../store/actions/profile';


const Dashboard = (props) => {
  let state =  props.location ? props.location.state : ""
  let message = state ? state.message : ""
  let updatemessage = props.location.state ? props.location.state.updateMessage : ""
  const history = useHistory();
  const [data, setData] = useState(null)
  
  useEffect(() => {
    let id = localStorage.getItem("teacher_id")

    if(data == null) {
      //console.log(props.profile)
      axios.get(constants.url + "teachers/dashboard/" + id)
      .then((res) => {
          //console.log(res)
          if(res.data.error_code === 1) {
            setData(res.data.output[0]) 
            props.onSetProfile(res.data.output[0])
            localStorage.setItem('email', res.data.output[0].Email)
            localStorage.setItem("type", res.data.output[0].type)
            localStorage.setItem('phone_number', res.data.output[0].Phone_Number)
            localStorage.setItem('applicant_name' , res.data.output[0].First_name + ' ' + res.data.output[0].Last_name)
          }
      })
      .catch((err) => {
          //console.log(err)
      })
    }
    }, [])

  const move_to_edit = () =>{
    history.push("/teacher/profile/edit")
  }
  
  return (
      <>
       <PageTitle title="Profile" button="Edit" add_new={move_to_edit}  />
      
        <Profile {...props} data={data}/>
        {
          message ? <Alert message={message}/> : null 
        }
        {updatemessage  ? <Alert message={updatemessage} color="green"/>: null}

      </>
  );
}

const mapStateToProps = (state) => {
  //console.log(state)
  return {
    profile: state.profile.profile
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    onSetProfile: (profile) => {
      //console.log("heeeeeeeeeeeeeeeeeeeeeeee")
      //console.log(profile)
      dispatch(setProfile(profile))}
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Dashboard));
