import React, { useState } from 'react';
import styles from "./add_new.module.css"
import CommonHeader from "../../../Teacher/CommonHeader/CommomHeader";
import * as constants from "../../constants/constants"
import axios from "axios"
import PageTitle from "../../../PageTitle";
import { useHistory } from "react-router-dom";
import Alert from "../../../Toast/Toast"
import Button from '@material-ui/core/Button';

const Add_new_achievement = (props) => {
    let isState = props.location.state
    const [message, setMessage] = useState("")
    let isEdit = props.match.url.search("edit") != -1
    const [isSuccess, setIsSuccess] = useState(false)
    const history = useHistory();


    const back = () =>{
        history.push("/institution/achievement")
      }
  
      const [title, setTitle] = useState({
          value: isEdit && isState ? props.location.state.title : "",
          isValid: isEdit && isState ? true : false,
          touched: false
      })
      const [year, setYear] = useState({
        value: isEdit && isState ? props.location.state.year : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })
      const [description, setDescription] = useState({
          value: isEdit && isState ? props.location.state.description : "",
          isValid: isEdit && isState ? true : false,
          touched: false
      })

    if(isEdit && !props.history.location.state) {
        history.replace("/institution/achievement")
    }

    const validateForm = () => {
        return title.isValid && description.isValid && year.isValid
    }

    const validate = (val, i) => {
        if(i==2) {
            return val.length && val.length==4
        }
        return val.length
    } 

    const setTouchedTrue = () => {
        setTitle({
            ...title,
            touched: true
        })
        setYear({
            ...year,
            touched: true
        })
        setDescription({
            ...description,
            touched: true
        })
    }
   
    const onAddAchievement = () => {
        let isValid = validateForm()
        setTouchedTrue()
        
        if(isValid) {
            let id = localStorage.getItem("id")
            axios.post(constants.url + "institution/Achievement/" + id + "/nill/2", {
                "Achievement":{
                    "title": title.value,
                    "description": description.value,
                    "year": year.value
                }
            })
            .then((res) => {
                history.push({
                    pathname: "/institution/achievement",
                    state: {
                    updateMessage: "added successfully"
                    }
                })
                // alert("certificate added successfully")
            }).catch((err) => {
                setMessage("something went wrong!")
                setIsSuccess(false)
                setTimeout(() => {
                    setMessage("")
                  }, 3000)
            })
        }
    } 

    const onUpdateAchievement = () => {
        let isValid = validateForm()
        setTouchedTrue()
        let id = localStorage.getItem("id")
        let achievementId = isState && isEdit ? props.location.state.id : "nill"
        if(isValid) {
            axios.post(constants.url + "institution/Achievement/" + id + `/${achievementId}/1`, {
                "Achievement":{
                    "title": title.value,
                    "description": description.value,
                    "year": year.value
                }
            })
            .then((res) => {
                //console.log(res)
                history.push({
                    pathname: "/institution/achievement",
                    state: {
                    updateMessage: "updated successfully"
                    }
                })
                clearFields()
            }).catch((err) => {
                setMessage("something went wrong!")
                setIsSuccess(false)
                setTimeout(() => {
                    setMessage("")
                  }, 3000)
            })
        }
    } 

    const clearFields = () => {
        setDescription({
            value: "",
            isValid: false,
            touched: false
        })
        setTitle({
            value: "",
            isValid: false,
            touched: false
        })
        setYear({
            value: "",
            isValid: false,
            touched: false
        })
    }

    const onChangeTexthandler = (event, i) => {
        let value = event.target.value

        if(i === 1) {
            setTitle({
                ...title,
                value: value,
                isValid: validate(value, 1)
            })
        }

        if(i === 2) {
            setYear({
                ...year,
                value: value,
                isValid: validate(value, 2)
            })
        }

        if(i === 3) {
            setDescription({
                ...description,
                value: value,
                isValid: validate(value, 3)
            })
        }
    }

    return (
        <>

    <PageTitle title="Add Achievement" button="Back" add_new={back} />

          <div className={styles["sub-container"]}>
         
                <form className={styles["post-job-form"]}>
                    <div className={styles["input-container"]}>
                    <label style={{color: title.touched && !title.isValid ? "red" : "grey" }}>
                        Title *
                    </label>
                    <input
                    value={title.value}
                    onBlur={() => {setTitle({
                        ...title,
                        touched: true
                    })}}
                    onChange={(event) => { onChangeTexthandler(event, 1)} }
                    />
                    </div>
                    <div className={styles["input-container"]}>
                        <label style={{color: year.touched && !year.isValid ? "red" : "grey" }}>
                            Year *
                        </label>
                        <input
                        maxlength={4}
                        onBlur={() => {setYear({
                            ...year,
                            touched: true
                        })}}
                        type="number"
                        value={year.value}
                        onChange={(event) => { onChangeTexthandler(event, 2)} }/>
                    </div>
                    <div className={styles["description"]}>
                    <label style={{color: description.touched && !description.isValid ? "red" : "grey" }}>
                            Description *
                        </label>
                        <textarea
                        onBlur={() => {setDescription({
                            ...description,
                            touched: true
                        })}}
                        value={description.value}
                        onChange={(event) => { onChangeTexthandler(event, 3)}}/>
                    </div>
                </form>
                <div className={styles["button-container"]}>
                <Button
                    style={{marginRight: "0.9rem"}}
                    variant="contained"
                    color="primary"
                    onClick={isEdit ? onUpdateAchievement : onAddAchievement}
                >
                    {isEdit ? "Update" : "Create"}
                </Button>
                <Button
                    variant="contained"
                    color="secondary"
                    onClick={() => {
                        if(isEdit) {
                            history.goBack()
                        } else {
                            clearFields()}}}
                >
                    Cancel
                </Button>
            </div>
            {
        message ? <Alert message={message} color={isSuccess ? "green" : "red"}/> : null
      }
          </div>
          {
        // isInvalid ? <Alert message="Invalid Credintials!" color="red"/> : null
      }
        </>
  );
}

export default Add_new_achievement;
