import React, { useEffect, useState} from 'react';
import styles from "./TrainingCenter.module.css"
import { Grid } from "@material-ui/core";
import MUIDataTable from "mui-datatables";
import PageTitle from "../../PageTitle";
import { useHistory, withRouter } from "react-router-dom";
import axios from "axios"

const TrainingCenter = (props) => {

    const history = useHistory()
    const [dataMap, setDataMap]  = useState([])

    let column =  [
      {
        name: "Logo",
        label:'Logo',
        options: {
          filter: true,
          customBodyRender: (value, meta, updatevalue) => {
            let initial = '';
            let name = meta.rowData[1]
            
            if(name) {
              let splittedName = name.split(" ")
              //console.log(splittedName)
              initial = initial + splittedName[0][0] 
            }

            return <div style={{
              backgroundImage: `url(${value})`,
              borderRadius: "50%",
              height: "40px",
              backgroundColor: "#0664b5",
              display: "flex",
              alignItems: 'center',
              justifyContent: 'center',
              width: "40px",
            backgroundSize: "cover"}}>{!value ? <p style={{margin: 0, color: "white", textTransform: "uppercase"}}>{initial}</p> : null}</div>
          }
       }},{
          name: "name",
          label:'Name',
          options: {
            filter: true,
          }
        },
        {
          name: "About",
          label:'About',
          options: {
            filter: false,
          }
        },
        {
          name: "State",
          label:'Location',
          options: {
            filter: true,
            customBodyRender: (value, tableMeta) => {
              return <p>
                {value ? value : dataMap[tableMeta.rowIndex].location}
              </p>
            }
          }
        }
    ]
    
    useEffect(() => {
      if(dataMap.length == 0) {
        axios.get('https://campusfield.in/api/training/training_center')
        .then(res => {
          if(res.data.error_code == 1 && res.data.output.length) {
            setDataMap(res.data.output)
          }
        })
        .catch(err => {
          //console.log(err)
        })
      }
    }, [])

    return (
    <div id="">
    <Grid container spacing={4}>
        <Grid item xs={12}>
          <MUIDataTable
            title="Training Centre"
            data={dataMap}
            columns={column}
            options={{  
              selectableRows: false,
              search: false,
              filter: true,
              selectableRowsOnClick: false,
              onRowsSelect: (a, b) => {
                b.map((el) => {
                  let tempList = [];
                })
              },
            //   onRowClick: (rowData, rowState) => {
            //     let index = rowState.rowIndex
            //     let id = dataMap[index]._id
            //     let domain = window.location.href.search('teacher') == -1? 'institution' : 'teacher'
            //     props.history.push({
            //       "pathname": `/${domain}/centre/${id}`,
            //       "data": {
            //         data: dataMap[index]
            //       }
            //     })              
            // }
          }
          }
          />
        </Grid>
    </Grid>
  </div>
)

}
export default withRouter(TrainingCenter);
