import React, {useEffect, useState} from 'react';
import styles from "./TieUps.module.css"
import PageTitle from "../../PageTitle";
import { useHistory } from "react-router-dom";
import axios from "axios"
import * as constants from "../constants/constants"
import Alert from "../../Toast/Toast"
import TieUp from "./TieUp/Tieup"
import EmptyList from "../../UI/EmptyList/EmptyList"

const TieUps = (props) => {
    let updatemessage = props.location.state ? props.location.state.updateMessage : ""
    const history = useHistory();
    const [data, setData] = useState("")
    const [message, setMessage] = useState("")
    let tieUps;

    const onDeleteTieUp = (tieUpId) => {
      let id = localStorage.getItem("id")
  
      axios.post(constants.url + "institution/TieuPs/" + id + `/${tieUpId}/0`, {
        "TieuPs":{
            "id": tieUpId,
        }
      })
      .then((res) => {
          if(res.data.msg.search("delete") != -1) {
            getTieUps()
            setMessage("deleted successfully")
          }
      }).catch((err) => {
          setMessage("network error!")
          setTimeout(() => {
              setMessage("")
            }, 3000)
          //console.log(err)
      })
  }

    let id = localStorage.getItem("id")
    useEffect(() => {
      //console.log(data)
      if(data.length === 0) {
        getTieUps()
      }
      })

      const getTieUps = () => {
        axios.get(constants.url + "institution/dashboard/" + id)
        .then((res) => {
            if(res.data.error_code === 1) {
              //console.log(res.data.output[0])
              setData(res.data.output[0]) 
            }
        })
        .catch((err) => {
            //console.log(err)
        })
      }

    if(data.TieuPs && data.TieuPs.length) {
      //console.log(data)
      tieUps = data.TieuPs.map((el, i) => {
        //console.log({...el})
        return (
          <TieUp
          onDelete={onDeleteTieUp}
          id={el.id}
          key={i}
          title={el.title}
          description={el.description}
          />
        )
      })   
    }

    const move_to_edit = () => {
      history.push("/institution/tie_up/new")
    }

  return (
    <>
    <PageTitle title="Dashboard / Tie-ups" button="Add" add_new={move_to_edit} />
        <div className={styles["sub-container"]}>
        {tieUps && tieUps.length ? tieUps : <EmptyList message="No Tie-ups added!"/>}
        </div>
        {message ? <Alert message={message} color={message.search("network") != -1 ? "red" : "green"}/> : null}
        {updatemessage  ? <Alert message={updatemessage} color="green"/>: null}
    </>
  );
}

export default TieUps;
