import React from "react"
import styles from "./Syllabus.module.css"
import * as constants from "../../../constants/constants"
import { NavLink } from "react-router-dom"

import syllabus1 from "../../../../../assets/syllabus1.docx"
import syllabus2 from "../../../../../assets/syllabus2.docx"

const Syllabus = (props) => {

    let backGround = props.index % 2 === 0 ? styles["even-colour"] : styles["odd-colour"]

    return (
        <div className={[styles["syllabus-container"], backGround].join(" ")}>
            <h5>
                {props.title}
            </h5>
            <p>
                {props.brief}
            </p>
            <div style={{ justifyContent: 'space-between' }} className={styles["link"]}>
                <NavLink to="/exams/about-ctet">
                    {constants.EXAM.PAPERS.LINK}
                </NavLink>
                
                {
                    (props.title=="SYLLABUS -I" || props.title=="SYLLABUS -II")?
                    <button style={{ marginLeft: 60 }}>
                        <a style={{ color:'#000' }} href={ ((props.title=="SYLLABUS -I")?syllabus1:syllabus2) } download={props.title+".docx"}>Download</a>
                    </button> : null
                }   
            </div>
        </div>
    )
    
}

export default Syllabus
