import React, { useEffect, useState } from 'react';
import styles from "./JobResponse.module.css"
import axios from "axios"
import PageTitle from "../../../PageTitle";
import { connect } from "react-redux"
// import Alert from "../../Toast/Toast"
import EmptyList from "../../../UI/EmptyList/EmptyList"
import {getJobResponse} from  "../../../../MockService/Teacher"
import Response from "./Response/Response"
import {setJobResponseCountToZero} from '../../../../store/actions/jobResponse';
import jobResponse from '../../../../store/reducers/jobResponse';

const JobResponse = (props) => {

  const [jobs, setJobs] = useState([])
  let jobList;
  let updatemessage = props.location.state ? props.location.state.updateMessage : ""

  useEffect(() => {
    let id = localStorage.getItem("teacher_id")
    //console.log(props.jobResponse)
    axios.get("https://campusfield.in/api/job/update_view_Status/" + id)
    .then(res => {
      props.onSetJobResponseCount()
    })
    .catch(err => {
      //console.log(err)
    })
  }, [])

  if(props.jobResponse.length) {
    jobList = props.jobResponse.map((el) => {
      return (
        <Response
        jobTitle={el.job_title}
        instituitonId={el.institution_id}
        applicantName={el.applicant_name}
        result={el.select_status}
        jobTitle={el.job_title}
        interview_date={el.interview_date}
        institutionName={el.institution_name}
        date={el.date}
        city={el.details ? el.details[0].location : ""}
        date={el.details ? el.details[0].date: ""}
        instruction={el.details ? el.details[0].instruction : ""}
        phone={el.details ? el.details[0].phone : ""}
        isViewed={el.isViewed}
        contact={el.contact}
        />
      )
    })
  }

  return (
    <>
        {/* <div className={styles["title"]}>
          DASHBOARD / JOBS 
        </div> */}
       <PageTitle title="Job Response" />
        <div className={styles["sub-container"]}>
            {/* <CommonHeader
            /> */}
              {jobList && jobList.length ? jobList :<EmptyList message="No Response to show!"/>}
        </div>

    </>
  );
}


const mapStateToProps = (state) => {
  //console.log(state)
  return {
    responseToView: state.jobResponse.responseToView,
    jobResponse: state.jobResponse.jobResponse
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    onSetJobResponseCount: () => dispatch(setJobResponseCountToZero())
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(JobResponse);
