import React, { useEffect, useState} from 'react';
import styles from "../Teacher/TrainingPrograms/TrainingPrograms.module.css"
import AdminPaymentCard from './AdminPaymentCard';
import { Grid } from "@material-ui/core";
import MUIDataTable from "mui-datatables";
import PageTitle from "../PageTitle";
import { useHistory, withRouter } from "react-router-dom";
import axios from 'axios';
import Alert from "../Toast/Toast"


let tempList = []

const AdminPayments = (props) => {

    const history = useHistory()
    const [programmeList, setProgrammeList] = useState([])
    const [tempProgrammeList, setTempProgrammeList] = useState([])
    const [selectedProgrammes, setSelectedProgrammes] = useState([])
    const [enrolled, setEnrolled] = useState([])
    const [reload, setReload] = useState({})
    const [message, setMessage] = useState('')
    const [isViewCart, setIsViewCart] = useState(false)
    const [isEnroll, setIsEnroll] = useState(false)
    const [inCartIds, setInCartIds] = useState([])

    let column =  [
        {
          name: "name",
          options: {
            filter: true,
          }
        },{
            name: "email",
            options: {
                filter: true,
            }
        },
        {
          name: "phoneNumber",
          options: {
            filter: true,
          }
        },{
          name: "type",
          options: {
                filter: true,
              }
        },
        {
          name: "amount",
          options: {
            filter: true,
          }
        },{
            name: "programmes",
            options: {
                filter: true,
              }
        },
        {
            name: "createDate",
            options: {
                filter: true,
              }
        }
    ]

    const updateOrders = () => {
        axios
        .get('https://campusfield.in/api/order')
        .then(res => {
            if (res.data.error_code === 1) {
                setProgrammeList(res.data.output)
            }
        })
        .catch(err => {

        })
    }
    useEffect(() => {
      if(programmeList.length == 0) {
        updateOrders()
      }
    }, [])
    
    return (
    <div
    style={localStorage.getItem('id') ? {} : {width: "95%", margin: "auto", paddingTop: "9%"}} 
    id="programs">

    <PageTitle title={`Transactions`}/>
    
    <Grid container spacing={4}>
        <Grid item xs={12}>
          <MUIDataTable
            title="Transactions"
            data={programmeList}
            columns={column}
            options={{  
              selectableRows: true,
              customRowRender: (data, dataIndex, rowIndex) =>{
                return (
                    <AdminPaymentCard
                    name={data[0]}  
                    email={data[1]}  
                    phoneNumber={data[2]}  
                    type={data[3]}  
                    amount={data[4]}  
                    programmes={data[5]}
                    createDate={data[6]}
                    />
                )
            },
              selectableRowsOnClick: false,
              filter: true,
            }}
          />
        </Grid>
        {props.location.state ? <Alert message={props.location.state.message} color="green"/> : null}        
    </Grid>
    {message ? <Alert message={message} color={message.search("network") != -1 ? "red" : "green"}/> : null}        

    </div>
)
}

export default withRouter(AdminPayments);
