import React, { useEffect, useState } from 'react';
import styles from "../TrainingProgrammes/TrainingProgrammes.module.css"
import { NavLink, useHistory, withRouter } from "react-router-dom" 
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import DeleteIcon from '@material-ui/icons/Delete';
import Create from '@material-ui/icons/Create';
import axios from "axios"
import Alert from "../Toast/Toast"
import { PinDropRounded } from '@material-ui/icons';

const AdminPaymentCard = (props) => {
    const [message, setMessage] = useState("")
    const [teachersCount, setTeachersCount] = useState(0)
    const [isExpand, setIsExpand] = useState(false)
    const history = useHistory()
    const useStyles = makeStyles((theme) => ({
        button: {
          margin: theme.spacing(1),
        },
      }));
    const classes = useStyles();

    useEffect(() => {
        if (props.type === 'institution') {
            let count = 0
            for(let d of props.programmes) {
                count += d.teachers.length
            } 
            setTeachersCount(count)
        }
    }, [])

    return (
      <div className={styles["experiences-container"]}>
          <div className={styles["experience-container"]}>
              <div className={styles["title-container"]}>
                <h4>
                  {props.name}
                </h4>
                <p onClick={() => {setIsExpand(!isExpand)}}>
                    {isExpand ? '-' : '+'}
                </p>
              </div>
            <div
            style={{display: isExpand ? "block": "none"}} 
            className={styles["detail"]}>
              <div className={styles["detail-container"]}>
                  <i class="fa fa-address-card" aria-hidden={true}/>
                  <h3>
                    <b>Email</b> : <span>{props.email}</span>
                  </h3>
              </div>
              <div className={styles["detail-container"]}>
                  <i class="fa fa-phone"  aria-hidden={true}/>
                  <h3>
                      <b>Phone number</b> : <span>{props.phoneNumber}</span>
                  </h3>
              </div>
              <div className={styles["detail-container"]}>
                  <i class="fa fa-user"  aria-hidden={true}/>
                  <h3>
                      <b>Type</b> : <span>{props.type}</span>
                  </h3>
              </div>
              <div className={styles["detail-container"]}>
                  <i class="fa fa-money"  aria-hidden={true}/>
                  <h3>
                      <b>Total Amount</b> : <span>{props.amount  ? props.amount : 0}</span>
                  </h3>
              </div>
              <div className={styles["detail-container"]}>
                  <i class="fa fa-university"  aria-hidden={true}/>
                  <h3>
                      <b>No of programmes</b> : <span>{props.programmes?.length}</span>
                  </h3>
              </div>
              <div className={styles["detail-container"]}>
                  <i class="fa fa-clock-o"  aria-hidden={true}/>
                  <h3>
                      <b>Transaction Date</b> : <span>{props.createDate.split('T')[0]}</span>
                  </h3>
              </div> 
              {
                  props.type === 'institution' ? 
                  <div className={styles["detail-container"]}>
                  <i class="fa fa-users"  aria-hidden={true}/>
                  <h3>
                      <b>No of Teachers</b> : <span>{teachersCount}</span>
                  </h3>
              </div> : null
              }            
              </div>
            </div>
          </div>
    );
}   

export default withRouter(AdminPaymentCard);
