import React, { useEffect, useState } from "react"
import {useHistory} from "react-router"
import styles from "./Dashboard.module.css"
import Info from "./Info/Info"
import PageTitle from "../../PageTitle";
import axios from "axios"
import * as constants from "../constants/constants"
import TieUp from "../TIE-UPS/TieUp/Tieup";
import Facility from "../FACILITIES/Facilities/Facility/Facility"
import Achievement from "../ACHIEVEMENTS/Achievement/Achievement"
import Placement from "../PLACEMENTS/Placement/Placement"
import EmptyList from "../../UI/EmptyList/EmptyList";
import UserPreference from '../../Home/UserPreference/UserPreference'

const Dashboard = (props) => {

    const [data, setData] = useState([])

    let facilities;
    let achievement;
    let tieUps;
    let placement
    const [isUserPreference, setUserPreference] = useState(false)
    
    if(data && data.Facilities && data.Facilities.length) {
      facilities = data.Facilities.map((el) => {
        //console.log(el)
        return (
          <Facility
          isNotEdit={true}
          id={el.id}
          title={el.title}
          description={el.description}
          />
        )
      })   
    }

    if(data && data.Achievement && data.Achievement.length) {
        achievement = data.Achievement.map((el) => {
          //console.log(el)
          return (
            <Achievement
            isNotEdit={true}
            id={el.id}
            title={el.title}
            year={el.year}
            description={el.description}
            />
          )
        })   
      }

    if(data && data.Placement && data.Placement.length) {
        placement = data.Placement.map((el) => {
          //console.log(el)
          return (
            <Placement
            isNotEdit={true}
            id={el.id}
            title={el.title}
            description={el.description}
            />
          )
        })   
    }

    if(data && data.TieuPs && data.TieuPs.length) {
        tieUps = data.TieuPs.map((el) => {
          return (
            <TieUp
            isNotEdit={true}
            id={el.id}
            title={el.title}
            description={el.description}
            />
          )
        })   
      }


    let id = localStorage.getItem("id")
    useEffect(() => {
      //console.log(props)
      if(data.length === 0) {
        axios.get(constants.url + "institution/dashboard/" + id)
        .then((res) => {
            //console.log(res.data)
            if(res.data.error_code === 1) {
              setData(res.data.output[0]) 
              localStorage.setItem("inst_name", res.data.output[0].Name)
              localStorage.setItem("email", res.data.output[0].Email)
              localStorage.setItem("phone_number", res.data.output[0].Phone_Number)
              localStorage.setItem("type", res.data.output[0].type)
              
            }
        })
        .catch((err) => {
            //console.log(err)
        })
      }
      })

    let history = useHistory()

    const onEdit = () => {
      history.push("/institution/profile/edit")
    }

    return(
        <>
            {
            isUserPreference ?
            <UserPreference
            About={data.About}
            Location={data.Location}
            isNotDesignation={true}
            isUserPreference={true} 
            isInstitution={true}
            setUserPreference={setUserPreference} max={1}/> : null
            }
            <PageTitle title="Profile" button="Edit" add_new={onEdit}/>
            <div className={styles["sub-container"]}>
            <div
        onClick={() => { setUserPreference(!isUserPreference)}} 
        className={styles['preference']}>
          <i style={{color: "white", fontSize: "1.1rem"}} className="fa fa-cog"/>
        </div>
                <Info 
                data={data}/>
                <div className={styles["profile-container"]}>
                    <p>
                        Facilities
                    </p>
                    {facilities && facilities.length ? facilities : <EmptyList message="No Facilities added to list!"/>}
                    
                </div>
                <div className={styles["profile-container"]}>
                    <p>
                        Achievements
                    </p>
                    {achievement && achievement.length ? achievement : <EmptyList message="No achievements added to list!"/>}
                        
                </div>
                <div className={styles["profile-container"]}>
                    <p>
                        Placements
                    </p>
                    {placement && placement.length ? placement : <EmptyList message="No Placements added to list!"/>}

                </div>
                <div className={styles["profile-container"]}>
                <p>
                    Tie-ups
                </p>
                {tieUps && tieUps.length ? tieUps : <EmptyList message="No Tie-ups added!"/>}
                </div>
            </div>
        </>
    )

}

export default Dashboard
