import React, { useEffect, useState } from 'react';
import styles from "./AvailableJobs.module.css"
import Availablejob from './AvailableJob/AvailableJob';
import CommonHeader from "../CommonHeader/CommomHeader"
import axios from "axios"
import PageTitle from "../../PageTitle";
import Alert from "../../Toast/Toast"
import EmptyList from "../../UI/EmptyList/EmptyList"
import { Grid } from "@material-ui/core";
import MUIDataTable from "mui-datatables";
import "../../App.css"


const Availablejobs = (props) => {

  const [jobs, setJobs] = useState([])
  let jobList;
  const [profileStrength, setProfileStrength] = useState(0);
  let updatemessage = props.location.state ? props.location.state.updateMessage : ""
  let column =  [
    {
      name: "_id",
    },
      {
        name: "title",
        options: {
          filter: true,
        }
      },{
          name: "status",
          options: {
              filter: true,
          }
      },
      {
        name: "required_exp",
        options: {
          filter: true,
        }
      },{
          name: "Qualification",
          options: {
              filter: true,
          }
      },
      {
        name: "Number_of_vacancy",
        options: {
          filter: true,
        }
      },{
          name: "Last_Date",
          options: {
              filter: true,
          }
      },
      {
        name: "Description",
        options: {
            filter: true,
        }
    },
    {
      name: "institution_id",
      options: {
          filter: true,
      }
  },
  {
    name: "institution_name",
    options: {
        filter: true,
    }
}
  ]

  useEffect(() => {
    //console.log(localStorage.getItem('profileStrength'))
    if(jobs.length === 0) {
      axios.get("https://campusfield.in/api/institution/job_list_filter/" + localStorage.getItem('teacher_id'))
      .then((res) => {
        if(res.data.error_code === 1) {

          let jobsArray = res.data.output.map(el => {
            let dueDate = new Date(el.Last_Date.split('T')[0])
            let date = new Date()
            let d = date.getDate()
            let m = date.getMonth()
            let y = date.getFullYear()
            let today = new Date(`${m + 1}-${d}-${y}`)
            if(dueDate.getTime() > today.getTime()) {
              return el
            }

          })  
          
          jobsArray = jobsArray.filter(el => {
            if(el !== undefined) {
              return el
            }
          })
          setJobs(jobsArray)
        }

      })
      .catch((err) => {
        //console.log(err)
      })
    }

    let strength = localStorage.getItem('profileStrength')
    if(strength) {
      setProfileStrength(strength)
    }

  }, [])

  return (
    <>
        {/* <div className={styles["title"]}>
          DASHBOARD / JOBS 
        </div> */}
       {/* <PageTitle title="Jobs" />  */}
        <div className={[styles["sub-container"],jobList && jobList[0] !== undefined ? null: styles["empty"]].join(" ")}>
            {
              profileStrength < 80 ? <EmptyList message="Your profile strength must be atleast 80% to apply job"/> :
            
              jobs && jobs.length ? 
    <div style={{width: "100%"}}>
    <div id="programs">
    <Grid container spacing={4}>
        <Grid item xs={12}>
          <MUIDataTable
            title="Jobs"
            data={jobs}
            columns={column}
            options={{  
              customRowRender: (data, dataIndex, rowIndex) =>{
                //console.log(data)
                return (
                  <Availablejob
                    id={data[0]}
                    title={data[1]}
                    experience={data[3]}
                    qualification={data[4]}
                    vacancy={data[5]}
                    lastDate={data[6]}
                    isSubscribedUser={false}
                    instId={data[8]}
                    inst_name={data[9]}
                    />
                )
              },
              selectableRows: false,
              selectableRowsOnClick: false,
              filter: true,
              onRowsSelect: (a, b) => {
                b.map((el) => {
                  let tempList = [];
                //   suggestedList.push(data[el.index]._id)
                  // se tSuggested(tempList)
                })
              },
            }}
          />
        </Grid>
        {props.location.state ? <Alert message={props.location.state.message} color="green"/> : null}        
    </Grid>
    </div>
    </div>: 
              <EmptyList message="No Jobs to show!"/>}
        </div>
        {updatemessage  ? <Alert message={updatemessage} color="green"/>: null}

    </>
  );
}

export default Availablejobs;
