import React, { useEffect, useState } from 'react';
import styles from "./Profile.module.css"
import {NavLink} from "react-router-dom" 
import BasicInfo from "./BasicInfo/BasicInfo"
import CommonHeader from "../../CommonHeader/CommomHeader";
import AcademicQualification from '../../AcademicQualifications/AcademicQualification/AcademicQualification';
import Experience from '../../Experiences/Experience/Experience';
import axios from "axios"
import * as constants from "../../constants/constants"
import { CircularProgressbar, buildStyles } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import Certification from "../../Certifications/Certification/Certification"
import EmptyList from "../../../UI/EmptyList/EmptyList"
import News from "../../News/News"
import { getNews } from '../../../../MockService/Teacher';
import Map from "../../../Map/Map"
import UserPreference from '../../../Home/UserPreference/UserPreference';

const Profile = (props) => {

  const [data, setData] = useState(null)
  // const [isUserPreference, setIsUserPreference] = useState(false)
  let profileStrength = 40

  useEffect(() => {
      setData(props.data)
  })

  let qualifications;
  let experiences;
  const [isUserPreference, setUserPreference] = useState(false)


  let certificates;

  const updateProfileStrength = (profileStrength) => {
    localStorage.setItem('profileStrength', profileStrength)
  }

  if (data && data.preferredDesignation) {
    profileStrength = profileStrength + 5
    updateProfileStrength(profileStrength)
  }

  if (data && data.preferredLocations) {
    profileStrength = profileStrength + 5
    updateProfileStrength(profileStrength)
  }

  if(data && data.Qualification && data.Qualification.length) {

    profileStrength = profileStrength + 25
    updateProfileStrength(profileStrength)
    qualifications = data.Qualification.map((el) => {
      
      if(typeof el === "string" && el.trim() === ""){
        return;
      } 
      
      //console.log({...el})
      
      return (
      
        <AcademicQualification
        isNotEdit={true}
        resume={el.data_path}
        college={el.college}
        qualification={el.qualification}
        passOut={el.passout}
        percentage={el.percentage}
        university={el.university}
        />
        )
    })   
  }

  
  if(data && data.Certification && data.Certification.length) {
    profileStrength = profileStrength + 10
    updateProfileStrength(profileStrength)

    certificates = data.Certification.map((el) => {
      if(typeof el === "string" && el.trim() === ""){
        return
      }
      return (
        <Certification
        isNotEdit={true}
        title={el.title}
        description={el.description}
        />
        )
      })   
    }

    if(data) {
      //console.log(data)
      // if(!data.isSkip) {
      //   setUserPreference(true)
      // }
    }

    if(data && data.Experience && data.Experience.length) {
      profileStrength = profileStrength + 25
      updateProfileStrength(profileStrength)

      experiences = data.Experience.map((el) => {
        if(typeof el === "string" && el.trim() === ""){
          return
      }

      return (
        <Experience
        isNotEdit={true}
        institution={el.institution}
        subject={el.subject}
        designation={el.designation}
        from={el.from}
        to={el.to}
        />
      )
    })   
  }

  return (
    <>
      {/* <UserPreference max={3}/> */}
    {
      isUserPreference ? 
      <UserPreference
      About={data.About}
      isFresher={data.isFresher}
      preferredDesignation={data.preferredDesignation}
      preferredLocations={data.preferredLocations} 
      setUserPreference={setUserPreference} 
      isUserPreference={isUserPreference} max={3}/> : null
    }
    {
      data ?
      <div className={styles["sub-container"]}>
        <div
        onClick={() => {setUserPreference(!isUserPreference)}} 
        className={styles['preference']}>
          <i style={{color: "white", fontSize: "1.1rem"}} className="fa fa-cog"/>
        </div>
        <div className={styles["basic-info-container"]}>
            <BasicInfo
            profilePic={data.Profile_pic}
            name={`${data.First_name} ${data.Last_name}`}
            address={`${data.Street}  ${data.city} ${data.District} ${data.State}`}
            phoneNumber={data.Phone_Number}/>
            <div className={styles["profile-strength-container"]}>
              <div style={{height: "70%", width: "70%"}}>
                <CircularProgressbar 
                className={styles["progressbarHeight"]} 
                value={profileStrength} 
                styles={buildStyles({
                  pathColor: '#0664b5',
                  textColor: "#0664b5"
                })}
                text={`${profileStrength}%`}/>
              </div>
            </div>
        </div>
        <div className={styles["about-me"]}>
          <p>
              About Me
          </p>
          <p>
            {data.About}
          </p>
        </div>
        <div className={styles["qualification"]}>
          <p>
              Academic Qualifications
          </p>
          {qualifications && qualifications[0] !== undefined ?  qualifications : 
          <EmptyList message="No qualifications added to show!"/>}
        </div>
        <div className={styles["qualification"]}>
          <p>
              Experience
          </p>
          {/* <Experience/> */}
          {experiences && experiences[0] !== undefined ? experiences : 
          <EmptyList
          message="No Experiences added to show!"/>}
        </div>
      </div> : null
    }
    </>
  );
}

export default Profile;
