import React, { useEffect } from "react"

import styles from "./Info.module.css"

const Info = (props) => {
    React.useEffect(() => {
        window.scrollTo(0, 0);
    }, [])


    useEffect(() => {
        //console.log(props.data.Logo == " ")
    })

    return(
        <div className={styles["info-container"]}>
            <div style={{width: "150px", 
                        backgroundColor: props.data?.Logo ? "transparent" : "#0664b5",
                        backgroundSize: "cover"}} className={styles["logo-container"]}>
            {
                typeof props.data?.Logo == 'string' && props.data?.Logo.trim() == "" ? <p className={styles["alt"]}>{props.data?.Name ? props.data?.Name[0] : null}</p>  :
                <img
                style={{borderRadius: "50%", height: "100%", width: "100%"}} 
                src={props.data?.Logo}
                alt="image not available"/>
            }
            </div>
            <div className={styles["basic-details-container"]}>
                <h3 style={{textTransform: "uppercase", fontSize: "1rem"}}>
                    {props.data.Name}   
                </h3>
                <div className={styles["basic-details-block"]}>
                    <i class="fa fa-map-marker" aria-hidden="true"/>
                    <p>
                        {props.data.Location}
                    </p>
                </div>
                <div className={styles["basic-details-block"]}>
                    <i class="fa fa-phone" aria-hidden="true"/>
                    <p>
                        {props.data.Phone_Number}
                    </p>
                </div>
                <div className={styles["basic-details-block"]}>
                    <i class="fa fa-globe" aria-hidden="true"/>
                    <p>
                        {props.data.Website}
                    </p>
                </div>
            </div>
            <div className={styles["about-container"]}>
                <h3>
                    About
                </h3>
                <p>
                    {props.data.About}
                </p>
            </div>
        </div>
    )

}

export default Info
