import React, { useEffect, useState } from 'react';
import styles from "./Programmes.module.css"
import CommonHeader from "../../Teacher/CommonHeader/CommomHeader"
import Postedjob from "../../Teacher/AvailableJobs/AvailableJob/AvailableJob"
import axios from "axios"
import * as constants from "../constants/constants"
import PageTitle from "../../PageTitle";

  import { useHistory, withRouter } from "react-router-dom";
import EmptyList from '../../UI/EmptyList/EmptyList';
import TrainingProgrammes from '../../TrainingProgrammes/TrainingProgrammes';
import { SettingsSystemDaydreamTwoTone } from '@material-ui/icons';

const Programmes = (props) => {
    const history = useHistory();

    const  [programmes, setProgrammes] = useState([]);

    // useEffect(() => {
    //     //console.log(props)
    // })

    let programmesList;


    if(programmes && programmes.length) {
        programmesList = programmes.map((el) => {
            return (
                <TrainingProgrammes
                title={el.title}
                date={el.date}
                amount={el.amount}
                fee={el.fee}
                city={el.city}
                duration={el.duration}
                description={el.description}
                />
            )
        })
    }

    useEffect(() => {
        if(programmes.length == 0) {
            setProgrammes([{
                title: "title",
                date: "date",
                fee: "fee",
                duration: "duration",
                description: "descriprion"
            }])
        }
    })

     return (
        <>
            <PageTitle title="Training Programmes"/>
            <div className={styles["sub-container"]}>
                {programmesList && programmesList.length ? programmesList : <EmptyList message="No programmes found!"/>}
            </div>
        </>
    );
}

export default  withRouter(Programmes);
