import React, { useEffect } from 'react';
import styles from "./Institution.module.css"
// import Header from "../Teacher/Header/Header";
import Header from "../Header";
import Sidebar from "../Sidebar_institution";
import NavBar from "../Teacher/NavBar/NavBar" ;
import { Route, Switch, useHistory } from "react-router-dom"
import * as constants from "./constants/constants"
import Postedjobs from "./PostedJobs/PostedJobs"
import Institutions from "./Institutions"
import PostJob from "./PostedJobs/PostJob/PostJob"
import Applicants from './PostedJobs/Applicants/Applicants';

import Add_new_facilities  from './FACILITIES/add_new/add_new' ;
// import Main_facilities  from './FACILITIES/main';
import Add_new_faculties  from './FACULTIES/add_new/add_new' ;
import Main_faculties  from './FACULTIES/main';
import Add_new_placement  from './PLACEMENTS/add_new/add_new' ;
import Main_placement  from './PLACEMENTS/main';
import Add_new_tie_up  from './TIE-UPS/add_new/add_new' ;
import Main_tie_up  from './TIE-UPS/main';
import Add_new_courses  from './COURSES/add_new/add_new' ;
import Main_courses  from './COURSES/main';
import Add_new_achievement  from './ACHIEVEMENTS/add_new/add_new' ;
import Main_achievement  from './ACHIEVEMENTS/main';     
import Dashboard from "./Dashboard/Dashboard"       
import Facilities from './FACILITIES/Facilities/Facilities';
import Placements from "./PLACEMENTS/Placements"
import Exams from "../Teacher/Exams/Exams"

import classnames from "classnames";
import useStyles from "./styles";
import { useLayoutState } from "../../context/LayoutContext";
import TieUps from './TIE-UPS/TieUps';
import Achievements from './ACHIEVEMENTS/Achievements';
import  Alert from '../Toast/Toast';
import Faculty from './Institutions/Faculty';
import EditProfile from './Dashboard/EditProfile/EditProfile';
import ExistingFaculties from "./FACULTIES/ExistingFaculties/ExistingFaculties"
import News from '../Teacher/News/News';
import Programmes from './Programmes/Progammes';
import TrainingCenter from '../Teacher/TrainingCenter/TrainingCenter';
import Testimonial from './Testimonial/Testimonial';
import ViewTrainingCenter from '../Teacher/TrainingCenter/ViewTrainingCenter/ViewTrainingCenter';
import TrainingPrograms from "../Teacher/TrainingPrograms/TrainingPrograms"
import Cart from "../Teacher/Cart/Cart"
import Order from "../Teacher/Order/Order"
import ViewTrainingPrograms from "../Teacher/TrainingPrograms/ViewProgramme/ViewProgramme"
import EnrollFaculties from './FACULTIES/EnrollFaculties/EnrollFaculties';
import EnrolledProgramme from './Programmes/EnrolledProgrammes/EnrolledProgrammes';

const Institution = (props) => {
    var classes = useStyles();
    var layoutState = useLayoutState();
    let history = useHistory()

    if(!localStorage.getItem("i_id")) {
        history.push("/sign-in")
    }

    return (
        <div className={classes.root}>
                <Header history={props.history} />
           <Sidebar />
          <div
            className={classnames(classes.content, {
              [classes.contentShift]: layoutState.isSidebarOpened,
            })}
          >
            <div className={classes.fakeToolbar} />
 
                <div className={styles["container"]}>
                    <Switch>
                        <Route path="/institution/jobs/new" component={PostJob}/>
                        <Route path="/institution/jobs/applicants" component={Applicants}/>
                        <Route path="/institution/jobs" component={Postedjobs}/>
                        <Route path="/institution/applications" component={Institutions}/>
                        <Route path="/institution/facilities/new" component={Add_new_facilities}/>
                        <Route path="/institution/facilities/edit" component={Add_new_facilities}/>
                        <Route path="/institution/facilities" component={Facilities}/>
                        <Route path="/institution/faculties/new" component={Add_new_faculties}/>
                        <Route path="/institution/faculties/edit" component={Add_new_faculties}/>
                        <Route path="/institution/faculties/existing" component={ExistingFaculties}/>
                        {/* <Route path="/institution/search/teacher" component={ExistingFaculties}/> */}
                        <Route path="/institution/faculties" component={Faculty}/>
                        {/* <Route path="/institution/faculties" component={Main_faculties}/> */}
                        <Route path="/institution/placements/new" component={Add_new_placement}/>
                        <Route path="/institution/placements/edit" component={Add_new_placement}/>
                        <Route path="/institution/payments" component={Exams}/>
                        <Route path="/institution/placements" component={Placements}/>
                        <Route path="/institution/tie_up/new" component={Add_new_tie_up}/>
                        <Route path="/institution/tie_up/edit" component={Add_new_tie_up}/>
                        <Route path="/institution/tie_up" component={TieUps}/>
                        <Route path="/institution/courses/new" component={Add_new_courses}/>
                        <Route path="/institution/courses" component={Main_courses}/>                    
                        <Route path="/institution/achievement/new" component={Add_new_achievement}/>
                        <Route path="/institution/achievement/edit" component={Add_new_achievement}/>
                        <Route path="/institution/achievement" component={Achievements}/>
                        <Route path="/institution/profile/edit" component={EditProfile}/>
                        <Route path="/institution/profile" component={Dashboard}/>
                        <Route path="/institution/programme/:name" component={ViewTrainingPrograms}/>
                        <Route path="/institution/programme" component={TrainingPrograms}/>
                        <Route path="/institution/enroll" component={EnrollFaculties} exact/>
                        <Route path="/institution/order" component={Order}/>
                        <Route path="/institution/enrolled_programme" component={EnrolledProgramme} exact/>
                        <Route path="/institution/centre/:id" component={ViewTrainingCenter}/>
                        <Route path="/institution/centre" component={TrainingCenter}/>
                        <Route path="/institution/cart" component={Cart}/>
                        <Route path="/institution/testimonial" component={Testimonial}/>
                        <Route path="/institution/dashboard" render={() => <News url=""/>}/>
                    </Switch>
                </div>
                <Alert message="Signed in successfully!" color="green"/>
            </div>
        </div>
    );
}

export default Institution;
