import React, { useEffect, useState} from 'react';
import { Grid } from "@material-ui/core";
import MUIDataTable from "mui-datatables";
import { useHistory, withRouter } from "react-router-dom";
import axios from 'axios';
import Alert from "../../../Toast/Toast"
import TrainingProgrammes from  "../../../TrainingProgrammes/TrainingProgrammes"

const EnrolledProgramme = (props) => {

    const history = useHistory()
    const [programmeList, setProgrammeList] = useState([])

    let column = [
      {
        name: "_id",
        label:'id',
      },
        {
          name: "title",
          label:'programme',
          options: {
            filter: true,
          }
        },{
            name: "city",
            label:'location',
            options: {
                filter: true,
            }
        },
        {
          name: "Description",
          label:'description',
          options: {
            filter: true,
          }
        },{
            name: "programme_amount",
            label:'cost',
            options: {
                filter: true,
            }
        },
        {
          name: "training_start_date",
          label:'startDate',
          options: {
            filter: true,
          }
        },{
            name: "training_end_date",
            label:'endDate',
            options: {
                filter: true,
            }
        },
        {
          name: "link",
          label:'Link'
      },{
        name: "amount",
        label: "Amount"
      }
    ]

    useEffect(() => {
      if(programmeList.length == 0) {
        //console.log(window.location)
        let id = localStorage.getItem('teacher_id')
        axios.get(`https://campusfield.in/api/training/get_teacher/${id}`)
        .then(res => {
          //console.log(res.data)
          if (res.data.error_code == 1) {
            let tempList = []
            for(let data of res.data.output) {
              if (data.members.indexOf(id) !== -1) {
                tempList = [...tempList, ...data.programme]
              } 
            }
            setProgrammeList(tempList)
          }
        })
        .catch(err => {
          //console.log(err)
        })
      }
    }, [])
    
    return (
      <div id="programs">
    <Grid container spacing={4}>
        <Grid item xs={12}>
          <MUIDataTable
            title="Enrolled Programmes"
            data={programmeList}
            columns={column}
            options={{  
              customRowRender: (data, dataIndex, rowIndex) =>{
                //console.log(data)
                return (
                  <TrainingProgrammes
                    title={data[1]}
                    enrolled={data.enrolled}
                    amount={data[8]}
                    id={data[0]}
                    enrolled={true}
                    duration={data.duration}
                    startDate={data[5]}
                    endDate={data[6]}
                    link={data[7]}
                    fee={data[4]}
                    city={data[2]}
                    description={data[3]}
                    enroll={""}
                    />
                )
              },
              selectableRows: false,
              selectableRowsOnClick: false,
              filter: true,
              onRowsSelect: (a, b) => {
                b.map((el) => {
                  let tempList = [];
                 })
              },
            }
          }
          />
        </Grid>
        {props.location.state ? <Alert message={props.location.state.message} color="green"/> : null}        
    </Grid>
    </div>
)
}

export default withRouter(EnrolledProgramme);
