import React, {useEffect, useState} from 'react';
import styles from "./Certifications.module.css"
import CommonHeader from "../CommonHeader/CommomHeader"
import PageTitle from "../../PageTitle";
import { useHistory } from "react-router-dom";
import axios from "axios"
import * as constants from "../constants/constants"
import Certification from './Certification/Certification';
import Alert from "../../Toast/Toast"

const Certifications = (props) => {
    let updatemessage = props.location.state ? props.location.state.updateMessage : ""
    const history = useHistory();
    const [data, setData] = useState([])
    const [message, setMessage] = useState("")
    let certificates;

    const onDeleteCertificate = (certId) => {
      let id = localStorage.getItem("id")
  
      axios.post(constants.url + "teachers/edit/" + id + "/Certification/" + certId + "/0", {"Certification": {
          "id": certId,
      }})
      .then((res) => {
          if(res.data.msg.search("delete") != -1) {
            getCertification()
            setMessage("deleted successfully")
          }
      }).catch((err) => {
          setMessage("network error!")
          setTimeout(() => {
              setMessage("")
            }, 3000)
          //console.log(err)
      })
  }

    let id = localStorage.getItem("id")
    useEffect(() => {
      //console.log(data)
      if(data.length === 0) {
        getCertification()
      }
      })

      const getCertification = () => {
        axios.get(constants.url + "teachers/dashboard/" + id)
        .then((res) => {
            if(res.data.error_code === 1) {
              if(typeof res.data.output[0].Qualification[0] === "string") {
                res.data.output[0].Qualification = JSON.parse(res.data.output[0].Qualification[0])
              }
  
              
              if(typeof res.data.output[0].Certification[0] === "string") {
                res.data.output[0].Certification = JSON.parse(res.data.output[0].Certification[0])
              }
  
              
              if(typeof res.data.output[0].Experience[0] === "string") {
                res.data.output[0].Experience = JSON.parse(res.data.output[0].Experience[0])
              }
              //console.log(res.data.output + "hello")
              setData(res.data.output[0]) 
            }
        })
        .catch((err) => {
            //console.log(err)
        })
      }

    if(data.Certification && data.Certification.length) {
      //console.log(data)
      certificates = data.Certification.map((el, i) => {
        //console.log({...el})
        // if(el === "") {
        //   return
        // }
        return (
          <Certification
          onDelete={onDeleteCertificate}
          id={el.id}
          key={i}
          title={el.title}
          description={el.description}
          />
        )
      })   
    }

    const move_to_edit = () => {
      history.push("/teacher/certificates/new")
    }

  return (
    <>
    <PageTitle title="Certifications" button="Add" add_new={move_to_edit} />
        <div className={styles["sub-container"]}>
        {certificates && certificates[0] !== undefined ? certificates : "No Certificates Added"}
          {/* {certificates ? certificates : "No Certificates Added"} */}
        </div>
        {message ? <Alert message={message} color={message.search("network") != -1 ? "red" : "green"}/> : null}
        {updatemessage  ? <Alert message={updatemessage} color="green"/>: null}
    </>
  );
}

export default Certifications;
