import React, { useEffect, useState } from 'react';
import styles from "./AddCertificates.module.css"
import Button from '@material-ui/core/Button';
import axios from "axios"
import * as constants from "../../constants/constants"
import PageTitle from "../../../PageTitle";
import { useHistory } from "react-router-dom";
import Alert from "../../../Toast/Toast"

const AddCertificates = (props) => {
    let isState = props.location.state
    const [message, setMessage] = useState("")
    let isEdit = props.match.url.search("edit") != -1
    const [isSuccess, setIsSuccess] = useState(false)
    const history = useHistory();

    if(isEdit && !props.history.location.state) {
        history.replace("/teacher/certificates")
    }
    const [certificate, setCertificate] = useState({
        val: "",
        // isValid: false,
        isValid: true,
        touched: false
    })
    const back = ()=>{
        history.goBack();
    }

    const [title, setTitle] = useState({
        val: isEdit && isState ? props.location.state.title : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    }) 
    const [description, setDescription] = useState({
        val: isEdit && isState ? props.location.state.description : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })

    const validate = (i, val) => {
        if(val) {
            return true
        }

        return false
    }

    const validateForm = () => {
        return title.isValid && certificate.isValid
    }

    const clearFields = () => {
        setTitle({
            isValid: false,
            touched: false,
            val: ''
        })
        setDescription({
            isValid: false,
            touched: false,
            val: ''
        })

        setCertificate({
            isValid: false,
            touched: false,
            val: ''
        })
    }
    const setTouchedTrue = () => {
        setTitle({...title, touched: true})
        setCertificate({...certificate, touched: true})
    }

    const onAddCertificate = () => {
        let isValid = validateForm()
        setTouchedTrue()

        if(isValid) {
            let id = localStorage.getItem("id")
            //console.log(title.val)
            let formData = new FormData()
            formData.append("title", title.val)
            formData.append("description", description.val ? description.val : "")
            formData.append("certificate", certificate.val)
            axios.post(constants.url + "teachers/update/" + id + "/Certification", {"Certification": formData})
            .then((res) => {
                //console.log(res)
                setMessage("added succesfully")
                setIsSuccess(true)
                setTimeout(() => {
                    setMessage("")
                  }, 3000)
                // alert("certificate added successfully")
                clearFields()
                // props.history.push("/sign-in")
            }).catch((err) => {
                setMessage("something went wrong!")
                setIsSuccess(false)
                setTimeout(() => {
                    setMessage("")
                  }, 3000)
            })
        }
    }

    const onUpdateCertificate = () => {
        let isValid = validateForm()
        setTouchedTrue()
        
        if(isValid) {
            let id = localStorage.getItem("id")
            //console.log(title.val)
            let formData = new FormData()
            formData.append("title", title.val)
            formData.append("description", description.val ? description.val : "")
            formData.append("certificate", certificate.val)
            axios.post(constants.url + "teachers/update/" + id + "/Certification/" + props.location.state.id + "/1", {"Certification": formData})
            .then((res) => {
                //console.log(res)
                history.push({
                    pathname: "/teacher/certificates",
                    state: {
                    updateMessage: "updated successfully"
                    }
                })
                // props.history.push("/sign-in")
            }).catch((err) => {
                setMessage("something went wrong!")
                setIsSuccess(false)
                setTimeout(() => {
                    setMessage("")
                  }, 3000)
            })
        }
    }

    const onFileChange = (event) => {
        let value = event.target.files[0]
        setCertificate({
            ...certificate,
            val: value,
            isValid: true
        })
    }

    const onChangeTextHandler = (event, i) => {
        let value = event.target.value
        
        if(i === 1) {
            setTitle({
                ...title,
                val: value,
                isValid: validate(i, value)
                
            })
        }

        if(i === 2) {
            setDescription({
                ...description,
                val: value,
                isValid: validate(i, value)
                
            })
        }
    }

    return (
    <>
    <PageTitle title={isEdit ? "Certification / Edit" : "Certification / Add"} button="Back" add_new={back} />

      {/* <div className={styles["title"]}>
        DASHBOARD / CERTIFICATION / ADD NEW CERTIFICATION 
      </div> */}
      <div className={styles["sub-container"]}>
            {/* <CommonHeader
            title="ADD NEW CERTIFICATION"
            link="BACK"
            to="/teacher/certificates"/> */}
            <form className={styles["certificates-form"]}>
                <div className={styles["input-container"]}>
                    <label>
                        Certificate no. *
                    </label>
                    <input
                    className={[title.touched && !title.isValid ? styles["invalid"] : null].join(" ")}
                    onBlur={() => {setTitle({...title, touched: true})}}
                    onChange={(event) => {onChangeTextHandler(event, 1)}}
                    value={title.val}
                    />
                </div>
                <div className={styles["description"]}>
                    <label>
                        Description *
                    </label>
                    <textarea
                    onChange={(event) => {onChangeTextHandler(event, 2)}}
                    value={description.val}
                    />
                </div>
                <div className={styles["input-container"]}>
                    <label style={{color: certificate.touched && !certificate.isValid ? "red" : "grey"}}>
                        Certificate(.png, .jpeg, .pdf) *
                    </label>
                    <input 
                    onChange={(event) => {onFileChange(event)}}
                    className={styles["file"]}
                    accept="image/png, image/jpeg, .pdf"
                    type="file"/>
                </div>
            </form>
            <div className={styles["button-container"]}>
                <Button
                    style={{marginRight: "0.9rem"}}
                    variant="contained"
                    color="primary"
                    onClick={isEdit ? onUpdateCertificate : onAddCertificate}
                >
                    {isEdit ? "Update" : "Create"}
                </Button>
                <Button
                    variant="contained"
                    color="secondary"
                    onClick={() => {
                        if(isEdit) {
                            history.goBack()
                        } else {
                            clearFields()}}}
                >
                    Cancel
                </Button>
            </div>
      </div>
      {
        message ? <Alert message={message} color={isSuccess ? "green" : "red"}/> : null
      }
    </>
  );
}

export default AddCertificates;
