import React from 'react';
import styles from "./Achievement.module.css"
import { useHistory } from "react-router-dom" 
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import DeleteIcon from '@material-ui/icons/Delete';
import Create from '@material-ui/icons/Create';

const Achievement = (props) => {
    const history = useHistory()
    const useStyles = makeStyles((theme) => ({
        button: {
        margin: theme.spacing(1),
        },
    }));
    
    const classes = useStyles();

  return (
    <div className={styles["qualification-container"]}>
        <div className={styles["job-container"]}>
            <h4>
                {props.title}
            </h4>
            <h3 style={{fontWeight: 400}}>
                <i className="fa fa-clock-o" aira-hidden={true}/>
                <strong>YEAR: </strong> {props.year}
            </h3>
            <p style={{whiteSpace: "pre-wrap", fontSize: "0.8rem"}}>
                {props.description}
            </p> 
        </div>
            {!props.isNotEdit ?
            <div className={styles["update-container"]}>
            <Button
                variant="contained"
                color="info"
                onClick={() => {history.push({
                    pathname: "/institution/achievement/edit",
                    state: {
                        id: props.id,
                        title: props.title,
                        description: props.description,
                        year: props.year
                    }})}}
                className={classes.button}
                startIcon={<Create />}
            >
                Edit
            </Button>
       
             <Button
                variant="contained"
                color="secondary"
                onClick={() => {props.onDelete(props.id)}}
                className={classes.button}
                startIcon={<DeleteIcon />}
            >
                Delete
            </Button>
            </div> : null }
    </div>
  );
}

export default Achievement;
