// import React from "react";
import { Grid } from "@material-ui/core";
import MUIDataTable from "mui-datatables";
import React, { useState ,useEffect} from "react"
import Alert from "../../Toast/Toast"
// import * as //constants from "./..//Home///constants/constants"

import * as constants from "../constants/constants" 
import axios from "axios"
// components
import PageTitle from "../../PageTitle";
import { useHistory } from "react-router";
import Add_new_faculties from "../FACULTIES/add_new/add_new"
// import Widget from "../../components/Widget";
// import Table from "../dashboard/components/Table/Table";

// data


export default function Faculty(props) {
  const history = useHistory()
  const [message, setMessage] = useState("")
  const [isSuccess, setIsSuccess] = useState(false)
  let updatemessage = props.location.state ? props.location.state.updateMessage : ""
  const [data, setData] = useState([])
  const [row, setRow] = useState([])
  const [currentIndex, setCurrentIndex] = useState(-1)
  const [isFaculties, setIsFaculties] = useState("")
  const column= isFaculties ? [
    {name: 'name', label: 'Name'},
    {name: 'Phone_Number', label: 'Mobile Number'}, 
    {name: 'email', label: 'Email'},
    {name: 'subject', label: 'Subject'}, 
    {name: 'isFresher', label: 'Experienced'},
  {name: 'Gender', 
  label: 'Edit', 
  options: {
    customBodyRender: (value, meta, updatevalue) => {
      return (
      <div
      onClick={() => {history.push({
        "pathname": "/institution/faculties/edit",
        "state": {
          data: data[meta.rowIndex],
          isEdit: true
        }
      })}}  
      style={{height: "30px", width: "60px", background: "#06b5ac", borderRadius: "10%", justifyContent: "center", alignItems: "center", display: "flex", color: "white", boxShadow: "0 1px 10px 0 lightgray", cursor: "pointer"}}> Edit 
      </div> )}
  }}] : [
    {
      name: "applicant_name",
      label:'User',
      options: {
        filter: true,
      }
    },
    {
      label: "Resume",
      name: "resume_path",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div>
              <a href={tableMeta.rowData[1]}>Download Resume</a>
           </div>
          );
        }
      }
    },
    {
      label: "Date",
      name: "date",
      options: {
        filter: false,
      }
    },
    {
      name: "status",
      options: {
        filter: true,
        sort: false,
        display:false
      }
    }]
  

  useEffect(() => {
    if(data.length === 0) {
      setIsFaculties(props.match.url.search("faculties") != -1)
      if(props.match.url.search("faculties") != -1) {
        getFaculties()
      } else {
        getDetails()
      }
    }

  },[]);
  const getDetails = () =>{
    //cons.log(props)

    let Job_id = localStorage.getItem("job_id");
    axios.get("https://127.0.0.1/api/job/getApplicantList/"+Job_id)
    .then(res => {
      if(res.data){
        //cons.log(res.data)
        setData(res.data.output)
      }
    })
  }

  let suggestedList = [] 

  const getFaculties = () => {
    let id = localStorage.getItem("id")
    axios.get(constants.url + "institution/dashboard/" + id)
        .then((res) => {
            //cons.log("hello")
            //cons.log(res.data)
            if(res.data.error_code === 1) {
              // //cons.log({...res.data.output} + "hello")
              setData(res.data.output[0].Faculties) 
              //cons.log(res.data.output[0].Faculties)
            }
        })
        .catch((err) => {
            //cons.log(err)
        })

  }

  const onAdd = () => {
    history.push("/institution/faculties/new")
  }

  let data_map;

  if(!isFaculties && data.length) {
    data_map = data.map((el) => {
      //cons.log(el)
      return {
        "user_id": el.user_id.slice(0, 4),
        "resume_path": el.resume_path,
        "date": el.date,
        "applicant_name": el.applicant_name,
        "status": el.status
      }
    })
  }
  
  if(isFaculties) {
      if(data.length) {
        data_map = data.map((el) => {
          return {
            "name": el.First_name + " " + el.Last_name,
            "email": el.Email,
            "subject": el.subject,
            "Phone_Number": el.Phone_Number,
            "isFresher": String(el.isFresher).toLowerCase() === "no" ? "YES" : "NO"
          }
        })
      }
  }

  if(suggestedList.length) {
    // //cons.log(suggestedList)
  } 


  if(currentIndex != -1) {
    return <Add_new_faculties isEdit={true} state={true} data={data[currentIndex]}/>
  }

  return (
    <>
      <PageTitle title="Faculties" button="Add" add_new={onAdd}/>
      <Grid container spacing={4}>
        <Grid item xs={12}>
          <MUIDataTable
            title="Faculty List"
            data={data_map}
            columns={column}
            options={{  
              // filterType: "checkbox",
              selectableRows: false,
              filter: true,
              selectableRowsOnClick: true,
              onRowsSelect: (a, b) => {
                b.map((el) => {
                  ////console.log(data[el.index])
                  let tempList = [];
                  suggestedList.push(data[el.index]._id)
                  // setSuggested(tempList)
                })
              },
              onRowClick: (rowData, rowState) => {
              if(isFaculties) {
                history.push({
                  "pathname": "/institution/faculties/edit",
                  "state": {
                    data: data[rowState.rowIndex]
                  }
                })
              } else {

              }
              
            }}
          }
          />
        </Grid>
        {/* <Grid item xs={12}>
          <Widget title="Material-UI Table" upperTitle noBodyPadding>
            <Table data={mock.table} />
          </Widget>
        </Grid> */}
      </Grid>
      {
        message ? <Alert message={message} color={isSuccess ? "green" : "red"}/> : null
      }
      {updatemessage  ? <Alert message={updatemessage} color="green"/>: null}
    </>
  );
}
