import React, { useState, useEffect } from 'react';
import styles from "./add_new.module.css"
import * as constants from "../../constants/constants"
import axios from "axios"
import PageTitle from "../../../PageTitle";
import { useHistory } from "react-router-dom";
import Alert from "../../../Toast/Toast"
import Button from '@material-ui/core/Button';


const Add_new_tie_up = (props) => {
   
    let isState = props.location.state
    const [message, setMessage] = useState("")
    let isEdit = props.match.url.search("edit") != -1
    const [isSuccess, setIsSuccess] = useState(false)
    const history = useHistory();

    const back = () =>{
      history.push("/institution/tie_up")
    }

    if(isEdit && !props.history.location.state) {
        history.replace("/institution/tie_up")
    }

    const [title, setTitle] = useState({
        value: isEdit && isState ? props.location.state.title : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })
    const [description, setDescription] = useState({
        value: isEdit && isState ? props.location.state.description : "",
        isValid: isEdit && isState ? true : false,
        touched: false
    })

    const validateForm = () => {
        return title.isValid && description.isValid
    }

    const validate = (val) => {
        return val.length
    } 

    const setTouchedTrue = () => {
        setTitle({
            ...title,
            touched: true
        })
        setDescription({
            ...description,
            touched: true
        })
    }

    const onAddTieUp = () => {
        let isValid = validateForm()
        setTouchedTrue()
        
        if(isValid) {
            let id = localStorage.getItem("id")
            axios.post(constants.url + "institution/TieuPs/" + id + "/nill/2", {
                "TieuPs":{
                    "title": title.value,
                    "description": description.value
                }
            })
            .then((res) => {
                history.push({
                    pathname: "/institution/tie_up",
                    state: {
                    updateMessage: "added successfully"
                    }
                })
                // alert("certificate added successfully")
                clearFields()
            }).catch((err) => {
                //console.log(err.msg)
                setMessage("something went wrong!")
                setIsSuccess(false)
                setTimeout(() => {
                    setMessage("")
                  }, 3000)
            })
        }
    } 

    const onUpdateTieUp = () => {
        let isValid = validateForm()
        setTouchedTrue()
        let id = localStorage.getItem("id")
        let tieUpId = isState && isEdit ? props.location.state.id : "nill"
        if(isValid) {
            axios.post(constants.url + "institution/TieuPs/" + id + `/${tieUpId}/1`, {
                "TieuPs":{
                    "title": title.value,
                    "description": description.value
                }
            })
            .then((res) => {
                //console.log(res)
                history.push({
                    pathname: "/institution/tie_up",
                    state: {
                    updateMessage: "updated successfully"
                    }
                })
                clearFields()
            }).catch((err) => {
                setMessage("something went wrong!")
                setIsSuccess(false)
                setTimeout(() => {
                    setMessage("")
                  }, 3000)
            })
        }
    } 

    const clearFields = () => {
        setDescription({
            value: "",
            isValid: false,
            touched: false
        })
        setTitle({
            value: "",
            isValid: false,
            touched: false
        })
    }

    const onChangeTexthandler = (event, i) => {
        let value = event.target.value

        if(i === 1) {
            setTitle({
                ...title,
                value: value,
                isValid: validate(value)
            })
        }

        if(i === 2) {
            setDescription({
                ...description,
                value: value,
                isValid: validate(value)
            })
        }
    }


    return (
    <>
    <PageTitle title={`Dashboard / Tie-up / ${isEdit ? "Edit": "New"}`} button="Back" add_new={back} />

          <div className={styles["sub-container"]}>
         
                <form className={styles["post-job-form"]}>
                    <div className={styles["input-container"]}>
                        <label style={{color: title.touched && !title.isValid ? "red" : "grey" }}>
                            Title *
                        </label>
                        <input
                        value={title.value}
                        onBlur={() => {setTitle({
                            ...title,
                            touched: true
                        })}}
                        onChange={(event) => { onChangeTexthandler(event, 1)} }
                        />
                    </div>
                    <div className={styles["description"]}>
                        <label style={{color: description.touched && !description.isValid ? "red" : "grey" }}>
                            Description *
                        </label>
                        <textarea
                        onBlur={() => {setDescription({
                            ...description,
                            touched: true
                        })}}
                        value={description.value}
                        onChange={(event) => { onChangeTexthandler(event, 2)}}/>
                    </div>
                </form>
                <div className={styles["button-container"]}>
                <Button
                    style={{marginRight: "0.9rem"}}
                    variant="contained"
                    color="primary"
                    onClick={isEdit ? onUpdateTieUp : onAddTieUp}
                >
                    {isEdit ? "Update" : "Create"}
                </Button>
                <Button
                    variant="contained"
                    color="secondary"
                    onClick={() => {
                        if(isEdit) {
                            history.goBack()
                        } else {
                            clearFields()}}}
                >
                    Cancel
                </Button>
            </div>
          </div>
          {
            message ? <Alert message={message} color={isSuccess ? "green" : "red"}/> : null
          }
        </>
  );
}

export default Add_new_tie_up;
