import React, { useEffect, useState } from 'react';
import styles from "./Experience.module.css"
import { NavLink, useHistory } from "react-router-dom" 
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import DeleteIcon from '@material-ui/icons/Delete';
import Create from '@material-ui/icons/Create';
import axios from "axios"
import * as constants from "../../constants/constants"
import Alert from "../../../Toast/Toast"

const Experience = (props) => {
    const history = useHistory()
    const [totalYears, setTotalYears] = useState(0)
    const useStyles = makeStyles((theme) => ({
        button: {
          margin: theme.spacing(1),
        },
      }));
    const classes = useStyles();

    const getExperience = (from , to) => {
        let toYear, toMonth, fromYear, fromMonth, toDate, fromDate

        if(!to) {
            toYear = new Date().getFullYear()
            toMonth = new Date().getMonth() + 1
            toDate = new Date().getDate()
        } else {
            toYear = new Date(to).getFullYear()
            toMonth = new Date(to).getMonth() + 1
            toDate = new Date(to).getDate()
        }

        fromYear = new Date(from).getFullYear()
        fromDate = new Date(from).getDate()
        fromMonth = new Date(from).getMonth() + 1
        var diffYears = toYear - fromYear 
        var diffMonth = toMonth - fromMonth
        var diffdate = toDate - fromDate
        // diff = (diff/365).toFixed(2)
        // props.setTotalYears(diff) 

        return `${diffYears} Years ${diffMonth} Months ${diffdate} Days`
    }

    useEffect(() => {
        // //console.log(props)
    })

  return (
    <div className={styles["experiences-container"]}>
        <div className={styles["experience-container"]}>
            <h4>
                {props.institution.search(":") !== -1  ? props.institution.split(":")[1]: props.institution}
            </h4>
            <div className={styles["detail-container"]}>
                <i class="fa fa-book" aria-hidden={true}/>
                <h3>
                    <b>SUBJECT</b> : <span>{props.subject}</span>
                </h3>
            </div>
            <div className={styles["detail-container"]}>
                <i class="fa fa-bookmark"  aria-hidden={true}/>
                <h3>
                    <b>DESIGNATION</b> : <span>{props.designation}</span>
                </h3>
            </div>
            <div className={styles["detail-container"]}>
                <i class="fa fa-history"  aria-hidden={true}/>
                <h3>
                    <b>EXPERIENCE IN YEARS</b> : <span>{getExperience(props.from, props.to)}</span>
                </h3>
            </div>
            <div className={styles["experience"]}>
                <i class="fa fa-clock-o" aria-hidden={true}/>
                <b>DATE</b> : <span>{props.from} - {props.to ? props.to : " PRESENT"}</span>
            </div>
            {

            !props.isNotEdit ?
            <div className={styles["update-container"]}>
            <Button
                variant="contained"
                color="info"
                onClick={() => {history.push({
                    pathname: "/teacher/experiences/edit",
                    state: {
                        id: props.id,
                        institution: props.institution,
                        subject: props.subject, 
                        designation: props.designation,
                        from: props.from,
                        jobType: props.jobType,
                        to: props.to,
                        isCurrentlyWorking: props.isCurrentlyWorking,
                        description: props.description
                    }})}}
                className={classes.button}
                startIcon={<Create />}
            >
                Edit
            </Button>
       
             <Button
                variant="contained"
                color="secondary"
                onClick={() => {props.onDelete(props.id)}}
                className={classes.button}
                startIcon={<DeleteIcon />}
            >
                Delete
            </Button>
            </div> : null }
        </div>
    </div>
  );
}

export default Experience;
