 const data = [
  { name: "Joe James", company: "Test Corp", city: "Yonkers", state: "NY" },
  { name: "John Walsh", company: "Test Corp", city: "Hartford", state: "CT" },
  { name: "Bob Herm", company: "Test Corp", city: "Tampa", state: "FL" },
  { name: "James Houston", company: "Test Corp", city: "Dallas", state: "TX" },
 ];

export default function Enquires() {
  const [open, setOpen] = React.useState(false);

  const view = (data) =>{
    //console.log(data)
    setOpen(true)
    setMessage(data.rowData[3])
  }
  const [data, setData] = useState([])
  const [message, setMessage] = useState("")


  const columns = [
    {
     name: "Name",
     label: "Name",
     options: {
      filter: true,
      sort: true,
     }
    },
    {
     name: "Email",
     label: "Email",
     options: {
      filter: true,
      sort: false,
     }
    },
    {
     name: "Mobile_Number",
     label: "Mobile Number ",
     options: {
      filter: true,
      sort: false,
     }
    },
    {
     name: "Message",
     label: "Message",
     options: {
      filter: true,
      sort: false,
      customBodyRender: (value, tableMeta, updateValue) => {
        return (
          <p>
            {String(value).split(" ").length < 30 ? value : String(value).split(" ").slice(0, 30).join(" ") + "........"}
          </p>
        );
      }
     }
    },
    {
      name: "status",
      label: "Actions",
      options: {
        filter: false,
        sort: false,
        empty: true,
  
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div>
              <Button
                onClick={() => { view(tableMeta) }}
                color="success"
                size="small"
                className="px-2"
                variant="contained"
              >
                View
                </Button>
              
            </div>
  
          );
        }
      }
    },
   ];


  useEffect(() => {
    getDetails()
  }, []);

  const getDetails = () =>{
    axios.get("https://campusfield.in/api/site_management/enquirie")
    .then(res => {
      if (res.data) {
        if (res.data.error_code) {
          // //console.log(res.data.output, 'dat')
          setData(res.data.output)
        }
      }
    })
  }
  return (
    <>
      <PageTitle title="Enquires" />
      <Grid container spacing={4}>
        <Grid item xs={12}>
          <MUIDataTable
            title="Enquires"
            data={data}
            columns={columns}
            options={{
              filterType: "checkbox",
              selectableRows:false
            }}
          />
        </Grid>

      </Grid>

     {
       open ?
       <div style={{position: "fixed",
     backgroundColor:"rgb(0, 0, 0, 0.5)",
     height: "100vh",
     width: "100vw",
     top: 0,
     left: 0,
     zIndex: 100000000,
     display: "flex",
     alignItems: "center",
     justifyContent: "center"}}>
       <div style={{height: "auto", 
                    width: "90%", 
                    maxWidth: "400px", 
                    overflowY: "scroll",
                    backgroundColor: "white", 
                    padding: "1rem",
                    paddingTop: "2rem",
                    textAlign: "center",
                    position: "relative",
                    borderRadius: "5px",
                    minHeight: "150px"}}>
          <i 
          onClick={() => {setOpen(false)}}
          className="fa fa-close"
          aria-hidden={true}
          style={{position: "absolute", top: "5%", right: "5%", cursor: "pointer"}}></i>
          {message}
        </div>
      </div> : null
    }
    </>
  );
}
